import { AppEvent, BoardCastEvent, getErrorMessage, Page, sleep } from '@td-org/chat-widget-shared'

import { useApp } from '@/stores/app/store'
import { useMessageStore } from '@/stores/message'
import baseLogger from '@/utils/logger'
import { useBoardCast } from '../../../../useBoardCast'
import type {
  ProviderConnectionImp,
  ProviderConnectionStatus,
  ProviderInitScene,
  ProviderPreloadClientScene,
  UserInput
} from '../../types'
import { updateTwilioMessageFailed } from '../provider-utils/twilio'

import Client from './client.ts'
import Conversation from './conversation.ts'

const logger = baseLogger.getSubLogger({ name: 'TwilioConnection' })

class TwilioConnection implements ProviderConnectionImp<Client, Conversation> {
  private readonly _client: Client
  private readonly _conversation: Conversation

  private _status: ProviderConnectionStatus = 'deactivated'
  private _bootstrapPromise: Promise<boolean> | null = null
  private _receivedFirstMessageSpan: ReturnType<typeof logger.startSpan> = null

  constructor() {
    this._client = new Client(this)
    this._conversation = new Conversation(this)
  }

  private handlerOnBootstrapError(error: unknown, span: ReturnType<typeof logger.startSpan>) {
    const errorMsg = getErrorMessage(error)
    const message = `Failed to bootstrap twilio connection sid=${
      useApp.getState().credentials.roomId
    } error=${errorMsg}`

    useApp.getState().hiddenScreenLoading()

    span?.recordException(error as Error)
    span?.setStatus({ code: 2, message })
    span?.end()
    logger.debug(message)

    const isProviderTimeout = /Twilsock: request timeout/gi.test(errorMsg)
    if (useApp.getState().is(useApp.getState().getInitialPage()) && isProviderTimeout) {
      return false
    }
    if (useApp.getState().isNot(Page.Error)) {
      useApp.getState().go(Page.Error)
      useBoardCast.getState().emit(BoardCastEvent.conversationEndedByError)
    }
    return false
  }

  private linkReceivedFirstMessageSpanToBootstrap(
    scene: ProviderInitScene,
    bootstrapSpan: ReturnType<typeof logger.startSpan>
  ) {
    if (['sendMessage', 'submitInitialScreen'].indexOf(scene) > -1) {
      this.receivedFirstMessageSpan = logger.startSpan(`Received first message for ${scene}`)
      const bootstrapCtx = bootstrapSpan?.spanContext()
      const receivedCtx = this.receivedFirstMessageSpan?.spanContext()
      if (bootstrapCtx) {
        this.receivedFirstMessageSpan?.addLink({ context: bootstrapCtx })
      }
      if (receivedCtx) {
        bootstrapSpan?.addLink({ context: receivedCtx })
      }
    }
  }

  private async bootstrap(scene: ProviderInitScene) {
    const span = logger.startSpan(`Bootstrap for ${scene}`)

    this.linkReceivedFirstMessageSpanToBootstrap(scene, span)

    try {
      await this._client.bootstrap(scene, span)
      await this._conversation.bootstrap(scene, span)

      useApp.setState({ hasInitialized: true })
      this.status = 'activated'

      span?.setAttributes({ roomId: this._conversation.current?.sid as string })
      span?.setStatus({ code: 1 })
      span?.end()

      sleep(1000).then(() => useApp.getState().emit(AppEvent.need_scroll_to_bottom))

      return true
    } catch (error) {
      return this.handlerOnBootstrapError(error, span)
    }
  }

  public preloadClient(scene: ProviderPreloadClientScene) {
    return this._client.preload(scene)
  }

  public async init(scene: ProviderInitScene) {
    const hasRoomId = !!useApp.getState().credentials.roomId

    if (!hasRoomId && scene === 'pageLoaded') {
      const initialPage = useApp.getState().getInitialPage()
      if (useApp.getState().isNot(initialPage)) {
        useApp.getState().go(initialPage)
      }
      return false
    }

    if (!this._bootstrapPromise) {
      this._bootstrapPromise = this.bootstrap(scene)
    }
    return await this._bootstrapPromise
  }

  public async send(userInput: UserInput, tid: string) {
    try {
      if (!this._bootstrapPromise) {
        logger.debug('Failed to send message error=no start connection bootstrap')
        updateTwilioMessageFailed(userInput, tid)
        return false
      }
      const result = await this._bootstrapPromise
      if (!result) {
        logger.debug('Failed to send message error=connection bootstrap failed')
        updateTwilioMessageFailed(userInput, tid)
        return false
      }

      await this._conversation.send(userInput, tid)

      useMessageStore.getState().showTypingMessage()
      useApp.getState().emit('send_success')

      return true
    } catch (e) {
      logger.warn(`Failed to send message error=${getErrorMessage(e)}`)
      useApp.getState().emit('send_failure', e)
      updateTwilioMessageFailed(userInput, tid)
      return false
    }
  }

  public typing() {
    return this._conversation.typing()
  }

  public async end() {
    return this._conversation.end()
  }

  public destroy() {
    this._conversation.destroy()
    this._client.destroy()
    this._bootstrapPromise = null
    this._status = 'deactivated'
    this.receivedFirstMessageSpan = null

    useMessageStore.getState().clear()
    useApp
      .getState()
      .reset([
        'hasInitialized',
        'credentials',
        'identity',
        'shouldLoadFeedback',
        'feedbackLink',
        'feedbackStatus',
        'formData',
        'interactionId',
        'chatContentScroll',
        'initialScreenScroll'
      ])
  }

  set status(target: ProviderConnectionStatus) {
    if (this._status !== target) {
      this._status = target
    }
  }

  get status() {
    return this._status
  }

  get client() {
    return this._client
  }

  get conversation() {
    return this._conversation
  }

  get receivedFirstMessageSpan() {
    return this._receivedFirstMessageSpan
  }

  set receivedFirstMessageSpan(target: ReturnType<typeof logger.startSpan>) {
    this._receivedFirstMessageSpan = target
  }
}

const twilioConnection = new TwilioConnection()

export default twilioConnection
