import { lazy, ReactElement, Suspense, useEffect, useMemo, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import type { Categories, EmojiStyle, SuggestionMode } from 'emoji-picker-react'

import { Spinner } from '@td-org/chat-widget-components'

import classes from './EmojiPicker.module.scss'

const Picker = lazy(() => import('emoji-picker-react'))

interface Props {
  onEmojiClick: (event: { emoji: string }) => void
  onTogglePicker: () => void
  visible: boolean
  enableRTL?: boolean
  bottom?: number
}

export const EmojiPickerPopup = (props: Props): ReactElement | null => {
  const { onEmojiClick, visible, onTogglePicker, enableRTL = false, bottom } = props
  const [t] = useTranslation()

  const pickerRef = useRef<HTMLDivElement>(null)

  const emojiConfigs = useMemo(() => {
    return {
      searchPlaceholder: t('emojiSearchPlaceholder'),
      categories: [
        { name: t('emojiCategorySuggested'), category: 'suggested' as Categories.SUGGESTED },
        { name: t('emojiCategorySmileysPeople'), category: 'smileys_people' as Categories.SMILEYS_PEOPLE },
        { name: t('emojiCategoryAnimalsNature'), category: 'animals_nature' as Categories.ANIMALS_NATURE },
        { name: t('emojiCategoryFoodDrink'), category: 'food_drink' as Categories.FOOD_DRINK },
        { name: t('emojiCategoryTravelPlaces'), category: 'travel_places' as Categories.TRAVEL_PLACES },
        { name: t('emojiCategoryActivities'), category: 'activities' as Categories.ACTIVITIES },
        { name: t('emojiCategoryObjects'), category: 'objects' as Categories.OBJECTS },
        { name: t('emojiCategorySymbols'), category: 'symbols' as Categories.SYMBOLS },
        { name: t('emojiCategoryFlags'), category: 'flags' as Categories.FLAGS }
      ]
    }
  }, [t])

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (pickerRef.current && !pickerRef.current.contains(event.target as Node)) {
        visible && onTogglePicker()
      }
    }

    document.addEventListener('click', handleClickOutside, false)
    return () => {
      document.removeEventListener('click', handleClickOutside, false)
    }
  }, [onTogglePicker, visible])

  return (
    <div
      className={[classes['emoji-picker'], enableRTL ? classes['emoji-picker-rtl'] : ''].filter(Boolean).join(' ')}
      data-testid={'emoji-picker'}
      ref={pickerRef}
      aria-hidden={!visible}
      aria-label='emoji'
      style={{ bottom }}
    >
      {visible && (
        <Suspense fallback={<Spinner size='medium' />}>
          <Picker
            width='100%'
            height='100%'
            className={classes['emoji-picker-wrapper']}
            previewConfig={{
              showPreview: false
            }}
            emojiStyle={'native' as EmojiStyle.NATIVE}
            onEmojiClick={onEmojiClick}
            suggestedEmojisMode={'recent' as SuggestionMode.RECENT}
            lazyLoadEmojis
            autoFocusSearch={true}
            searchPlaceholder={emojiConfigs.searchPlaceholder}
            categories={emojiConfigs.categories}
          />
        </Suspense>
      )}
    </div>
  )
}
