import Fail from '../Fail/Fail.tsx'
import Spinner from '../Spinner/Spinner.tsx'

type BubbleStatusProps = {
  status: 'sending' | 'failure' | 'success'
  onResend: () => void
}

function BubbleStatus(props: BubbleStatusProps): JSX.Element | undefined {
  const { status, onResend } = props

  if (status === 'sending') {
    return <Spinner />
  }

  if (status === 'failure') {
    return <Fail onResend={onResend}></Fail>
  }
}

export default BubbleStatus
