import type { Client as TwilioClient } from '@twilio/conversations'

import { getErrorMessage, Page } from '@td-org/chat-widget-shared'

import { useApp } from '@/stores/app/store'
import baseLogger from '@/utils/logger'
import type { ProviderConnectionImp, ProviderInitScene, ProviderPreloadClientScene } from '../../types'
import {
  buildTwilioClientOptions,
  createTwilioClient,
  fetchAccessTokenAndCreateTwilioClient,
  fetchTwilioAllMessages,
  fetchTwilioRoomId
} from '../provider-utils/twilio'

import type Conversation from './conversation.ts'

const logger = baseLogger.getSubLogger({ name: 'TwilioClient' })

class Client {
  private readonly _retryConfiguration = {
    fetchProviderRoomId: 2,
    createProviderClient: 3,
    connectProviderClient: 0
  }

  private _preloadClientPromise: Promise<{ client?: TwilioClient; error?: unknown }> | null = null
  private _providerClient: TwilioClient | null = null
  private _bindEventsHandlers: (() => void) | null = null
  private _preloadClientData: { fetchAccessTokenDuration: number; connectClientDuration: number } | null = null

  constructor(private readonly parent: ProviderConnectionImp<Client, Conversation>) {}

  private handleOnStateChanged(state: string) {
    logger.info(`client changed state=${state} conversationEnded=${this.parent.conversation.isEnd}`)

    if (this.parent.conversation.isEnd) return

    if (state === 'connected') {
      this.parent.status = 'activated'
      useApp.getState().emit('reconnect')
      fetchTwilioAllMessages(this.parent.conversation.current).catch((error) =>
        logger.warn(`Failed to fetch messages after reconnect error=${getErrorMessage(error)}`)
      )
    } else {
      this.parent.status = 'deactivated'
      useApp.getState().emit('disconnect')
    }
  }

  private handleOnTokenExpired() {
    logger.warn('client token expired')

    if (useApp.getState().isNot(Page.Error)) {
      useApp.getState().go(Page.Error)
      this.parent.destroy()
    }
  }

  private listenerEvents() {
    if (this._providerClient && !this._bindEventsHandlers) {
      const boundHandleOnStateChanged = this.handleOnStateChanged.bind(this)
      const boundHandleOnTokenExpired = this.handleOnTokenExpired.bind(this)

      this._bindEventsHandlers = () => {
        this._providerClient?.off('connectionStateChanged', boundHandleOnStateChanged)
        this._providerClient?.off('tokenExpired', boundHandleOnTokenExpired)
      }

      this._providerClient.on('connectionStateChanged', boundHandleOnStateChanged)
      this._providerClient.on('tokenExpired', boundHandleOnTokenExpired)
    }
  }

  public async preload(scene: ProviderPreloadClientScene) {
    if (useApp.getState().credentials.roomId) return

    if (this._preloadClientPromise && scene === 'tabEvent') {
      const result = await this._preloadClientPromise
      if (result?.client) {
        await result.client.updateToken(useApp.getState().credentials.accessToken)
        logger.debug(`Success update twilio token for preload client scene=${scene}`)
        return
      }
    }

    if (!this._preloadClientPromise) {
      this._preloadClientPromise = fetchAccessTokenAndCreateTwilioClient({
        shouldFetchAccessToken: scene !== 'tabEvent',
        onResolve: (data) => {
          this._preloadClientData = data
        }
      })
    }

    await this._preloadClientPromise
  }

  public async bootstrap(scene: ProviderInitScene, parentSpan: ReturnType<typeof logger.startSpan>) {
    const span = logger.startChildSpan('Bootstrap', parentSpan)

    try {
      if (this._preloadClientPromise) {
        const result = await this._preloadClientPromise
        if (result?.client) {
          this._providerClient = result.client
        }
        span?.setAttributes({
          preloadFetchAccessTokenDuration: this._preloadClientData?.fetchAccessTokenDuration || 0,
          preloadConnectClientDuration: this._preloadClientData?.connectClientDuration || 0,
          preloadProviderConnectClientError: getErrorMessage(result?.error)
        })
      }

      const fetchTwilioRoomIdAt = Date.now()
      const { accessToken, region } = await fetchTwilioRoomId(this._retryConfiguration.fetchProviderRoomId)
      span?.setAttributes({ fetchTwilioRoomIdDuration: Date.now() - fetchTwilioRoomIdAt })

      if (!this._providerClient) {
        const createTwilioClientAt = Date.now()
        const createTwilioClientRetry =
          scene === 'pageLoaded'
            ? this._retryConfiguration.connectProviderClient
            : this._retryConfiguration.createProviderClient
        this._providerClient = await createTwilioClient(
          accessToken,
          buildTwilioClientOptions(region),
          createTwilioClientRetry
        )
        span?.setAttributes({ connectProviderClientDuration: Date.now() - createTwilioClientAt })
      } else {
        await this._providerClient.updateToken(accessToken)
        logger.debug('Success update twilio client token for connect client')
      }

      this.listenerEvents()

      span?.setStatus({ code: 1 })
      span?.end()

      return this._providerClient
    } catch (e) {
      span?.recordException(e as Error)
      span?.setStatus({ code: 2, message: getErrorMessage(e) })
      span?.end()
      throw e
    }
  }

  public destroy() {
    this._bindEventsHandlers?.()
    this._bindEventsHandlers = null
    this._providerClient?.removeAllListeners()
    this._providerClient?.shutdown()
    this._providerClient = null
    this._preloadClientPromise = null
    this._preloadClientData = null
  }

  get current() {
    return this._providerClient
  }
}

export default Client
