import type { MessageTypes } from '@td-org/chat-widget-shared'

import MessageBox from './MessageBox.tsx'

import classes from './ChatBubbleBox.module.scss'

interface MessageErrorProps {
  type: MessageTypes
  errorMessage?: string
}

const DEFAULT_ERROR_MESSAGE = 'Failed to load dynamically imported message.'

const MessageError = ({ type, errorMessage }: MessageErrorProps) => {
  return (
    <MessageBox className={classes.error} type={type}>
      {errorMessage || DEFAULT_ERROR_MESSAGE}
    </MessageBox>
  )
}

export default MessageError
