import type { TwilioMediaPayload } from 'src/utils/twilio'

import type { MediaItem, TextItem } from '@td-org/chat-widget-shared'

import { convertTextPayload } from '@/utils/receiver/converter/text.ts'

export function convertMediaPayload(responses: string[], payload: TwilioMediaPayload): Array<TextItem | MediaItem> {
  const { media_type, url, ignore_valid, provider, getContentTemporaryUrl } = payload.media

  return [
    ...convertTextPayload(responses.filter((item) => item !== '')),
    {
      type: media_type,
      url,
      ignoreValid: ignore_valid,
      provider,
      getContentTemporaryUrl,
      smart_text: {
        key: 'download'
      }
    }
  ]
}
