import { SystemMessageItem } from '@td-org/chat-widget-shared'

import { TwilioSystemMessagePayload } from '@/utils/twilio'

export function convertSystemMessagePayload(payload: TwilioSystemMessagePayload): SystemMessageItem[] {
  return [
    {
      type: 'system',
      action_type: payload.action_type,
      smart_text: payload.smart_text
    }
  ]
}
