import { KeyboardEvent, useEffect, useMemo, useRef } from 'react'
import { ChangeEvent, useState } from 'react'
import { useTranslation } from 'react-i18next'
import Textarea from 'react-textarea-autosize'
import debounce from 'lodash.debounce'

import { type MediaType, WIDGET_DEFAULT_MESSAGE_BOX_HEIGHT } from '@td-org/chat-widget-shared'

import Attachment from '@/components/Attachment/Attachment.tsx'
import EmojiPicker from '@/components/EmojiPicker'
import EmojiButton from '@/components/EmojiPicker/EmojiButton.tsx'
import SendButton from '@/components/SendButton/SendButton.tsx'
import { type UserInput } from '@/stores/app/connection/types'
import { useChatContentScroll, useFrameOpened } from '@/stores/app/selectors.ts'
import { useApp } from '@/stores/app/store.ts'
import { optionsSelector, useConfig } from '@/stores/config'
import clsx from '@/utils/clsx.ts'

import classes from './UserInputArea.module.scss'

interface Props {
  enableEmoji: boolean
  enableAttachments: boolean
  send: (input: UserInput) => Promise<void>
  typing: () => Promise<void>
  connected: boolean
  enableRTL?: boolean
  showScreenLoading?: boolean
}

const UserInputArea = (props: Props): JSX.Element => {
  const [emojiPickerVisible, setEmojiPickerVisible] = useState<boolean>(false)
  const {
    enableEmoji,
    enableAttachments,
    send,
    typing,
    connected,
    enableRTL = false,
    showScreenLoading = false
  } = props
  const [textarea, setTextarea] = useState<string>('')
  const { chatPlaceholder, chatAriaLabel } = useConfig(optionsSelector)
  const [t] = useTranslation()

  const [textAreaRef, setTextAreaRef] = useState<HTMLTextAreaElement | null>(null)

  const opened = useFrameOpened()
  const { footerVisible, enableScroll } = useChatContentScroll()

  const i18n = useMemo(
    () => ({
      chatPlaceholder: chatPlaceholder || t('chatPlaceholder'),
      chatAriaLabel: chatAriaLabel || t('chatAriaLabel')
    }),
    [t, chatPlaceholder, chatAriaLabel]
  )

  useEffect(() => {
    useApp.setState({ userInputAreaRef: textAreaRef })
    if (opened && !showScreenLoading) {
      textAreaRef?.focus()
    }

    return () => {
      useApp.setState({ userInputAreaRef: null })
    }
  }, [textAreaRef, opened, showScreenLoading])

  useEffect(() => {
    if (textarea.length > 0 && useApp.getState().typingMode !== 'va') {
      typing()
    }
  }, [textarea, typing])

  const onSendMessage = async (): Promise<void> => {
    if (!textarea.trim()) return
    setEmojiPickerVisible(false)
    textAreaRef?.focus()
    send({
      data: textarea.trim(),
      type: 'text'
    })

    setTextarea('')
  }

  const onTextareaChange = (event: ChangeEvent<HTMLTextAreaElement>): void => {
    const text = event.target.value
    setTextarea(text)
  }

  const onTogglePicker = useRef(
    debounce((): void => {
      setEmojiPickerVisible((val) => !val)
    }, 0)
  ).current

  const handleSelectedEmoji = (event: { emoji: string }): void => {
    setTextarea((textarea) => `${textarea}${event.emoji}`)
    textAreaRef?.focus()
  }

  const onKeyDown = async (event: KeyboardEvent<HTMLTextAreaElement>) => {
    if (event.nativeEvent.isComposing) {
      return
    }

    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault()
      await onSendMessage()
    }
  }

  const onAttachmentChange = (data: FormData, fileType: MediaType) => {
    send({ data, type: fileType })
    textAreaRef?.focus()
  }

  return (
    <div
      id='user-input-area'
      className={clsx(classes['user-input-area'], {
        [classes.withSeparator]: !footerVisible && enableScroll
      })}
      data-testid='user-input-area'
    >
      <div className={classes['textarea-container']}>
        <Textarea
          name='text'
          ref={(element) => setTextAreaRef(element)}
          placeholder={i18n.chatPlaceholder}
          maxLength={1000}
          wrap='soft'
          maxRows={4}
          onChange={onTextareaChange}
          value={textarea}
          aria-label={i18n.chatAriaLabel}
          onKeyDown={(e) => onKeyDown(e)}
          enterKeyHint='send'
          disabled={!connected}
        />

        <div className={classes['action-buttons']}>
          {enableAttachments ? <Attachment disabled={!connected} onChange={onAttachmentChange} /> : null}
          {enableEmoji ? <EmojiButton disabled={!connected} onClick={onTogglePicker} /> : null}
          <SendButton disabled={!connected || !textarea.trim()} onClick={onSendMessage} />
        </div>
        {enableEmoji && (
          <EmojiPicker
            bottom={textAreaRef?.offsetHeight || WIDGET_DEFAULT_MESSAGE_BOX_HEIGHT}
            visible={emojiPickerVisible}
            setVisible={onTogglePicker}
            onSelect={handleSelectedEmoji}
            enableRTL={enableRTL}
          />
        )}
      </div>
    </div>
  )
}

export default UserInputArea
