import { CarouselCard, CarouselItem, MAX_ALLOWED, TextItem } from '@td-org/chat-widget-shared'

import { convertTextPayload } from '@/utils/receiver/converter/text.ts'
import type { TwilioCarouselPayload } from '@/utils/twilio'
import { convertButtonElements } from '../helpers.ts'

export function convertCarouselPayload(
  responses: string[],
  payload: TwilioCarouselPayload
): [...TextItem[], CarouselItem] {
  const cards: CarouselCard[] = payload.carousel.elements.slice(0, MAX_ALLOWED.CAROUSEL_CARDS).map((elem) => {
    const { title, text, image_url, buttons } = elem

    return {
      title,
      description: text,
      imageUrl: image_url,
      buttons: convertButtonElements(buttons.slice(0, MAX_ALLOWED.CAROUSEL_BUTTONS))
    }
  })

  return [
    ...convertTextPayload(responses.filter((item) => item !== '')),
    {
      type: 'carousel',
      cards
    }
  ]
}
