import { type ReactElement, useCallback } from 'react'

import { Message, MessageButton } from '@td-org/chat-widget-shared'

import { useConnectionStatus } from '@/hooks/useConnectionStatus.ts'
import { useMessagesAction } from '@/stores/message'

import { flatMapMessages } from './utils/flatMapMessages'
import { openLink } from './utils/OpenLink'
import { MessageBubble, WelcomeMessages } from './MessageItem'

const MessageList = ({ messages }: { messages: Message[] }): ReactElement => {
  const { send } = useMessagesAction()
  const connected = useConnectionStatus()

  const onButtonClick = useCallback(
    async (info: MessageButton) => {
      switch (info.type) {
        case 'reply':
          if (connected) {
            await send({ data: info.text, type: info.type, payload: info.payload })
          }
          break
        case 'call':
        case 'link':
          openLink(info.payload)
          break
      }
    },
    [connected, send]
  )

  return (
    <>
      <WelcomeMessages onButtonClick={onButtonClick} />
      {flatMapMessages(messages).map((message, index, messageArray) => {
        return (
          <MessageBubble
            key={`${message.tid || message.sid}-${index}`}
            message={message}
            index={index}
            messageArray={messageArray}
            onButtonClick={onButtonClick}
          />
        )
      })}
    </>
  )
}

export default MessageList
