import { snakeCase } from 'change-case/keys'
import { z } from 'zod'

import { invalidTypeErrorMessage, nonEmptyArrayErrorMessage, requiredErrorMessage } from './helper.ts'

const PayloadTypeEnum = {
  Text: 'text',
  CustomText: 'customtext',
  HTMLContent: 'htmlcontent',
  Media: 'media',
  Carousel: 'carousel',
  QuickReplies: 'quickreplies',
  Buttons: 'buttons',
  MultiContent: 'multicontent'
} as const

//#region Base Schema
const ButtonItemSchema = z.object({
  type: z.enum(['payload', 'link', 'call']),
  payload: z.string().optional(),
  display_text: z.string(),
  phone_number: z.string().optional(),
  country_code: z.string().optional(),
  target: z.string().optional(),
  id: z.string().optional(),
  url: z.string().optional()
})
//#endregion

//#region Payload Schema
const PayloadTypeSchema = z.enum([
  PayloadTypeEnum.Text,
  PayloadTypeEnum.CustomText,
  PayloadTypeEnum.HTMLContent,
  PayloadTypeEnum.Media,
  PayloadTypeEnum.Carousel,
  PayloadTypeEnum.QuickReplies,
  PayloadTypeEnum.Buttons,
  PayloadTypeEnum.MultiContent
])

const PayloadSchema = z.object({
  media_type: PayloadTypeSchema
})

const TextPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.Text)
})

const CustomTextPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.CustomText),
  text_response: z.string()
})

const HTMLContentPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.HTMLContent),
  content: z
    .array(
      z.string({
        invalid_type_error: invalidTypeErrorMessage('content', PayloadTypeEnum.HTMLContent)
      }),
      {
        required_error: requiredErrorMessage('content', PayloadTypeEnum.HTMLContent)
      }
    )
    .nonempty({ message: nonEmptyArrayErrorMessage('content', PayloadTypeEnum.HTMLContent) })
})

const MediaPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.Media),
  media: z.object({
    media_type: z.enum(['image', 'audio', 'video', 'file']),
    url: z.string(),
    ignore_valid: z.boolean().optional(),
    provider: z.literal('twilio').optional(),
    getContentTemporaryUrl: z.function().returns(z.promise(z.string())).optional()
  })
})

const CarouselPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.Carousel),
  carousel: z.object({
    elements: z
      .array(
        z.object({
          title: z.string(),
          text: z.string().optional(),
          image_url: z.string().optional(),
          buttons: z
            .array(ButtonItemSchema)
            .nonempty({ message: nonEmptyArrayErrorMessage('carousel.elements.buttons', PayloadTypeEnum.Carousel) })
        })
      )
      .nonempty({ message: nonEmptyArrayErrorMessage('carousel.elements', PayloadTypeEnum.Carousel) })
  })
})

const QuickRepliesPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.QuickReplies),
  text_response: z.string().optional(),
  quick_actions: z.object({
    elements: z
      .array(ButtonItemSchema)
      .nonempty({ message: nonEmptyArrayErrorMessage('quick_actions.elements', PayloadTypeEnum.QuickReplies) })
  })
})

const ButtonPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.Buttons),
  text_response: z.string().optional(),
  quick_actions: z.object({
    elements: z
      .array(ButtonItemSchema)
      .nonempty({ message: nonEmptyArrayErrorMessage('quick_actions.elements', PayloadTypeEnum.Buttons) })
  })
})

const MultiContentPayloadSchema = z.object({
  media_type: z.literal(PayloadTypeEnum.MultiContent),
  content: z
    .array(
      z.union([
        CustomTextPayloadSchema,
        MediaPayloadSchema,
        CarouselPayloadSchema,
        QuickRepliesPayloadSchema,
        ButtonPayloadSchema,
        HTMLContentPayloadSchema
      ])
    )
    .nonempty({ message: nonEmptyArrayErrorMessage('content', PayloadTypeEnum.MultiContent) })
})

//#endregion

//#region Body Schema
const BodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(PayloadSchema)
})

const TextBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(z.never().or(TextPayloadSchema))
})

const CustomTextBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(CustomTextPayloadSchema)
})

const MediaBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(MediaPayloadSchema)
})

const HTMLContentBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(HTMLContentPayloadSchema)
})

const CarouselBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(CarouselPayloadSchema)
})

const QuickRepliesBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(QuickRepliesPayloadSchema)
})

const ButtonBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(ButtonPayloadSchema)
})

const MultiContentBodySchema = z.object({
  alt_text: z.string(),
  responses: z.array(z.string()),
  payloads: z.array(MultiContentPayloadSchema)
})
//#endregion

export function validateSchema(payload: unknown) {
  const result = snakeCase(payload, 10)

  const textBodyParsedResult = TextBodySchema.safeParse(result)

  if (textBodyParsedResult.success) {
    return textBodyParsedResult.data
  }

  const parsedResult = BodySchema.safeParse(result)

  if (!parsedResult.success) {
    throw parsedResult.error
  }

  let unSupportedType: never
  const parsedType = parsedResult.data.payloads[0].media_type

  switch (parsedType) {
    case PayloadTypeEnum.Text:
      return TextBodySchema.parse(result)

    case PayloadTypeEnum.CustomText:
      return CustomTextBodySchema.parse(result)

    case PayloadTypeEnum.HTMLContent:
      return HTMLContentBodySchema.parse(result)

    case PayloadTypeEnum.Media:
      return MediaBodySchema.parse(result)

    case PayloadTypeEnum.Carousel:
      return CarouselBodySchema.parse(result)

    case PayloadTypeEnum.QuickReplies:
      return QuickRepliesBodySchema.parse(result)

    case PayloadTypeEnum.Buttons:
      return ButtonBodySchema.parse(result)

    case PayloadTypeEnum.MultiContent:
      return MultiContentBodySchema.parse(result)

    default:
      unSupportedType = parsedType
      throw new Error('Unsupported type: ' + unSupportedType)
  }
}
