import { type ReactNode, useLayoutEffect, useMemo, useState } from 'react'

import { PortalContext, type PortalContextType } from './PortalContext.tsx'

type PortalProviderProps = {
  children: ReactNode
}

const CONTAINER_ID = 'tdw-portal-container'

export function PortalProvider(props: PortalProviderProps) {
  const { children } = props
  const [portal, setPortal] = useState<HTMLElement | null>(null)

  useLayoutEffect(() => {
    const portalContainer = document.getElementById(CONTAINER_ID)

    setPortal(portalContainer)
  }, [])

  const contextValue = useMemo<PortalContextType>(() => {
    return {
      element: portal
    }
  }, [portal])

  return (
    <PortalContext.Provider value={contextValue}>
      {portal ? children : null}
      <div id={CONTAINER_ID}></div>
    </PortalContext.Provider>
  )
}
