import type { RichTextItem } from '@td-org/chat-widget-shared'

import type { TwilioHTMLContentPayload } from '@/utils/twilio'

export function convertHTMLContentPayload(payload: TwilioHTMLContentPayload): RichTextItem[] {
  return payload.content.map((html) => {
    return {
      type: 'rich_text',
      html
    }
  })
}
