import { ReactElement } from 'react'
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react'

import { BottomFixedAreaContainer } from '@td-org/chat-widget-components'
import { Page } from '@td-org/chat-widget-shared'

import { type PageType } from '@/stores/app/store.ts'
import { useChatConfig } from '@/stores/config.ts'
import clsx from '@/utils/clsx.ts'

import classes from './BottomFixedArea.module.scss'

type BottomFixedAreaProps = {
  page: PageType
}

const BottomFixedArea = (props: BottomFixedAreaProps): ReactElement | null => {
  const { page } = props

  const { enableUserInput } = useChatConfig()

  if (page !== Page.Conversation) {
    return null
  }

  return (
    <OverlayScrollbarsComponent
      className={clsx(classes.bottomFixedArea, {
        [classes.paddingBottom]: !enableUserInput
      })}
      options={{ scrollbars: { autoHide: 'scroll' } }}
    >
      <BottomFixedAreaContainer />
    </OverlayScrollbarsComponent>
  )
}

export default BottomFixedArea
