import React from 'react'
import { animated, config, useSpring } from '@react-spring/web'

export interface AnimatePageProps {
  disable?: boolean
  children?: React.ReactNode
  className?: string
}

function AnimatePage(props: AnimatePageProps) {
  const { disable = false, children, className = '' } = props

  const animateStyle = useSpring({
    from: { opacity: 0 },
    to: { opacity: 1 },
    config: config.gentle,
    immediate: disable
  })

  return (
    <animated.div style={animateStyle} className={className}>
      {children}
    </animated.div>
  )
}

export default AnimatePage
