import type { ComponentProps } from 'react'
import Linkify from 'linkify-react'

import { DEFAULT_OPTIONS } from '@td-org/chat-widget-shared'

import { optionsSelector, useConfig } from '@/stores/config'

export type Props = {
  className?: string
  target?: ComponentProps<typeof Linkify>['children']
}

const TextLink = (props: Props): JSX.Element | null => {
  const { target, className = 'custom-bot' } = props
  const { linkProtocol, linkTarget } = useConfig(optionsSelector)

  if (!target) return null

  return (
    <Linkify
      className={className}
      as='span'
      options={{
        defaultProtocol: linkProtocol || DEFAULT_OPTIONS.linkProtocol,
        target: linkTarget || DEFAULT_OPTIONS.linkTarget,
        rel: 'noopener noreferrer'
      }}
    >
      {target}
    </Linkify>
  )
}

export default TextLink
