import omit from 'lodash.omit'
import { create } from 'zustand'
import { persist, subscribeWithSelector } from 'zustand/middleware'
import { useShallow } from 'zustand/react/shallow'

import type { Message, Role } from '@td-org/chat-widget-shared'

import { type UserInput } from '@/stores/app/connection/types'
import { provider } from '@/stores/app/provider'
import { useApp } from '@/stores/app/store.ts'
import { useRpc } from '@/stores/rpc.ts'
import { buildTypingMessage, isTypingMessage, removeTypingMessage } from '@/utils/buildStaticMessages.ts'
import { findLastIndex } from '@/utils/findLastIndex.ts'
import { getWindowTouchpointId } from '@/utils/getWindowParams.ts'
import { generalSettingsSelector, useConfig } from '../config'

let timerId: NodeJS.Timeout | null = null

type State = {
  lastReadIndexByConversationSid: Record<string, number>
  assigneeNamesByConversationSid: Record<string, Record<string, string>>
  messages: Message[]
}

type Action = {
  clear: () => void
  add: (message: Message) => void
  set: (messages: Message[]) => void
  send: (input: UserInput) => Promise<void>
  update: (tid: string, message: Partial<Message>) => boolean
  remove: (tid: string) => boolean
  end: () => Promise<boolean>
  batchUpdate: (predicate: (message: Message) => boolean, message: Partial<Message>) => void
  updateLastReadIndex: () => void
  typing: () => Promise<void>
  showTypingMessage: () => void
  hideTypingMessage: () => void
  getAssigneeName: (id: string) => string
  setAssigneeName: (id: string, name: string) => void
}

export type Store = State & Action

const persistName = `td-message-${getWindowTouchpointId() || 'unknown-touchpoint-id'}`

export const useMessageStore = create<Store>()(
  subscribeWithSelector(
    persist(
      (set, get) => ({
        lastReadIndexByConversationSid: {},

        assigneeNamesByConversationSid: {},

        messages: [] as Message[],

        add: (message: Message) => {
          const { messages, lastReadIndexByConversationSid } = get()
          const conversationSid = useApp.getState().credentials.roomId

          const { sid, tid, alignment, index } = message

          if (sid !== '' && messages.find((msg) => msg.sid === sid)) {
            return
          }

          if (tid !== '' && messages.find((msg) => msg.tid === tid)) {
            return
          }

          const nextState = {
            lastReadIndexByConversationSid,
            messages
          }
          const isReceivedMessage = alignment === 'received'
          const isFrameOpened = useApp.getState().isFrameOpened

          if (conversationSid && index !== undefined && isReceivedMessage && isFrameOpened) {
            nextState.lastReadIndexByConversationSid[conversationSid] = index

            useApp.getState().updateUnreadMessageCount(1)
          }

          nextState.messages = [...messages, message]

          set(nextState)
        },

        set: (_messages: Message[]) => {
          const { messages, lastReadIndexByConversationSid } = get()
          const conversationSid = useApp.getState().credentials.roomId

          if (conversationSid) {
            let lastReadIndex = -1

            const receivedMessages = _messages
              .map((message) => {
                const { sid, tid, alignment, index } = message

                if (sid !== '' && messages.find((msg) => msg.sid === sid)) {
                  return
                }

                if (tid !== '' && messages.find((msg) => msg.tid === tid)) {
                  return
                }

                if (
                  conversationSid &&
                  index !== undefined &&
                  alignment === 'received' &&
                  useApp.getState().isFrameOpened
                ) {
                  lastReadIndex = index
                }

                return message
              })
              .filter(Boolean)

            const nextState = {
              lastReadIndexByConversationSid,
              messages
            }

            if (lastReadIndex > -1) {
              nextState.lastReadIndexByConversationSid[conversationSid] = lastReadIndex
            }

            if (receivedMessages.length) {
              nextState.messages = [...messages, ...receivedMessages]
            }

            set(nextState)
          }
        },

        clear: () => {
          const { lastReadIndexByConversationSid, assigneeNamesByConversationSid } = get()
          const conversationSid = useApp.getState().credentials.roomId

          set({
            messages: [],
            lastReadIndexByConversationSid: omit(lastReadIndexByConversationSid, conversationSid),
            assigneeNamesByConversationSid: omit(assigneeNamesByConversationSid, conversationSid)
          })
        },

        update: (tid: string, message: Partial<Message>) => {
          if (tid === '') {
            return false
          }

          const messages = [...get().messages]
          const index = messages.findIndex((message) => message.tid === tid)
          if (index > -1) {
            messages[index] = Object.assign({ ...messages[index] }, message)
            set({ messages })

            return true
          }

          return false
        },

        batchUpdate: (predicate: (message: Message) => boolean, message: Partial<Message>): void => {
          const messages = get().messages.map((msg) => {
            return predicate(msg) ? Object.assign(msg, message) : msg
          })

          set({ messages })
        },

        remove: (tid: string) => {
          if (tid === '') {
            return false
          }

          const messages = [...get().messages]
          const index = messages.findIndex((message) => message.tid === tid)
          if (index > -1) {
            messages.splice(index, 1)
            set({ messages })

            return true
          }

          return false
        },

        send: async (input: UserInput) => {
          await provider.sendMessage(input)
        },

        end: async () => {
          return await provider.end()
        },

        updateLastReadIndex: () => {
          const { messages, lastReadIndexByConversationSid } = get()
          const conversationSid = useApp.getState().credentials.roomId

          if (conversationSid) {
            const items = messages
              .filter((message) => message.alignment === 'received')
              .flatMap((message) => {
                return message.items.map((item) => {
                  return { ...item, index: message.index }
                })
              })

            const item = items[items.length - 1] || { index: -1 }

            if (item?.index !== undefined) {
              set({
                lastReadIndexByConversationSid: { ...lastReadIndexByConversationSid, [conversationSid]: item.index }
              })
            }
          }
        },

        typing: async () => {
          await provider.typing()
        },

        showTypingMessage: () => {
          const hasTypingIndicator = generalSettingsSelector(useConfig.getState()).showTypingIndicatorButton

          if (!hasTypingIndicator) {
            return
          }

          const { messages, add, hideTypingMessage } = get()

          const typingMode = useApp.getState().typingMode

          const hasTypingMessage = messages.find(({ tid }) => isTypingMessage(tid))

          if (typingMode === 'va') {
            timerId && clearTimeout(timerId)

            hasTypingMessage && hideTypingMessage()

            add(buildTypingMessage({ role: 'va' }))

            timerId = setTimeout(() => {
              hideTypingMessage()

              timerId = null
            }, 5000)

            return
          }

          if (typingMode === 'agent' && hasTypingMessage) {
            hideTypingMessage()

            add(buildTypingMessage({ role: 'agent' }))
          }
        },

        hideTypingMessage: () => {
          const { remove } = get()

          removeTypingMessage((id) => remove(id))
        },

        setAssigneeName: (id: string, name: string) => {
          if (!id) return ''

          const conversationSid = useApp.getState().credentials.roomId
          const assigneeNamesByConversationSid = get().assigneeNamesByConversationSid

          if (conversationSid) {
            if (!assigneeNamesByConversationSid[conversationSid]) {
              assigneeNamesByConversationSid[conversationSid] = {}
            }
            assigneeNamesByConversationSid[conversationSid][id] = name
            set({ assigneeNamesByConversationSid })
          }
        },

        getAssigneeName: (id: string) => {
          if (!id) return ''

          const { assigneeNamesByConversationSid } = get()
          const conversationSid = useApp.getState().credentials.roomId

          return assigneeNamesByConversationSid[conversationSid]?.[id] || ''
        }
      }),
      {
        name: persistName,
        partialize: (state) => ({
          assigneeNamesByConversationSid: state.assigneeNamesByConversationSid,
          lastReadIndexByConversationSid: state.lastReadIndexByConversationSid
        })
      }
    )
  )
)

const messagesSelector = (state: Store) => state.messages
const actionsSelector = (state: Store) => ({
  set: state.set,
  add: state.add,
  send: state.send,
  clear: state.clear,
  update: state.update,
  remove: state.remove,
  batchUpdate: state.batchUpdate,
  end: state.end,
  updateLastReadIndex: state.updateLastReadIndex,
  typing: state.typing,
  showTypingMessage: state.showTypingMessage,
  hideTypingMessage: state.hideTypingMessage,
  getAssigneeName: state.getAssigneeName,
  setAssigneeName: state.setAssigneeName
})
const unreadCountSelector = (state: Store): number => {
  const { messages, lastReadIndexByConversationSid } = state
  const conversationSid = useApp.getState().credentials.roomId

  const lastReadIndex = lastReadIndexByConversationSid[conversationSid] || -1

  const messageItems = messages
    .filter((message) => message.alignment === 'received')
    .flatMap((message) => {
      return message.items.map((item) => {
        return { ...item, index: message.index }
      })
    })

  const lastItem = messageItems[messageItems.length - 1]
  const lastItemMessageIndex = lastItem?.index ? lastItem.index : -1

  if (lastReadIndex >= lastItemMessageIndex) {
    return 0
  }

  const index = findLastIndex(messageItems, (item) => item.index === lastReadIndex)

  return messageItems.length - 1 - index
}
const typingMessageSelector = (state: Store): Message | null => {
  const messages = state.messages
  return messages.find((message) => isTypingMessage(message.tid)) || null
}

export const switchTypingMode = (state: Store): Role => {
  const { messages } = state

  if (!messages.length) return 'agent'

  const lastInboundMessage = messages.reduce(
    (last, message) => (message.alignment === 'received' && !isTypingMessage(message.tid) ? message : last),
    {}
  ) as Message

  return lastInboundMessage.role !== 'va' ? 'agent' : 'va'
}

export const useMessages = (): Message[] => useMessageStore(useShallow(messagesSelector))
export const useTypingMessage = (): Message | null => useMessageStore(useShallow(typingMessageSelector))
export const useMessagesAction = (): Action => useMessageStore(useShallow(actionsSelector))

useMessageStore.subscribe(unreadCountSelector, async (unread) => {
  await useRpc.getState().setBadge(unread)
})

useMessageStore.subscribe(switchTypingMode, async (typingMode: Role) => {
  useApp.setState({ typingMode })
})
