import { ZodError } from 'zod'

import type { MessageButton } from '@td-org/chat-widget-shared'

import logger from '@/utils/logger.ts'
import type { TwilioButtonPayload } from '@/utils/twilio'

export function convertButtonElements(elements: TwilioButtonPayload['quick_actions']['elements']): MessageButton[] {
  return elements
    .map((element) => {
      const { type, payload, display_text, phone_number, url } = element

      switch (type) {
        case 'payload':
          return {
            type: 'reply' as const,
            payload: payload ?? '',
            text: display_text
          }

        case 'call':
          return {
            type: 'call' as const,
            payload: `tel:${payload ?? phone_number}`,
            text: display_text
          }

        case 'link':
          return {
            type: 'link' as const,
            payload: payload ?? url ?? '',
            text: display_text,
            target: '_blank'
          }

        default:
          console.error('Invalid button type: ', type)
          break
      }
    })
    .filter(Boolean)
}

export function getLastMessageAndPrevious(
  messages: string[],
  defaultLastMessage = ''
): {
  lastMessage: string
  previousMessages: string[]
} {
  if (messages.length === 0) {
    return {
      lastMessage: defaultLastMessage,
      previousMessages: []
    }
  }

  if (messages.length === 1) {
    return {
      lastMessage: messages[0],
      previousMessages: []
    }
  }

  const lastMessage = messages[messages.length - 1]
  const previousMessages = messages.slice(0, -1)

  return {
    lastMessage,
    previousMessages
  }
}

export function schemaErrorHandler(error: unknown): string {
  let errorMessage: string
  if (error instanceof ZodError) {
    error.issues.forEach((issue) => {
      logger.error(
        `${issue.message}: ${issue.path
          .map((value) => {
            if (typeof value === 'number') {
              return `[${value}]`
            }
            return value
          })
          .join('.')}`
      )
    })
    errorMessage = error.issues.map((issue) => issue.message).join('\n')
  } else if (error instanceof Error) {
    errorMessage = error.message
    logger.error(error)
  } else {
    errorMessage = 'Unknown error'
  }

  return errorMessage
}
