import { nanoid } from 'nanoid'

import { BoardCastEvent, connectionProvider, Media, MediaType, Page } from '@td-org/chat-widget-shared'

import { Store as AppStore, useApp } from '@/stores/app/store'
import { useConfig } from '@/stores/config.ts'
import { useMessageStore } from '@/stores/message'
import { useBoardCast } from '@/stores/useBoardCast'
import { buildMediaMessage, buildTextMessage } from '@/utils/buildStaticMessages.ts'

import type {
  ConnectionProvider,
  ProviderConnectionImp,
  ProviderInitScene,
  ProviderPreloadClientScene,
  UserInput
} from './connection/types'
import { twilioConnection } from './connection'

class Provider {
  private readonly _current: ConnectionProvider | null = null
  private readonly _providerImps: Record<ConnectionProvider, ProviderConnectionImp> = {
    [connectionProvider.twilio]: twilioConnection
  }

  constructor() {
    this._current = connectionProvider.twilio
    this.listenerPageEvents()
    this.listenerTabEvents()
  }

  private listenerPageEvents() {
    useApp.getState().on('leave_conversation', () => {
      useApp.getState().reset(['unreadMessageCount'])
    })
    useApp.getState().on('enter_error', () => {
      this.clear()
    })
  }

  private listenerTabEvents() {
    useBoardCast.getState().on<Pick<AppStore, 'credentials' | 'identity'>>(BoardCastEvent.preloadClient, (payload) => {
      const { credentials, identity } = payload
      useApp.getState().setCredentials({
        accessToken: credentials.accessToken,
        region: credentials.region,
        createAccessTokenAt: credentials.createAccessTokenAt
      })
      useApp.getState().createIdentity(true, identity)
      this.preloadClient('tabEvent')
    })

    useBoardCast
      .getState()
      .on<Pick<AppStore, 'credentials' | 'identity'>>(BoardCastEvent.conversationCreated, (payload) => {
        const { credentials, identity } = payload
        useApp.getState().setCredentials(credentials)
        useApp.getState().createIdentity(true, identity)

        if (useApp.getState().isNot(Page.Conversation)) {
          useApp.getState().go(Page.Conversation)
        }
        this.init('tabEvent')
      })

    useBoardCast.getState().on(BoardCastEvent.conversationEnded, () => {
      useApp.getState().enableLoadFeedback()
      if (useApp.getState().isNot(Page.End)) {
        useApp.getState().go(Page.End)
      }
    })

    useBoardCast.getState().on(BoardCastEvent.conversationEndedByError, () => {
      if (useApp.getState().isNot(Page.Error)) {
        useApp.getState().go(Page.Error)
      }
    })

    useBoardCast.getState().on(BoardCastEvent.resetFrame, () => {
      const initialPage = useApp.getState().getInitialPage()
      if (useApp.getState().isNot(initialPage)) {
        useApp.getState().go(initialPage)
        this.clear()
      }
    })

    useBoardCast.getState().on(BoardCastEvent.enableScreenLoading, (time: number) => {
      useApp.getState().enableScreenLoading(time, false)
    })
  }

  public preloadClient(scene: ProviderPreloadClientScene) {
    if (useConfig.getState().config?.enableSplitTwilioBootstrap && this.connection) {
      return this.connection.preloadClient(scene)
    }
  }

  public async init(scene: ProviderInitScene) {
    if (this.connection) {
      return this.connection.init(scene)
    }
    return false
  }

  public clear() {
    if (this.connection) {
      return this.connection.destroy()
    }
  }

  public async sendMessage(userInput: UserInput) {
    if (this.connection) {
      if (useApp.getState().isNot('conversation')) {
        return false
      }

      if (!useApp.getState().hasInitialized) {
        this.init('sendMessage')
      }

      const { data, type } = userInput
      const isMedia = data instanceof FormData
      const tid = nanoid()
      const isMediaType = isMedia && Object.values(Media).indexOf(type) > -1

      if (!isMedia || isMediaType) {
        const preMessage = !isMedia
          ? buildTextMessage(tid, data as string, { role: 'contact' })
          : buildMediaMessage(tid, data as FormData, type as MediaType, { role: 'contact' })
        useMessageStore.getState().add(preMessage)
      }
      return this.connection.send(userInput, tid)
    }
    return false
  }

  public typing() {
    if (this.connection) {
      return this.connection.typing()
    }
  }

  public async end() {
    if (this.connection) {
      return this.connection.end()
    }
    return false
  }

  get status() {
    if (this.connection) {
      return this.connection.status
    }
    return 'deactivated'
  }

  get connection() {
    if (this._current) {
      return this._providerImps[this._current]
    }
    return null
  }
}

const provider = new Provider()

export { provider }
