import TextMessage from '../TextMessage'

import Buttons from './index.ts'
import { VaButtonsProps } from './types.ts'

import classes from './Buttons.module.scss'

const VaButtons = ({ text, linkProtocol, linkTarget, buttons, onButtonClick }: VaButtonsProps) => {
  return (
    <div className={classes.vaButtons}>
      <TextMessage className={classes.buttonsText} text={text} linkProtocol={linkProtocol} linkTarget={linkTarget} />
      <Buttons buttons={buttons} onClick={onButtonClick} />
    </div>
  )
}

export default VaButtons
