import { type ReactElement, useMemo } from 'react'
import { useTranslation } from 'react-i18next'

import { Dialog } from '@/components/Dialog'
import { Confirm } from '@/components/Icons'
import { optionsSelector, useConfig } from '@/stores/config'

interface Props {
  visible: boolean
  isLoading?: boolean
  onConfirm: () => void
  onCancel: () => void
}

const ConfirmEndChat = (props: Props): ReactElement => {
  const { visible, isLoading = false, onConfirm, onCancel } = props
  const [t] = useTranslation()

  const { chatDialogBoxEndChat, chatConfirmButtonEndChat, chatCancelButtonEndChat } = useConfig(optionsSelector)

  const i18n = useMemo(
    () => ({
      chatDialogBoxEndChatTitle: t('chatConfirmButtonEndChat'),
      chatDialogBoxEndChatDescription: chatDialogBoxEndChat || t('chatDialogBoxEndChat'),
      chatConfirmButtonEndChat: chatConfirmButtonEndChat || t('chatConfirmButtonEndChat'),
      chatCancelButtonEndChat: chatCancelButtonEndChat || t('chatCancelButtonEndChat')
    }),
    [t, chatDialogBoxEndChat, chatConfirmButtonEndChat, chatCancelButtonEndChat]
  )

  return (
    <Dialog
      visible={visible}
      title={i18n.chatDialogBoxEndChatTitle}
      description={i18n.chatDialogBoxEndChatDescription}
      isLoading={isLoading}
      onConfirm={onConfirm}
      onCancel={onCancel}
      confirmText={i18n.chatConfirmButtonEndChat}
      cancelText={i18n.chatCancelButtonEndChat}
      icon={<Confirm />}
    />
  )
}

export default ConfirmEndChat
