import i18next from 'i18next'
import { create } from 'zustand'
import { useShallow } from 'zustand/react/shallow'

import {
  type AllConfig,
  type AppConfig,
  GET_CREDENTIALS_TYPE,
  type PreviewConfig,
  RpcConstants
} from '@td-org/chat-widget-shared'
import RPC, { type RPCResponse } from '@td-org/digital-postmessage-rpc'

import { formatTime, getTimezone } from '@/utils/time.ts'

import { type Store as AppStore, useApp } from './app/store.ts'
import { useOriginalConfig } from './config.ts'

type Store = {
  frameReady: () => Promise<RPCResponse<void>>
  frameResize: (payload: { height?: number | string }) => Promise<RPCResponse<void>>
  maximize: () => Promise<RPCResponse<void>>
  minimize: () => Promise<RPCResponse<void>>
  getAllConfig: () => Promise<RPCResponse<AllConfig>>
  getCredentials: (payload: {
    identity: string
    custom_fields?: Record<string, unknown>
    bootstrap_type?: (typeof GET_CREDENTIALS_TYPE)[keyof typeof GET_CREDENTIALS_TYPE]
  }) => Promise<RPCResponse<Omit<AppStore['credentials'], 'createAccessTokenAt' | 'tdAccessToken'>>>
  setBadge: (count: number) => Promise<RPCResponse<void>>
  downloadTranscript: (interactionId: string) => Promise<RPCResponse<boolean>>
  previewReady: () => Promise<void>
  onPreviewConfigChange: (callback: (previewConfig: PreviewConfig) => void) => void
  conversationStart: () => Promise<RPCResponse<void>>
}

export const useRpc = create<Store>(() => {
  const rpc = new RPC({
    name: 'Widget',
    sender: window.parent,
    topic: RpcConstants.Topic,
    debug: import.meta.env.DEV
  })

  rpc.register(RpcConstants.Methods.FrameOpened, () => {
    useApp.getState().setIsFrameOpened(true)
  })

  rpc.register(RpcConstants.Methods.UpdateAppConfigs, (payload: { appConfig: AppConfig }) => {
    useOriginalConfig.getState().update(payload)
  })

  rpc.register(RpcConstants.Methods.UpdateContext, (payload: Record<string, string>) => {
    useOriginalConfig.getState().updateContext(payload)
  })

  const getTdToken = async () => {
    const response = await rpc.trigger<string>(RpcConstants.Methods.GetTdAccessToken)
    if (response?.data) {
      useApp.getState().setCredentials({ tdAccessToken: response.data })
      return response.data
    }
    return ''
  }

  return {
    frameReady: () => {
      return rpc.trigger<void>(RpcConstants.Methods.FrameReady)
    },
    frameResize: (payload: { height?: number | string }) => {
      return rpc.trigger<void>(RpcConstants.Methods.FrameResize, payload)
    },
    maximize: () => {
      return rpc.trigger<void>(RpcConstants.Methods.Maximize)
    },
    minimize: async () => {
      const result = await rpc.trigger<void>(RpcConstants.Methods.Minimize)
      useApp.getState().setIsFrameOpened(false)
      return result
    },
    getAllConfig: (): Promise<RPCResponse<AllConfig>> => {
      return rpc.trigger<AllConfig>(RpcConstants.Methods.GetAllConfig)
    },
    getCredentials: (payload: {
      identity: string
      custom_fields?: Record<string, unknown>
      init_conversation?: boolean
    }): Promise<RPCResponse<Omit<AppStore['credentials'], 'createAccessTokenAt' | 'tdAccessToken'>>> => {
      return rpc.trigger<AppStore['credentials']>(RpcConstants.Methods.GetCredentials, payload)
    },
    setBadge: (count: number) => {
      return rpc.trigger<void>(RpcConstants.Methods.SetBadge, count)
    },
    downloadTranscript: async (interactionId: string) => {
      const fetchDownloadTranscript = (token: string) => {
        const fileName = `${i18next.t('downloadTranscriptFileName')}-${formatTime(new Date(), 'YYYY-MM-DD-HH-mm')}.txt`
        const timezone = getTimezone()
        return rpc.trigger<boolean>(RpcConstants.Methods.DownloadTranscript, {
          token,
          interactionId,
          fileName,
          timezone
        })
      }

      let token = useApp.getState().credentials.tdAccessToken
      if (!token) {
        token = await getTdToken()
      }

      const result = await fetchDownloadTranscript(token)

      if (!result?.data) {
        token = await getTdToken()
        return fetchDownloadTranscript(token)
      }

      return result
    },
    previewReady: async (): Promise<void> => {
      await rpc.trigger(RpcConstants.Methods.PreviewReady)
    },
    onPreviewConfigChange: (callback) => {
      rpc.register(RpcConstants.Methods.UpdatePreviewConfig, (config: PreviewConfig) => {
        callback(config)
      })
    },
    conversationStart: () => rpc.trigger(RpcConstants.Methods.ConversationStart)
  }
})

export const useRpcAction = () =>
  useRpc(
    useShallow((state) => ({
      maximize: state.maximize,
      frameResize: state.frameResize
    }))
  )
