import { type ReactNode } from 'react'
import clsx from 'clsx'

import type { MessageTypes } from '@td-org/chat-widget-shared'

import { getBoxVariation } from './getVariations.ts'

import classes from './ChatBubbleBox.module.scss'

interface MessageBoxProps {
  type: MessageTypes
  className?: string
  children?: ReactNode
}

const MessageBox = ({ type, className, children }: MessageBoxProps) => {
  return (
    <div className={clsx(classes.message, getBoxVariation(type), className)} data-testid='message-box'>
      {children}
    </div>
  )
}

export default MessageBox
