export function requiredErrorMessage(field: string, owner: string) {
  return `${field} field of type '${owner}' is required`
}

export function invalidTypeErrorMessage(field: string, owner: string, expectedType: string = 'string') {
  return `'${field}' field of type '${owner}' must be a ${expectedType}`
}

export function nonEmptyArrayErrorMessage(field: string, owner: string) {
  return `${field} field of type '${owner}' must be a non-empty array`
}
