import clsx from 'clsx'

import { MessageAvatar } from '@td-org/chat-widget-shared'

import Avatar from '../Avatar/Avatar'

import classes from './ChatBubble.module.scss'

type BubbleAvatarProps = {
  url?: string
  align?: string
  type?: MessageAvatar['type']
}

function BubbleAvatar(props: BubbleAvatarProps): JSX.Element | undefined {
  const { url, align, type } = props

  return <Avatar className={clsx({ [classes.bottom]: align === 'bottom' })} url={url} type={type} />
}

export default BubbleAvatar
