import { useTranslation } from 'react-i18next'

import { useConfig } from '@/stores/config'

import classes from './ChatEndedMessage.module.scss'

export const ChatEndedMessage = () => {
  const endedChatMessage = useConfig((state) => state.config.options.endedChatMessage)
  const [t] = useTranslation()

  return (
    <p className={classes.text} data-qa='chat ended'>
      {endedChatMessage || t('endedChatMessage')}
    </p>
  )
}
