import { type ReactNode } from 'react'

export type Role = 'contact' | 'agent' | 'va' | 'system'

export type TextItem = {
  type: 'text'
  text: string
}

export type RichTextItem = {
  type: 'rich_text'
  html: string
}

export type MessageButton =
  | {
      type: 'link'
      payload: string // url
      target: string
      text: string
    }
  | {
      type: 'call'
      payload: string // phone number
      text: string
    }
  | {
      type: 'reply'
      payload: string // content
      text: string
    }

export type ButtonType = MessageButton['type']

export type CarouselCard = {
  title: string
  description?: string
  imageUrl?: string
  buttons: MessageButton[]
}

export type CarouselItem = {
  type: 'carousel'
  cards: CarouselCard[]
}

export const Media = {
  Image: 'image',
  Video: 'video',
  Audio: 'audio',
  File: 'file'
} as const

export type MediaType = (typeof Media)[keyof typeof Media]

export type MediaItem = {
  type: MediaType
  url?: string
  ignoreValid?: boolean
  provider?: 'twilio'
  getContentTemporaryUrl?: () => Promise<string | null>
  smart_text?: {
    key: string
    options?: Record<string, string>
  }
  children?: ReactNode
}

export type ButtonsItem = {
  type: 'buttons'
  text: string
  buttons: MessageButton[]
}

export type QuickReplyItem = {
  type: 'quick_replies'
  text: string
  replies: MessageButton[]
}

export type ErrorItem = {
  type: 'error'
  error_message: string
}

export type TypingMessageItem = {
  type: 'typing'
}

export type SystemMessageActionType = 'assign'

export type SystemMessageItem = {
  type: 'system'
  action_type: SystemMessageActionType
  smart_text?: {
    key: string
    options?: Record<string, string>
  }
  children?: ReactNode
}

export type MessageItem =
  | TextItem
  | RichTextItem
  | CarouselItem
  | MediaItem
  | ButtonsItem
  | QuickReplyItem
  | TypingMessageItem
  | SystemMessageItem
  | ErrorItem

export type MessageTypes = MessageItem['type']

export type MessageAlignment = 'sent' | 'received'

export type MessageAvatar = {
  url: string
  type?: 'system' | 'agent'
}

export type MessageBaseInfo = {
  index?: number
  /**
   * tid: temporary id
   * Setting tid is because when sending a message, we don't know its sid yet,
   * but we need to immediately display this message and set its status to 'sending'.
   * Only when we receive this message again from Twilio's push message will we set it to 'success' status.
   * We need a tid to match the sent message with the received message.
   */
  tid: string
  sid: string
  rid?: string // unique id for role
  status: 'sending' | 'failure' | 'success'
  alignment: MessageAlignment
  timestamp: number
  formattedTime: string
  role?: Role
  avatar?: MessageAvatar
  participantName?: string
  resend?: () => void
}

export type MessageBubble = MessageItem & MessageBaseInfo

export type Message = MessageBaseInfo & {
  items: MessageItem[]
}

export type MessageParticipantType = 'UNKNOWN' | 'CONTACT' | 'VIRTUAL_AGENT' | 'LIVE_AGENT'

export type MessageAttributesAssignAction = {
  name: 'digital_interaction_assigned'
  metadata: {
    cur_display_name: string
    cur_identified_id: string
    cur_participant_type: MessageParticipantType
    pre_participant_type?: MessageParticipantType
    pre_identified_id?: string
  }
}

export type MessageAttributes = {
  tid?: ''
  agentId?: string
  isReachabilityMessage: boolean
  is_virtual_agent: boolean
  action?: MessageAttributesAssignAction
}
