import { context, Span, trace } from '@opentelemetry/api'
import { LogAttributes, logs, SeverityNumber } from '@opentelemetry/api-logs'
import { ZoneContextManager } from '@opentelemetry/context-zone'
import { OTLPLogExporter } from '@opentelemetry/exporter-logs-otlp-proto'
import { OTLPTraceExporter } from '@opentelemetry/exporter-trace-otlp-proto'
import { Resource, ResourceAttributes } from '@opentelemetry/resources'
import { LoggerProvider, SimpleLogRecordProcessor } from '@opentelemetry/sdk-logs'
import { SimpleSpanProcessor, Tracer, WebTracerProvider } from '@opentelemetry/sdk-trace-web'
import { SEMRESATTRS_SERVICE_NAME } from '@opentelemetry/semantic-conventions'

type Context = ResourceAttributes
type LoggerRecord = {
  severityNumber: number
  severityText: string
} & LogAttributes
type Logger = (message: string, record: LoggerRecord) => void
type ProviderPayload = {
  context: Context
  proxyUrl?: string
}
export type ProviderLoggerType = 'debug' | 'info' | 'error' | 'warn'

export interface Provider {
  logger?: Logger

  debug: (name: string, details: Record<string, unknown>) => void
  info: (name: string, details: Record<string, unknown>) => void
  error: (name: string, details: Record<string, unknown>) => void
  warn: (name: string, details: Record<string, unknown>) => void
  startSpan: (name: string, details: Record<string, unknown>) => Span | null
  startChildSpan: (name: string, parent: Span | null | undefined, details: Record<string, unknown>) => Span | null
}

const { VITE_DYNATRACE_API_URL, VITE_DYNATRACE_ACCESS_TOKEN, VITE_DYNATRACE_OTEL_SERVICE_NAME } = import.meta.env

const parseAccessToken = (target: unknown) => {
  try {
    return window.atob(target as string)
  } catch (e) {
    console.warn('Failed to parse provider reporter api key', e)
    return target
  }
}

const accessToken = parseAccessToken(VITE_DYNATRACE_ACCESS_TOKEN)

class DynatraceProvider implements Provider {
  static instance: DynatraceProvider
  logger?: Logger
  tracer?: Tracer

  constructor({ context, proxyUrl }: ProviderPayload) {
    const exporter = this.createExporter(proxyUrl)
    const traceExporter = this.createTraceExporter(proxyUrl)

    this.logger = this.createLogger(exporter, context)
    this.tracer = this.createTracer(traceExporter, context)
  }

  createExporter(proxyUrl?: string) {
    return new OTLPLogExporter({
      url: `${proxyUrl || VITE_DYNATRACE_API_URL}/v1/logs`,
      headers: {
        Authorization: `Api-Token ${accessToken}`
      }
    })
  }

  createTraceExporter(proxyUrl?: string) {
    return new OTLPTraceExporter({
      url: `${proxyUrl || VITE_DYNATRACE_API_URL}/v1/traces`,
      headers: {
        Authorization: `Api-Token ${accessToken}`
      }
    })
  }

  createLogger(exporter: OTLPLogExporter, context: Context): Logger | undefined {
    if (!exporter) return

    const resource = new Resource({ [SEMRESATTRS_SERVICE_NAME]: VITE_DYNATRACE_OTEL_SERVICE_NAME, ...context })
    const provider = new LoggerProvider({ resource })

    provider.addLogRecordProcessor(new SimpleLogRecordProcessor(exporter))
    logs.setGlobalLoggerProvider(provider)

    const logger = provider.getLogger(VITE_DYNATRACE_OTEL_SERVICE_NAME)

    return (message, { severityNumber, severityText, ...attributes }) => {
      logger.emit({ body: message, severityNumber, severityText, attributes })
    }
  }

  createTracer(exporter: OTLPTraceExporter, context: Context) {
    if (!exporter) return

    const resource = new Resource({ [SEMRESATTRS_SERVICE_NAME]: VITE_DYNATRACE_OTEL_SERVICE_NAME, ...context })
    const provider = new WebTracerProvider({ resource })

    provider.addSpanProcessor(new SimpleSpanProcessor(exporter))
    provider.register({ contextManager: new ZoneContextManager() })
    trace.setGlobalTracerProvider(provider)

    return provider.getTracer(VITE_DYNATRACE_OTEL_SERVICE_NAME)
  }

  debug(message: string, details = {}) {
    this.escapeLogger?.(message, {
      ...details,
      severityNumber: SeverityNumber.DEBUG,
      severityText: 'DEBUG'
    })
  }

  info(message: string, details = {}) {
    this.escapeLogger?.(message, {
      ...details,
      severityNumber: SeverityNumber.INFO,
      severityText: 'INFO'
    })
  }

  error(message: string, details = {}) {
    this.escapeLogger?.(message, {
      ...details,
      severityNumber: SeverityNumber.ERROR,
      severityText: 'ERROR',
      'otel.status_code': 'ERROR'
    })
  }

  warn(message: string, details = {}) {
    this.escapeLogger?.(message, {
      ...details,
      severityNumber: SeverityNumber.WARN,
      severityText: 'WARN'
    })
  }

  startSpan(name: string, defaultAttributes = {}) {
    const span = this.tracer?.startSpan(name)
    if (span) {
      span.setAttributes({
        ...defaultAttributes,
        startAt: new Date().toISOString()
      })
      return span
    }
    return null
  }

  startChildSpan(name: string, parent: Span | null | undefined, defaultAttributes = {}) {
    if (!parent) {
      return null
    }
    const ctx = trace.setSpan(context.active(), parent)
    const span = this.tracer?.startSpan(name, {}, ctx)
    if (span) {
      span.setAttributes({
        ...defaultAttributes,
        startAt: new Date().toISOString()
      })
      return span
    }
    return null
  }

  get escapeLogger() {
    if (!this.logger) {
      console.error('Can not reach to the dynatrace logger!')
      return
    }

    return this.logger
  }
}

export function createDynatraceProvider({ context, proxyUrl }: ProviderPayload) {
  if (!DynatraceProvider.instance) {
    DynatraceProvider.instance = new DynatraceProvider({ context, proxyUrl })
  }

  return DynatraceProvider.instance
}
