export function getFileMimes(mime = ''): { prefix: string; suffix: string } {
  try {
    const [prefix, suffix] = mime.split('/')

    return {
      prefix,
      suffix
    }
  } catch (e) {
    return {
      prefix: '',
      suffix: ''
    }
  }
}

export const ACCEPT_MIME_TYPES_MAP = [
  'audio/mpeg',
  'audio/mp3',
  'image/jpeg',
  'image/png',
  'video/mp4',
  'video/quicktime',
  'application/pdf'
] as const

export const acceptFileType = ACCEPT_MIME_TYPES_MAP.join(',')
