import { initReactI18next } from 'react-i18next'
import i18n from 'i18next'

import { SupportRTLLanguageCodes } from '@td-org/chat-widget-shared'

function I18nConfig() {
  const defaultLang = 'en-US'

  let currentLanguage = defaultLang
  let initialized = false
  let resources = null
  let supportedLngs: string[] = []

  const getSelectedLang = (lang = '') => {
    const selectedLang = lang || navigator.language || defaultLang

    if (supportedLngs.indexOf(selectedLang) > -1) {
      return selectedLang
    }

    const findLang = supportedLngs.find(
      (item) => item.toLocaleLowerCase().indexOf(selectedLang.toLocaleLowerCase()) > -1
    )
    if (findLang) return findLang

    if (/^(ar|AR)/.test(selectedLang)) {
      return SupportRTLLanguageCodes[0]
    }

    return defaultLang
  }

  const initialize = async (lang = '') => {
    initialized = true
    resources = (await import('@/locales')).default
    supportedLngs = Object.keys(resources)

    currentLanguage = getSelectedLang(lang)

    await i18n.use(initReactI18next).init({
      lng: currentLanguage,
      fallbackLng: defaultLang,
      resources,
      keySeparator: false,
      supportedLngs,
      interpolation: {
        escapeValue: false
      },
      react: {
        useSuspense: false
      }
    })

    return currentLanguage
  }

  return {
    changeLanguage: async (lang = '') => {
      if (!initialized) {
        return await initialize(lang)
      }
      if (lang === currentLanguage) {
        return currentLanguage
      }
      currentLanguage = getSelectedLang(lang)
      await i18n.changeLanguage(currentLanguage)
      return currentLanguage
    },
    get current() {
      return currentLanguage
    }
  }
}

const i18nConfig = I18nConfig()

export default i18nConfig
