import { nanoid } from 'nanoid'
import { create } from 'zustand'
import { persist, subscribeWithSelector } from 'zustand/middleware'

import {
  AppEvent,
  BoardCastEvent,
  LayoutDirection,
  Page,
  type Role,
  safeJsonToString,
  SupportRTLLanguageCodes,
  WIDGET_DEFAULT_HEIGHT
} from '@td-org/chat-widget-shared'

import { useConfig } from '@/stores/config.ts'
import { useBoardCast } from '@/stores/useBoardCast.ts'
import { getWindowTouchpointId } from '@/utils/getWindowParams.ts'
import baseLogger from '@/utils/logger.ts'

type PageTransitionsMapper = {
  [T in PageType as Exclude<PageType, T>]: {
    pageEnter: `enter_${T}`
    pageLeave: `leave_${T}`
    pageTransition: `${T}_to_${Exclude<PageType, T>}`
  }
}[PageType]

export type FeedbackStatus = {
  status:
    | 'init'
    | 'error'
    | 'SURVEY_INVALID'
    | 'SURVEY_NOT_AVAILABLE'
    | 'SURVEY_UNSUBMITTED'
    | 'SURVEY_SUBMITTED'
    | 'SURVEY_CLOSED'
  message: string
  height: number
  reason?: unknown
}
export type PageType = (typeof Page)[keyof typeof Page]
export type InitialScreenFormData = Record<PropertyKey, string> | null
export type AppEventHandler<T = unknown> = (params: T) => void
export type AppEvents = (typeof AppEvent)[keyof typeof AppEvent]
export type PageEvents = PageTransitionsMapper[keyof PageTransitionsMapper]

type State = {
  showScreenLoading: boolean
  layoutDirection: (typeof LayoutDirection)[keyof typeof LayoutDirection]
  page: PageType
  events: Partial<Record<AppEvents | PageEvents, AppEventHandler[]>>
  enableInitialForm: boolean
  formData: InitialScreenFormData
  hasInitialized: boolean
  userInputAreaRef: HTMLTextAreaElement | null
  interactionId: string
  shouldLoadFeedback: boolean
  feedbackLink: string
  feedbackStatus: FeedbackStatus
  typingMode: Role
  isFrameOpened: boolean
  identity: string
  credentials: {
    accessToken: string
    roomId: string
    region: string
    tdAccessToken: string
    createAccessTokenAt?: number
  }
  chatContentScroll: {
    headerVisible: boolean
    footerVisible: boolean
    scrollTop: number
    scrollOver: number
    scrollPercent: number
  }
  initialScreenScroll: {
    headerVisible: boolean
    footerVisible: boolean
  }
  initialScreenHeight: number
  unreadMessageCount: number
}
type StateKeys = keyof State

export type Action = {
  go: <T = unknown>(page: PageType, params?: T) => void
  on: <T = unknown>(event: AppEvents | PageEvents, cb: AppEventHandler<T>) => void
  off: <T = unknown>(event: AppEvents | PageEvents, cb: AppEventHandler<T>) => void
  emit: <T = unknown>(event: AppEvents, params?: T) => void
  reset: (key?: StateKeys | StateKeys[]) => void
  is: {
    (page: PageType): boolean
    (page: PageType[]): boolean
  }
  isNot: {
    (page: PageType): boolean
    (page: PageType[]): boolean
  }
  getInitialPage: () => 'initial_screen' | 'conversation'
  focusUserInputArea: () => void
  setIsFrameOpened: (target: boolean) => void
  setCredentials: (credentials: Partial<Store['credentials']>) => State['credentials']
  hasProviderCredentials: () => boolean
  createIdentity: (focus?: boolean, target?: string) => string
  setFormData: (target: InitialScreenFormData) => void
  setLayoutDirection: (languageCode: string) => void
  enableScreenLoading: (time: number, notifyTab?: boolean) => void
  hiddenScreenLoading: () => void
  updateChatContentScroll: (payload: Partial<State['chatContentScroll']>) => void
  updateInitialScreenScroll: (payload: Partial<State['initialScreenScroll']>) => void
  updateInitialScreenHeight: (payload: State['initialScreenHeight']) => void
  updateFeedbackStatus: (status: FeedbackStatus) => void
  enableLoadFeedback: () => void
  updateUnreadMessageCount: (count: number) => void
}

export type Store = State & Action

function getInitialState(): State {
  return {
    showScreenLoading: false,
    layoutDirection: LayoutDirection.ltr,
    page: Page.Start,
    events: {},
    enableInitialForm: false,
    formData: null,
    hasInitialized: false,
    userInputAreaRef: null,
    interactionId: '',
    shouldLoadFeedback: false,
    feedbackLink: '',
    feedbackStatus: {
      status: 'init',
      message: '',
      height: 0
    },
    typingMode: 'agent',
    isFrameOpened: false,
    identity: '',
    credentials: {
      accessToken: '',
      roomId: '',
      region: '',
      tdAccessToken: ''
    },
    chatContentScroll: {
      headerVisible: false,
      footerVisible: false,
      scrollTop: 0,
      scrollOver: 0,
      scrollPercent: 0
    },
    initialScreenScroll: {
      headerVisible: false,
      footerVisible: false
    },
    initialScreenHeight: WIDGET_DEFAULT_HEIGHT,
    unreadMessageCount: 0
  }
}

const logger = baseLogger.getSubLogger({ name: 'useApp' })
const persistName = `td-app-${getWindowTouchpointId() || 'unknown-touchpoint-id'}`
let timerScreenLoading: NodeJS.Timeout | null = null

export const useApp = create<Store>()(
  subscribeWithSelector(
    persist(
      (set, get) => ({
        ...getInitialState(),

        go: <T>(page: PageType, params?: T) => {
          const { page: curPage, events } = get()

          if (curPage !== page) {
            const leaveEvent = `leave_${curPage}` as PageEvents
            const enterEvent = `enter_${page}` as PageEvents
            const transitionEvent = `${curPage}_to_${page}` as PageEvents

            events[leaveEvent]?.forEach((cb: AppEventHandler) => cb(params))

            set({ page })

            events[enterEvent]?.forEach((cb: AppEventHandler) => cb(params))
            events[transitionEvent]?.forEach((cb: AppEventHandler) => cb(params))

            logger.debug(`Going from ${curPage} to ${page} with params=${safeJsonToString(params ?? {})}`)
          } else {
            logger.debug(`Going to same ${page} with params=${safeJsonToString(params ?? {})}`)
          }
        },

        getInitialPage: () => {
          const { enableInitialForm } = get()

          if (enableInitialForm) {
            return Page.InitialScreen
          } else {
            return Page.Conversation
          }
        },

        is: (page: PageType | PageType[]): boolean => {
          const pages = Array.isArray(page) ? page : [page]

          return pages.includes(get().page)
        },

        isNot: (page: PageType | PageType[]): boolean => {
          const pages = Array.isArray(page) ? page : [page]

          return !pages.includes(get().page)
        },

        on: <T>(event: AppEvents | PageEvents, cb: AppEventHandler<T>) => {
          set((state) => {
            if (!state.events[event]) {
              state.events[event] = []
            }
            state.events[event]?.push(cb as never)
            return state
          })
        },

        off: <T>(event: AppEvents | PageEvents, cb: AppEventHandler<T>) => {
          const { events } = get()
          if (events[event]) {
            set((state) => {
              state.events[event] = events[event]?.filter((handler) => handler !== cb)
              return state
            })
          }
        },

        reset: (key?: StateKeys | StateKeys[]) => {
          if (key) {
            const keys = Array.isArray(key) ? key : [key]
            const initialState = getInitialState()
            const nextStatePartial = keys.reduce((acc, k) => ({ ...acc, [k]: initialState[k] }), {} as Partial<State>)
            set(nextStatePartial)
          } else {
            set(getInitialState())
          }
        },

        emit: <T>(event: AppEvents, params?: T) => {
          const { events } = get()
          if (events[event]) {
            events[event]?.forEach((cb) => cb(params))
          }
        },

        focusUserInputArea: () => {
          const { userInputAreaRef } = get()
          userInputAreaRef?.focus()
        },

        setIsFrameOpened: (target: boolean) => {
          set({ isFrameOpened: target })
        },

        createIdentity: (focus: boolean = false, target: string = '') => {
          let current = get().identity

          if (focus || !current) {
            current =
              focus && target ? target : `widget.${useConfig.getState().config?.injectorWindowHostname}-${nanoid()}`
            set({ identity: current })
          }

          return current
        },

        setCredentials: (target: Partial<Store['credentials']>) => {
          const payload = { ...get().credentials, ...target }
          set({ credentials: payload })
          return payload
        },

        hasProviderCredentials: () => {
          const { accessToken, roomId } = get().credentials
          return !!(accessToken && roomId)
        },

        setFormData: (formData: InitialScreenFormData) => {
          set({ formData })
        },

        setLayoutDirection: (languageCode: string) => {
          const { layoutDirection } = get()
          const current = SupportRTLLanguageCodes.indexOf(languageCode) > -1 ? LayoutDirection.rtl : LayoutDirection.ltr
          if (layoutDirection !== current) {
            set({ layoutDirection: current })
          }
        },

        hiddenScreenLoading: () => {
          if (timerScreenLoading) {
            clearTimeout(timerScreenLoading)
            timerScreenLoading = null
          }

          if (get().showScreenLoading) {
            set({ showScreenLoading: false })
          }
        },

        enableScreenLoading: (time: number, notifyTab = false) => {
          if (!get().showScreenLoading) {
            set({ showScreenLoading: true })

            if (notifyTab) {
              useBoardCast.getState().emit(BoardCastEvent.enableScreenLoading, time)
            }

            timerScreenLoading = setTimeout(() => {
              set({ showScreenLoading: false })
              timerScreenLoading = null
            }, time)
          }
        },

        updateChatContentScroll: (payload: Partial<State['chatContentScroll']>) => {
          const current = { ...get().chatContentScroll, ...payload }
          if (current.footerVisible || current.scrollPercent === 100) {
            set({ chatContentScroll: current, unreadMessageCount: 0 })
          } else {
            set({ chatContentScroll: current })
          }
        },

        updateInitialScreenScroll: (payload: Partial<State['initialScreenScroll']>) => {
          set({ initialScreenScroll: { ...get().initialScreenScroll, ...payload } })
        },

        updateInitialScreenHeight: (height: State['initialScreenHeight']) => {
          set({ initialScreenHeight: height })
        },

        updateFeedbackStatus: (status: FeedbackStatus) => {
          logger.info(
            `Received feedback page message current=${safeJsonToString(status)} prev=${safeJsonToString(
              get().feedbackStatus
            )}`
          )
          set({ feedbackStatus: status })
        },

        enableLoadFeedback: () => {
          if (!get().shouldLoadFeedback) {
            set({ shouldLoadFeedback: true })
          }
        },

        updateUnreadMessageCount: (count: number) => {
          if (count > 0 && !get().chatContentScroll.footerVisible) {
            set({ unreadMessageCount: count })
          } else {
            set({ unreadMessageCount: 0 })
          }
        }
      }),
      {
        name: persistName,
        partialize: (state) =>
          getWindowTouchpointId()
            ? {
                isFrameOpened: state.isFrameOpened,
                credentials: state.credentials,
                identity: state.identity
              }
            : {}
      }
    )
  )
)
