import { useMemo } from 'react'
import { useTranslation } from 'react-i18next'

import { ChatBubble } from '@td-org/chat-widget-components'
import { AppEvent } from '@td-org/chat-widget-shared'

import Animate from '@/components/Animate'
import { useAppAction, useEnableRTLLayoutDirection } from '@/stores/app/selectors.ts'
import { optionsSelector, useConfig } from '@/stores/config.ts'
import { logForErrorBoundary, logForMessageMediaError } from '@/utils/logger-helper.ts'
import { isFirstInSeries, isLastInSeries, isSeries } from '../utils/isSeriesMessage'

import { type MessageItemProps } from './types.ts'

function noop() {}

function MessageBubble(props: MessageItemProps) {
  const { message, messageArray, index, onButtonClick } = props
  const { linkProtocol, linkTarget, botIconVerticalAlign } = useConfig(optionsSelector)
  const { type, alignment, formattedTime, participantName = '', avatar, resend } = message
  const [t] = useTranslation()

  const { emit } = useAppAction()
  const enableRTL = useEnableRTLLayoutDirection()

  const chatBubbleProps = {
    avatarVerticalAlign: botIconVerticalAlign,
    linkProtocol,
    linkTarget,
    errorMessageText: t('invalidRichMedia'),
    onButtonClick,
    onMediaSourceError: logForMessageMediaError,
    onMessageError: logForErrorBoundary
  }

  const isReceivedMessage = alignment === 'received'
  const isSentMessage = alignment === 'sent'
  const isTypingMessage = type === 'typing'
  const isSystemMessage = type === 'system'
  const isFileMediaMessage = type === 'file'
  const isMediaMessage = ['file', 'audio', 'video', 'image'].indexOf(type) > -1
  const needI18nMessage = isSystemMessage || isFileMediaMessage

  const isLastMessage = index === messageArray.length - 1
  const isInSeries = isSeries(message, messageArray[index - 1])
  const firstInSeries = isFirstInSeries(message, messageArray[index - 1])
  const lastInSeries = isLastInSeries(message, messageArray[index + 1])

  const hasAvatar = !isSystemMessage && firstInSeries && isReceivedMessage
  const hasName = !isSystemMessage && lastInSeries && !!participantName
  const hasTimestamp = !isSystemMessage && lastInSeries && !!formattedTime

  const i18nFormatTime = useMemo(() => {
    if (hasTimestamp) {
      return formattedTime.replace(/AM/gi, t('AM')).replace(/PM/gi, t('PM'))
    }
    return ''
  }, [t, hasTimestamp, formattedTime])

  const handleOnMediaLoadedData = () => {
    if (isSentMessage) {
      emit(AppEvent.need_scroll_to_bottom)
    }
  }

  if (needI18nMessage && message?.smart_text?.key) {
    message.children = t(message.smart_text.key, message.smart_text?.options || {})
  }

  return (
    <Animate.message immediate={isMediaMessage || isSystemMessage || isTypingMessage} isReceived={isReceivedMessage}>
      <ChatBubble
        {...message}
        {...chatBubbleProps}
        enableRTL={enableRTL}
        hasAvatar={hasAvatar}
        hasName={hasName}
        hasTimestamp={hasTimestamp}
        formattedTime={i18nFormatTime}
        avatarUrl={avatar?.url}
        avatarType={avatar?.type}
        participantName={t(participantName)}
        isInSeries={isInSeries}
        isLastMessage={isLastMessage}
        onResend={resend || noop}
        onMediaLoadedData={handleOnMediaLoadedData}
      />
    </Animate.message>
  )
}

export default MessageBubble
