import React from 'react'
import { animated, config, useSpring } from '@react-spring/web'

export interface AnimateMessageProps {
  isReceived: boolean
  immediate: boolean
  className?: string
  children?: React.ReactNode
}

function AnimateMessage(props: AnimateMessageProps) {
  const { immediate, className, children } = props

  const animateStyle = useSpring({
    from: { opacity: 0, transform: 'translateY(12px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    config: config.gentle,
    immediate
  })

  return (
    <animated.div className={className} style={animateStyle}>
      {children}
    </animated.div>
  )
}

export default AnimateMessage
