import { create } from 'zustand'
import { useShallow } from 'zustand/react/shallow'

import {
  BoardCastEvent,
  safeJsonParse,
  safeJsonToString,
  safeRemoveStorage,
  safeSetStorage
} from '@td-org/chat-widget-shared'

import { getWindowTouchpointId } from '@/utils/getWindowParams.ts'
import baseLogger from '@/utils/logger.ts'

type BoardCastEvents = (typeof BoardCastEvent)[keyof typeof BoardCastEvent]
type BoardCastHandler<T = unknown> = (...args: T[]) => void
type BoardCaseEventProcess = { event?: BoardCastEvents; data?: unknown[] }

interface BoardCastState {
  init(): void
  on<T = unknown>(event: BoardCastEvents, callback: BoardCastHandler<T>): void
  emit<T = unknown>(event: BoardCastEvents, ...parameters: T[]): void
  off<T = unknown>(event: BoardCastEvents, callback: BoardCastHandler<T>): void
  destroy(): void
  count(): Record<BoardCastEvents, BoardCastHandler[]>
}

const supportBroadcastChannel = () => typeof window.BroadcastChannel === 'function'
const logger = baseLogger.getSubLogger({ name: 'useBoardCast' })

const useBoardCast = create<BoardCastState>()(() => {
  const namespace = `td-board-cast-${getWindowTouchpointId() || 'unknown-touchpoint-id'}`
  const events = new Map<BoardCastEvents, BoardCastHandler[]>()

  let initialized = false
  let bc: BroadcastChannel | null = null

  const emitHandlers = (event?: BoardCastEvents, data: unknown[] = []) => {
    if (event) {
      const handlers = events.get(event)
      handlers?.forEach((handler) => handler(...data))
      logger.debug(`Received tab event=${event} data=${safeJsonToString(data)}`)
    }
  }

  const handleOnStorageChanged = (e: StorageEvent) => {
    if (e.key === namespace && e.newValue) {
      const { event, data } = safeJsonParse<BoardCaseEventProcess>(e.newValue, {})
      emitHandlers(event, data)
      safeRemoveStorage(namespace)
    }
  }

  return {
    init() {
      if (initialized) return

      if (supportBroadcastChannel()) {
        logger.debug(`Using tab events for broadcast channel namespace=${namespace}`)

        bc = new BroadcastChannel(namespace)
        bc.onmessage = (e: MessageEvent<BoardCaseEventProcess>) => {
          const { event, data } = e.data
          emitHandlers(event, data)
        }
      } else {
        logger.debug(`Using tab events for storage changed namespace=${namespace}`)

        window.addEventListener('storage', handleOnStorageChanged)
      }

      initialized = true
    },
    on<T = unknown>(event: BoardCastEvents, callback: BoardCastHandler<T>) {
      const handlers = events.get(event) || []
      events.set(event, [...handlers, callback] as BoardCastHandler[])
    },
    emit<T>(event: BoardCastEvents, ...parameters: T[]) {
      if (supportBroadcastChannel()) {
        bc?.postMessage({ event, data: parameters })
      } else {
        safeSetStorage(namespace, safeJsonToString({ event, data: parameters }))
      }
      logger.debug(`Emit tab from event=${event} data=${safeJsonToString(parameters)}`)
    },
    off<T>(event: BoardCastEvents, callback: BoardCastHandler<T>) {
      if (events.has(event)) {
        events.set(event, events.get(event)?.filter((handler) => handler !== callback) || [])
      }
    },
    destroy() {
      bc?.close?.()
      bc = null
      window.removeEventListener('storage', handleOnStorageChanged)
      events.clear()
      initialized = false
    },
    count() {
      const count = {} as Record<BoardCastEvents, BoardCastHandler[]>
      events.forEach((value, key) => {
        count[key] = value
      })
      return count
    }
  }
})

const actionSelector = (state: BoardCastState) => ({
  on: state.on,
  off: state.off,
  emit: state.emit
})

const useBoardCastAction = () => useBoardCast(useShallow(actionSelector))

export { useBoardCast, useBoardCastAction }
