import { useCallback, useEffect, useRef } from 'react'
import { useTranslation } from 'react-i18next'

import { AppEvent } from '@td-org/chat-widget-shared'
import RPC from '@td-org/digital-postmessage-rpc'

import { useAppAction, useFeedbackStatus } from '@/stores/app/selectors.ts'
import { type Store } from '@/stores/app/store.ts'

import classes from './Feedback.module.scss'

interface Props {
  feedbackLink: string
}

export const Feedback = ({ feedbackLink }: Props) => {
  const [t] = useTranslation()
  const iframeRef = useRef<HTMLIFrameElement | null>(null)
  const rpcRef = useRef<RPC | null>(null)
  const tid = useRef<NodeJS.Timeout | null>(null)
  const { showMessage, height, message, hidden, pending } = useFeedbackStatus()
  const { updateFeedbackStatus, emit } = useAppAction()

  const removeTimer = useCallback(() => {
    if (tid.current) {
      clearTimeout(tid.current)
      tid.current = null
    }
  }, [])

  const handleOnStatusChange = useCallback(
    (result: Store['feedbackStatus']) => {
      removeTimer()
      if (!result || typeof result.status !== 'string' || typeof result.message !== 'string') {
        updateFeedbackStatus({
          status: 'error',
          message: 'feedbackLoadFailed',
          height: 0,
          reason: result
        })
      } else {
        const { status, message, height = 300 } = result
        updateFeedbackStatus({
          status,
          message,
          height
        })
        if (status === 'SURVEY_UNSUBMITTED') {
          emit(AppEvent.need_scroll_to_bottom)
        }
      }
    },
    [removeTimer, updateFeedbackStatus, emit]
  )

  const startTimer = useCallback(() => {
    removeTimer()
    tid.current = setTimeout(() => {
      updateFeedbackStatus({ status: 'error', message: 'feedbackLoadFailed', height: 0, reason: 'timeout' })
    }, 15000)
  }, [removeTimer, updateFeedbackStatus])

  const createRpc = useCallback(() => {
    if (rpcRef.current) {
      return false
    }
    const iframe = iframeRef.current
    if (!iframe) {
      return false
    }
    rpcRef.current = new RPC({
      name: 'Parent',
      sender: iframe.contentWindow as Window,
      topic: 'feedback'
    })
    rpcRef.current?.register('feedbackStatusChange', handleOnStatusChange)
    return true
  }, [handleOnStatusChange])

  const removeRpc = useCallback(() => {
    rpcRef.current?.unregister('feedbackStatusChange')
    rpcRef.current?.destroy()
    rpcRef.current = null
  }, [])

  const handleOnError = () => {
    updateFeedbackStatus({ status: 'error', message: 'feedbackLoadFailed', height: 0, reason: 'iframeLoadedError' })
  }

  useEffect(() => {
    if (iframeRef.current) {
      startTimer()
      createRpc()
    }

    return () => {
      removeTimer()
      removeRpc()
    }
  }, [startTimer, removeTimer, createRpc, removeRpc])

  useEffect(() => {
    if (hidden || showMessage) {
      removeTimer()
      removeRpc()
    }
  }, [hidden, showMessage, removeTimer, removeRpc])

  if (hidden) {
    return <div aria-hidden='true' className={classes.hidden} />
  }

  if (showMessage) {
    return (
      <p className={classes.feedbackMessage} aria-label={t(message)}>
        {t(message)}
      </p>
    )
  }

  return (
    <div
      className={classes.feedbackContainer}
      style={{ height: pending || hidden ? 0 : height }}
      data-testid='feedback-container'
      aria-hidden={pending || hidden}
      aria-label='Feedback'
    >
      <iframe
        ref={iframeRef}
        src={feedbackLink}
        className={classes.iframe}
        onError={handleOnError}
        title='Feedback'
        data-testid='feedback-iframe'
      />
    </div>
  )
}
