import { MessageBubble } from '@td-org/chat-widget-shared'

function isSystemMessage(message: MessageBubble) {
  return message.type === 'system'
}

export function isSeries(message: MessageBubble, other: MessageBubble): boolean {
  if (!other) return false

  return (
    message.alignment === other.alignment &&
    message.formattedTime === other.formattedTime &&
    message.role === other.role &&
    message.rid === other.rid
  )
}

export function isFirstInSeries(message: MessageBubble, previous?: MessageBubble): boolean {
  if (previous === undefined) {
    return true
  }

  if (isSystemMessage(previous)) {
    return true
  }

  return !isSeries(message, previous)
}

export function isLastInSeries(message: MessageBubble, next?: MessageBubble): boolean {
  if (next === undefined) {
    return true
  }

  if (isSystemMessage(next)) {
    return true
  }

  return !isSeries(message, next)
}
