import { MessageAvatar, Role } from '@td-org/chat-widget-shared'

import { useConfig } from '@/stores/config.ts'

export const getMessageAvatarByConfig = (role: Role): MessageAvatar | undefined => {
  const { appearanceConfig, options } = useConfig.getState().config
  const { displayAgentsName, displayAutopilotsAvatar, autopilotsAvatar } = appearanceConfig.generalSettings
  const { botIcon } = options

  switch (role) {
    case 'agent':
      return {
        url: botIcon,
        type: displayAgentsName ? 'agent' : 'system'
      }
    case 'va':
      return {
        url: displayAutopilotsAvatar && autopilotsAvatar ? autopilotsAvatar : botIcon
      }
    case 'system':
      return { url: botIcon }
    default:
      return
  }
}

export const getMessageAvatar = (role: Role): MessageAvatar | undefined => {
  if (role === 'contact') return

  return getMessageAvatarByConfig(role)
}
