import { MouseEvent, RefObject } from 'react'

import IconButton from '@/components/IconButton'
import { MoreVert as MoreVertIcon } from '@/components/Icons'

import classes from '../ChatHeader.module.scss'

interface MoreVertButtonProps {
  forwardedRef?: RefObject<HTMLButtonElement>
  onClick?: (e: MouseEvent<HTMLButtonElement>) => void
}

const MoreVertButton = ({ forwardedRef, onClick }: MoreVertButtonProps) => {
  return (
    <IconButton forwardedRef={forwardedRef} onClick={onClick} aria-label='more operations'>
      <div className={classes.icon}>
        <MoreVertIcon />
      </div>
    </IconButton>
  )
}

export default MoreVertButton
