import { MouseEvent, ReactNode, RefObject } from 'react'

import { Arrow } from '../Icons'

import { useDropdownContext } from './context'

import classes from './Dropdown.module.scss'

export interface DropdownInputProps {
  placeholder?: string
  disabled?: boolean
  children?: ReactNode
  forwardedRef?: RefObject<HTMLButtonElement>
  onClick?: (e: MouseEvent<HTMLButtonElement>) => void
}

const DropdownInput = (props: DropdownInputProps) => {
  const { forwardedRef, placeholder = 'Please input...', disabled, onClick, children } = props

  const { open } = useDropdownContext()

  return (
    <button
      ref={forwardedRef}
      className={classes['dropdown-input']}
      data-focus={open}
      disabled={disabled}
      aria-disabled={disabled}
      onClick={onClick}
    >
      {children || <span className={classes['dropdown-input-placeholder']}>{placeholder}</span>}
      <Arrow className={classes['dropdown-input-arrow']} />
    </button>
  )
}

export default DropdownInput
