import { useMemo } from 'react'

import type { Option } from '@/components/Dropdown'

interface UseDropdownOptionsPayload {
  showEndChatButton: boolean
  disableEndChat: boolean
  disableDownloadTranscript: boolean
  onDownloadTranscript: () => void
  onEndChat: () => void
}

enum Options {
  DOWNLOAD_TRANSCRIPT = 'headerDownloadTranscriptButton',
  END_CHAT = 'headerEndChatButton'
}

const defaultOptions: Option[] = [
  {
    label: Options.DOWNLOAD_TRANSCRIPT,
    value: Options.DOWNLOAD_TRANSCRIPT,
    icon: 'download',
    disabled: false
  },
  {
    label: Options.END_CHAT,
    value: Options.END_CHAT,
    icon: 'terminate',
    disabled: false
  }
]

const useDropdownOptions = ({
  showEndChatButton,
  disableEndChat,
  disableDownloadTranscript,
  onDownloadTranscript,
  onEndChat
}: UseDropdownOptionsPayload) => {
  const options = useMemo(() => {
    return defaultOptions.reduce<Option[]>((arr, item) => {
      switch (item.value) {
        case Options.DOWNLOAD_TRANSCRIPT:
          arr.push({
            ...item,
            label: item.label,
            disabled: disableDownloadTranscript
          })
          break
        case Options.END_CHAT:
          showEndChatButton &&
            arr.push({
              ...item,
              label: item.label,
              disabled: disableEndChat
            })
          break
        default:
          break
      }

      return arr
    }, [])
  }, [showEndChatButton, disableEndChat, disableDownloadTranscript])

  const onOptionItemChange = ({ value }: Option) => {
    switch (value) {
      case Options.DOWNLOAD_TRANSCRIPT:
        onDownloadTranscript?.()
        break
      case Options.END_CHAT:
        onEndChat?.()
        break
      default:
        break
    }
  }

  return {
    options,
    onOptionItemChange
  }
}

export default useDropdownOptions
