const defaultLocales = [Intl.DateTimeFormat().resolvedOptions().locale, 'en-US']
const defaultTimeFormat = {
  hour: '2-digit',
  minute: '2-digit'
} as const

export function toLocaleTimeString(timestamp: number): string {
  return new Date(timestamp).toLocaleTimeString(defaultLocales, defaultTimeFormat)
}

function parseNumber(num: number) {
  return num < 10 ? `0${num}` : num
}

function buildFullYear(date: Date) {
  return new Date(date).getFullYear()
}

function buildFullMonth(date: Date) {
  return parseNumber(new Date(date).getMonth() + 1)
}

function buildFullDay(date: Date) {
  return parseNumber(new Date(date).getDate())
}

function buildFullHour(date: Date) {
  return parseNumber(new Date(date).getHours())
}

function buildMinutes(date: Date) {
  return new Date(date).getMinutes()
}

function buildSeconds(date: Date) {
  return new Date(date).getSeconds()
}

const formatTokens = /(\[[^[]*\])|([-_:/.,()\s]+)|(A|a|YYYY|YY?|MM?M?M?|Do|DD?|hh?|HH?|mm?|ss?|S{1,3}|z|ZZ?)/g
const formatTokenFunctions = {
  YYYY: buildFullYear,
  MM: buildFullMonth,
  DD: buildFullDay,
  HH: buildFullHour,
  mm: buildMinutes,
  ss: buildSeconds
}

export function formatTime(date: Date, format = 'YYYY-MM-DD'): string {
  const array = format.match(formatTokens)

  if (!array) return ''

  const newArray = array
    .map((item) => formatTokenFunctions[item as keyof typeof formatTokenFunctions] || item)
    .filter((item) => !!item)

  return newArray.reduce<string>((output, item) => {
    output += typeof item === 'function' ? item(date) : item

    return output
  }, '')
}

export function getTimezone(): string {
  return Intl.DateTimeFormat().resolvedOptions().timeZone
}
