import { Message } from '@td-org/chat-widget-shared'

import classes from './index.module.scss'

const Voiceover = ({ message }: { message: Message | undefined }) => {
  if (!message) {
    return null
  }
  return (
    <div className={classes.hidden} role='log' aria-atomic='true' aria-live='polite'>
      {message.items.map((item, index) => {
        if (item.type === 'text') {
          return <p key={index}>{item.text}</p>
        } else {
          return null
        }
      })}
    </div>
  )
}

export default Voiceover
