import React, { MouseEvent, ReactElement, ReactNode, useMemo, useRef, useState } from 'react'

import useClickOutside from '../../hooks/useClickOutside'

import { Align, DropdownProvider } from './context'
import DropdownContent, { Option } from './DropdownContent'
import DropdownInput from './DropdownInput'
import DropdownPopup from './DropdownPopup'
import DropdownWrapper from './DropdownWrapper'

export interface DropdownProps {
  option?: Option | null
  align?: Align.LEFT | Align.RIGHT
  placeholder?: string
  options?: Option[]
  customInput?: ReactElement
  children?: ReactNode | ((v: { onClick: (o: Option) => void }) => ReactNode)
  onChange?: (option: Option) => void
}

const Dropdown = (props: DropdownProps) => {
  const {
    option,
    options,
    align = Align.LEFT,
    placeholder = 'Please select...',
    customInput,
    children,
    onChange
  } = props
  const inputRef = useRef<HTMLButtonElement | null>(null)
  const contentRef = useRef<HTMLDivElement | null>(null)
  const [open, setOpen] = useState(false)

  const inputNode = inputRef.current
  const contentNode = contentRef.current

  const top = useMemo(() => inputNode?.clientHeight ?? 0, [inputNode])

  useClickOutside({
    isExecuted: open,
    nodes: [inputNode, contentNode],
    handler: () => setOpen(false)
  })

  const onClickInput = (e: MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation()
    e.preventDefault()

    setOpen((isOpen) => !isOpen)
  }

  const onClickOption = (opt: Option) => {
    setOpen(false)
    onChange?.(opt)
  }

  return (
    <DropdownProvider value={{ open, align, top }}>
      <DropdownWrapper>
        {customInput ? (
          React.cloneElement(customInput, {
            [typeof customInput.type === 'function' ? 'forwardedRef' : 'ref']: inputRef,
            onClick: onClickInput
          })
        ) : (
          <DropdownInput placeholder={placeholder} forwardedRef={inputRef} onClick={onClickInput}>
            {option?.label}
          </DropdownInput>
        )}
        <DropdownPopup visible={open} forwardedRef={contentRef}>
          {typeof children === 'function' ? (
            children({ onClick: onClickOption })
          ) : (
            <DropdownContent active={option?.value} options={options} onClickOption={onClickOption} />
          )}
        </DropdownPopup>
      </DropdownWrapper>
    </DropdownProvider>
  )
}

Dropdown.whyDidYouRender = true

export default Dropdown
