import { ReactElement, ReactNode, useEffect, useRef, useState } from 'react'

import { Spinner } from '@td-org/chat-widget-components'

import { Close } from '@/components/Icons'
import ModalMask from '@/components/ModalMask/index.ts'
import { Portal } from '@/components/PortalProvider/index.ts'
import clsx from '@/utils/clsx.ts'

import classes from './Dialog.module.scss'

interface Props {
  visible: boolean
  icon: ReactNode
  title?: string
  description?: string
  confirmText: string
  cancelText?: string
  onCancel: () => void
  onConfirm: () => void
  isLoading?: boolean
}

export const Dialog = (props: Props): ReactElement | null => {
  const { visible, icon, title, description, confirmText, cancelText, isLoading = false, onConfirm, onCancel } = props
  const confirmButtonRef = useRef<HTMLButtonElement>(null)
  const [show, setShow] = useState(false)

  useEffect(() => {
    if (visible) {
      confirmButtonRef.current?.focus()

      setShow(true)
    } else {
      const timer = setTimeout(() => setShow(false), 200)

      return () => clearTimeout(timer)
    }
  }, [visible])

  if (!show) {
    return null
  }

  return (
    <Portal>
      <ModalMask className={classes[`${visible ? 'fade-in' : 'fade-out'}`]} align='start'>
        <div className={clsx(classes.wrapper, classes[`${visible ? 'slide-in' : 'slide-out'}`])}>
          <div className={classes.dialog}>
            <button className={classes.close} aria-label={cancelText} onClick={onCancel}>
              <Close />
            </button>
            <div className={classes.header}>
              <div className={classes.icon}>{icon}</div>
            </div>
            <div className={classes.content}>
              <p className={classes.title}>{title}</p>
              <p className={classes.description}>{description}</p>
            </div>
            <div className={classes.footer}>
              <button
                ref={confirmButtonRef}
                className={classes.confirm}
                onClick={onConfirm}
                disabled={isLoading}
                aria-disabled={isLoading}
                aria-label={confirmText}
              >
                {isLoading ? <Spinner size='tiny' /> : null}
                <span>{confirmText}</span>
              </button>
            </div>
          </div>
        </div>
      </ModalMask>
    </Portal>
  )
}
