import type { CSSProperties, ReactNode } from 'react'

import clsx from '@/utils/clsx'

import classes from './ModalMask.module.scss'

type ModalMaskProps = {
  backgroundColor?: CSSProperties['backgroundColor']
  align?: 'center' | 'start'
  className?: string
  children: ReactNode
}

export function ModalMask(props: ModalMaskProps): JSX.Element {
  const { className, children, backgroundColor, align = 'center' } = props

  return (
    <div
      style={{ backgroundColor }}
      className={clsx(
        classes.mask,
        {
          [classes.start]: align === 'start'
        },
        className
      )}
    >
      {children}
    </div>
  )
}
