import { useState } from 'react'

import { useConnectionStatus } from '@/hooks/useConnectionStatus'
import { useEnableEndChat } from '@/stores/app/selectors.ts'

interface UseEndChatButtonPayload {
  endChat: () => Promise<boolean>
  endApp: () => void
}

const useEndChat = ({ endChat, endApp }: UseEndChatButtonPayload) => {
  const [confirmEndVisible, setConfirmEndVisible] = useState<boolean>(false)
  const [retryEndVisible, setRetryEndVisible] = useState<boolean>(false)
  const [isEndingChat, setIsEndingChat] = useState<boolean>(false)

  const enableEndChat = useEnableEndChat()
  const connected = useConnectionStatus()

  const disableEndChat = !enableEndChat || confirmEndVisible || retryEndVisible || !connected

  const onEndChat = async (): Promise<void> => {
    setIsEndingChat(true)

    const closeComplete = await endChat()

    setConfirmEndVisible(false)
    setIsEndingChat(false)

    closeComplete ? endApp?.() : setRetryEndVisible(true)
  }

  const onRetryEndChat = async (): Promise<void> => {
    setIsEndingChat(true)

    const closeComplete = await endChat()

    setIsEndingChat(false)

    if (closeComplete) {
      setRetryEndVisible(false)
      endApp?.()
    }
  }

  const showConfirmEndChat = (): void => {
    if (disableEndChat) return

    setConfirmEndVisible(true)
  }

  const onCancelConfirmEndChat = (): void => setConfirmEndVisible(false)

  const onCancelRetryEndChat = (): void => setRetryEndVisible(false)

  return {
    isEndingChat,
    showConfirmEndChat,
    confirmEndVisible,
    retryEndVisible,
    enableEndChat,
    disableEndChat,
    onEndChat,
    onRetryEndChat,
    onCancelRetryEndChat,
    onCancelConfirmEndChat
  }
}

export default useEndChat
