type Status = 'pending' | 'fulfilled' | 'rejected'

export class Loader<T> {
  private _status: Status = 'pending'
  private _suspender: Promise<void>
  private _result!: T
  private _error: unknown

  constructor(promise: Promise<T>) {
    this._suspender = promise
      .then((result) => {
        this._status = 'fulfilled'
        this._result = result
      })
      .catch((err) => {
        this._status = 'rejected'
        this._error = err
      })
  }

  get status() {
    return this._status
  }

  load(): T {
    if (this._status === 'pending') {
      throw this._suspender
    } else if (this._status === 'rejected') {
      throw this._error
    } else if (this._status === 'fulfilled') {
      return this._result
    } else {
      const n: never = this._status
      throw new Error(`Status "${n}" is not supported`)
    }
  }
}
