import { type MessageTypes } from '@td-org/chat-widget-shared'

import classes from './ChatBubbleBox.module.scss'

export function getContainerVariation(type: MessageTypes): string {
  let unSupportedType: string
  switch (type) {
    case 'text':
    case 'rich_text':
    case 'buttons':
    case 'quick_replies':
    case 'file':
    case 'error':
    case 'image':
    case 'video':
    case 'audio':
    case 'carousel':
    case 'typing':
      return classes.bubble
    case 'system':
      return classes.systemBubble
    default:
      unSupportedType = type
      console.warn(`Type "${unSupportedType}" is not supported`)
      return classes.bubble
  }
}

export function getBoxVariation(type: MessageTypes): string {
  let unSupportedType: string
  switch (type) {
    case 'text':
    case 'quick_replies':
    case 'file':
    case 'error':
    case 'typing':
    case 'system':
      return classes.normal
    case 'image':
    case 'video':
    case 'audio':
      return classes.transparent
    case 'rich_text':
    case 'buttons':
      return classes.rounded
    case 'carousel':
      return classes.relative
    default:
      unSupportedType = type
      console.warn(`Type "${unSupportedType}" is not supported`)
      return classes.normal
  }
}
