import { type FormEvent, forwardRef } from 'react'

import classes from './CustomButton.module.scss'

interface Props {
  trigger: string
  label: string
  submitHandle: () => void
}

const CAN_SUBMIT = 'start_the_chat'

export const CustomButton = forwardRef<HTMLButtonElement, Props>((props, ref) => {
  const { trigger, label, submitHandle } = props

  const handleClick = (e: FormEvent<HTMLButtonElement>): void => {
    e.preventDefault()
    e.stopPropagation()

    if (trigger === CAN_SUBMIT) {
      submitHandle()
    }
  }

  return (
    <div className={classes.customButton}>
      <div className={classes.wrapper}>
        <button type='button' className={classes.submitButton} ref={ref} title={label} onClick={handleClick}>
          {label}
        </button>
      </div>
    </div>
  )
})
