import { type ErrorInfo } from 'react'

import logger from './logger'

const errorBoundaryLogger = logger.getSubLogger({ name: 'ErrorBoundary' })
const globalErrorBoundaryLogger = logger.getSubLogger({ name: 'GlobalErrorHandler' })
const mediaSourceErrorBoundaryLogger = logger.getSubLogger({ name: 'MediaSourceErrorBoundary' })

function _logForErrorBoundary(error: Error, errorInfo: ErrorInfo, log: ReturnType<typeof logger.getSubLogger>): void {
  log.error(error)
  if (errorInfo.componentStack) {
    log.error(`Component stack: ${errorInfo.componentStack}`)
  }
}

export function logForErrorBoundary(error: Error, errorInfo: ErrorInfo) {
  _logForErrorBoundary(error, errorInfo, errorBoundaryLogger)
}

export function logForGlobalErrorBoundary(error: Error, errorInfo: ErrorInfo) {
  _logForErrorBoundary(error, errorInfo, globalErrorBoundaryLogger)
}

export function logForMessageMediaError(error: Error) {
  _logForErrorBoundary(error, {}, mediaSourceErrorBoundaryLogger)
}
