import omit from 'lodash.omit'
import pick from 'lodash.pick'
import pickBy from 'lodash.pickby'

import { type Appearance, OPTIONS_KEYS, type Theme } from '@td-org/chat-widget-shared'

import { type Options } from '@/models/Options'

export function extractCssVars(styles?: Partial<Theme>): Partial<Theme> {
  if (!styles) return {}
  return omit(styles, OPTIONS_KEYS)
}

export function extractOptions(styles?: Partial<Theme>): Partial<Options> {
  if (!styles) return {}
  return pick(styles, OPTIONS_KEYS)
}

export function reassignCssVars(appearance?: Appearance, styles?: Partial<Theme>) {
  let cssVars = {}
  if (appearance?.theme) {
    const {
      brandColor,
      headerTextColor,
      headerBgColor,
      welcomeBgColor,
      welcomeTextColor,
      initialFormBgColor,
      agentBubbleColor,
      agentTextColor,
      customerBubbleColor,
      customerTextColor,
      buttonColors: {
        primary: {
          textColor: primary_textColor,
          backgroundColor: primary_backgroundColor,
          hoverBackgroundColor: primary_hoverBackgroundColor
        },
        secondary: {
          textColor: secondary_textColor,
          backgroundColor: secondary_backgroundColor,
          hoverBackgroundColor: secondary_hoverBackgroundColor
        },
        outLine: {
          textColor: outLine_textColor,
          borderColor: outLine_borderColor,
          backgroundColor: outLine_backgroundColor,
          hoverBackgroundColor: outLine_hoverBackgroundColor
        },
        transparent: {
          textColor: transparent_textColor = outLine_textColor,
          hoverBackgroundColor: transparent_hoverBackgroundColor = outLine_hoverBackgroundColor
        } = {}
      }
    } = appearance.theme

    cssVars = {
      chatThemeColor: brandColor,
      chatHoverThemeColor: brandColor,

      chatHeaderBackgroundColor: headerBgColor,
      chatTitleTextColor: headerTextColor,
      chatSubtitleTextColor: headerTextColor,
      chatCloseButtonColor: headerTextColor,
      chatTitleIconColor: primary_textColor,

      welcomeMessageBackgroundColor: welcomeBgColor,
      welcomeMessageTitleColor: welcomeTextColor,
      welcomeMessageContentColor: welcomeTextColor,
      initialFormBackgroundColor: initialFormBgColor,

      botTextColor: agentTextColor,
      botBackgroundColor: agentBubbleColor,
      botAnchorTextColor: agentTextColor,
      botAnchorTextColorVisited: agentTextColor,
      userTextColor: customerTextColor,
      userBackgroundColor: customerBubbleColor,
      userAnchorTextColor: customerTextColor,
      userAnchorTextColorVisited: customerTextColor,

      customButtonBackgroundColor: primary_backgroundColor,
      customButtonColor: primary_textColor,
      customButtonBorderColor: primary_backgroundColor,
      customButtonHoverBackgroundColor: primary_hoverBackgroundColor,
      customButtonHoverColor: primary_textColor,
      customButtonHoverBorderColor: primary_hoverBackgroundColor,

      startChatButtonTextColor: primary_textColor,
      startChatButtonBorderColor: primary_backgroundColor,
      startChatButtonBackgroundColor: primary_backgroundColor,
      startChatButtonHoverTextColor: primary_textColor,
      startChatButtonHoverBorderColor: primary_hoverBackgroundColor,
      startChatButtonHoverBackgroundColor: primary_hoverBackgroundColor,

      startChatTransparentButtonTextColor: transparent_textColor,
      startChatTransparentButtonHoverBackgroundColor: transparent_hoverBackgroundColor,

      replyTextColor: outLine_textColor,
      replyBorderColor: outLine_borderColor,
      replyBackgroundColor: outLine_backgroundColor,
      replyHoverTextColor: outLine_textColor,
      replyHoverBackgroundColor: outLine_hoverBackgroundColor,

      buttonTextColor: secondary_textColor,
      buttonHoverTextColor: secondary_textColor,
      buttonBackgroundColor: secondary_backgroundColor,
      buttonHoverBackgroundColor: secondary_hoverBackgroundColor,

      chatConfirmButtonFontColor: primary_textColor,
      chatConfirmButtonBackgroundColor: primary_backgroundColor,
      chatConfirmButtonHoverBackgroundColor: primary_hoverBackgroundColor
    }
  }
  if (appearance?.logoUrl) {
    cssVars = {
      ...cssVars,
      chatTitleIcon: `url(${appearance.logoUrl})`
    }
  }

  if (styles) {
    cssVars = {
      ...cssVars,
      ...styles
    }
    if (styles.chatTitleIcon) {
      cssVars = {
        ...cssVars,
        chatTitleIcon: `url(${styles.chatTitleIcon})`
      }
    }
  }

  return cssVars
}

export function reassignOptions(appearance: Appearance | undefined) {
  if (!appearance) return {}

  return pickBy(
    {
      chatTitleIcon: appearance.logoUrl,
      chatTitle: appearance.title,
      chatSubtitle: appearance.description
    },
    (value) => {
      return !!value
    }
  )
}
