import type { ReactElement } from 'react'

import IconButton from '@/components/IconButton'
import { Send as SendIcon } from '../Icons'

interface Props {
  disabled: boolean
  onClick: () => void
}

const SendButton = (props: Props): ReactElement => {
  const { disabled, onClick } = props

  const onSaveClick = () => {
    if (disabled) return
    onClick()
  }

  return (
    <IconButton onClick={onSaveClick} disabled={disabled} aria-label='send message'>
      <SendIcon />
    </IconButton>
  )
}

export default SendButton
