import { type ErrorInfo, type ReactNode, Suspense } from 'react'

import type { MessageTypes } from '@td-org/chat-widget-shared'

import ErrorBoundary from '../ErrorBoundary'

import MessageBox from './MessageBox.tsx'
import MessageError from './MessageError.tsx'
import MessageLoading from './MessageLoading.tsx'

interface MessageProps {
  type: MessageTypes
  children?: ReactNode
  errorMessage?: string
  onError?: (error: Error, errorInfo: ErrorInfo) => void
}

const Message = ({ type, errorMessage, children, onError }: MessageProps) => {
  return (
    <ErrorBoundary onError={onError} fallback={<MessageError type={type} errorMessage={errorMessage} />}>
      <Suspense fallback={<MessageLoading type={type} />}>
        <MessageBox type={type}>{children}</MessageBox>
      </Suspense>
    </ErrorBoundary>
  )
}

export default Message
