import i18next from 'i18next'

import { MAX_ALLOWED, type QuickReplyItem, TextItem } from '@td-org/chat-widget-shared'

import { convertTextPayload } from '@/utils/receiver/converter/text.ts'
import type { TwilioQuickRepliesPayload } from '@/utils/twilio'
import { convertButtonElements, getLastMessageAndPrevious } from '../helpers.ts'

export function convertQuickReplyPayload(
  responses: string[],
  payload: TwilioQuickRepliesPayload
): Array<QuickReplyItem | TextItem> {
  const { quick_actions, text_response } = payload

  const textMessages = text_response ? [text_response] : responses

  const { lastMessage, previousMessages } = getLastMessageAndPrevious(textMessages, i18next.t('yourOptions'))

  return [
    ...convertTextPayload(previousMessages),
    {
      type: 'quick_replies',
      text: lastMessage,
      replies: convertButtonElements(quick_actions.elements.slice(0, MAX_ALLOWED.BUTTONS))
    }
  ]
}
