import classes from './ErrorScreen.module.scss'

type ErrorScreenProps = {
  onRetry: () => void
}

export function ErrorScreen(props: ErrorScreenProps) {
  const { onRetry } = props
  return (
    <div className={classes.error}>
      <p className={classes.message}>There was a problem while trying to load the widget</p>
      <button className={classes.retry} onClick={onRetry}>
        Try again
      </button>
    </div>
  )
}
