type Options = {
  mode?: Storage
  onError?: (error: unknown) => void
}

export function safeSetStorage(key: string, value: string, options: Options = {}) {
  const { mode = window.localStorage, onError } = options
  try {
    mode.setItem(key, value)
    return true
  } catch (e) {
    onError?.(e)
    return false
  }
}

export function safeGetStorage(key: string, options: Options = {}) {
  const { mode = window.localStorage, onError } = options
  try {
    return mode.getItem(key)
  } catch (e) {
    onError?.(e)
    return null
  }
}

export function safeRemoveStorage(key: string, options: Options = {}) {
  const { mode = window.localStorage, onError } = options
  try {
    mode.removeItem(key)
    return true
  } catch (e) {
    onError?.(e)
    return false
  }
}
