import type {
  Conversation as TwilioConversation,
  ConversationUpdateReason as TwilioConversationUpdateReason,
  JSONValue as TwilioJSONValue,
  Message as TwilioMessage,
  Participant as TwilioParticipant
} from '@twilio/conversations'
import omit from 'lodash.omit'

import {
  BoardCastEvent,
  getErrorMessage,
  Page,
  ParticipantAttributes,
  safeJsonToString,
  sleep
} from '@td-org/chat-widget-shared'

import { type Store as AppStore, useApp } from '@/stores/app/store'
import { generalSettingsSelector, useConfig } from '@/stores/config'
import { useMessageStore } from '@/stores/message'
import { useBoardCast } from '@/stores/useBoardCast.ts'
import { buildTypingMessage, isTypingMessage } from '@/utils/buildStaticMessages'
import baseLogger from '@/utils/logger'
import type { UserInput } from '../../types'
import { ProviderConnectionImp, ProviderInitScene } from '../../types'
import {
  convertTwilioMessage,
  fetchContactPersonTwilioParticipant,
  fetchTwilioAllMessages,
  fetchTwilioConversation,
  getContactPersonParticipantAttributes,
  getTwilioAttributes,
  getTwilioConversationAttributes,
  isEndedTwilioConversation,
  selfCloseTwilioConversation,
  triggerFlowForTwilio
} from '../provider-utils/twilio'

import type Client from './client.ts'

const logger = baseLogger.getSubLogger({ name: 'TwilioConversation' })

class Conversation {
  private _providerConversation: TwilioConversation | null = null
  private _contactPersonParticipant: TwilioParticipant | null = null
  private _bindEventsHandlers: (() => void) | null = null
  private _isEnd: boolean = false

  constructor(private readonly parent: ProviderConnectionImp<Client, Conversation>) {}

  private handleOnUpdated(data: { updateReasons: TwilioConversationUpdateReason[]; conversation: TwilioConversation }) {
    const { conversation, updateReasons } = data
    const { interactionId, feedbackLink, shouldLoadFeedback } = getTwilioConversationAttributes(conversation)
    const preState: Partial<AppStore> = {}

    if (!useApp.getState().interactionId && interactionId) {
      preState.interactionId = interactionId
    }

    if (!useApp.getState().shouldLoadFeedback && shouldLoadFeedback) {
      preState.shouldLoadFeedback = shouldLoadFeedback
    }

    if (!useApp.getState().feedbackLink && feedbackLink) {
      preState.feedbackLink = feedbackLink
    }

    if (Object.keys(preState).length > 0) {
      useApp.setState(preState)
    }

    if (shouldLoadFeedback) {
      if (useApp.getState().isNot(Page.End)) {
        useApp.getState().go(Page.End)
      }
    }

    logger.info(
      `Received updated sid=${conversation.sid} updateReasons=${safeJsonToString(
        updateReasons
      )} attrs=${safeJsonToString(conversation?.attributes)} updatedAppState=${safeJsonToString(preState)}`
    )
  }

  private handleOnMessageAdded(message: TwilioMessage) {
    logger.info(
      `Received message added. sid=${this._providerConversation?.sid} m_sid=${message.sid} p_sid=${
        message.participantSid
      } index=${message.index} type=${message.type} author=${message.author} attr=${safeJsonToString(
        getTwilioAttributes(message?.attributes)
          ? omit(getTwilioAttributes(message?.attributes), ['raw_message', 'rawMessage', 'attachments'])
          : ''
      )} create_at=${message.dateCreated?.toISOString()} update_at=${message.dateUpdated?.toISOString()}`
    )

    const localMessage = convertTwilioMessage(message)

    if (localMessage) {
      useMessageStore.getState().add(localMessage)

      const isReceived = localMessage.alignment

      if (isReceived) {
        useApp.getState().hiddenScreenLoading()
        if (this.parent?.receivedFirstMessageSpan) {
          this.parent.receivedFirstMessageSpan!.setAttributes({
            roomId: this._providerConversation?.sid as string,
            m_sid: message?.sid,
            p_sid: message?.participantSid || '',
            index: message?.index,
            type: message?.type,
            author: message?.author || '',
            attributes: safeJsonToString(message?.attributes),
            create_at: message.dateCreated?.toISOString(),
            update_at: message.dateUpdated?.toISOString()
          })
          this.parent.receivedFirstMessageSpan!.setStatus({ code: 1 })
          this.parent.receivedFirstMessageSpan!.end()
          this.parent.receivedFirstMessageSpan = null
        }
      }

      if (isReceived && !isTypingMessage(localMessage.tid)) {
        useMessageStore.getState().hideTypingMessage()
      }
    }
  }

  private handleOnTypingStarted(participant: TwilioParticipant) {
    const hasTypingIndicator = generalSettingsSelector(useConfig.getState()).showTypingIndicatorButton

    if (!hasTypingIndicator || !this._contactPersonParticipant) {
      return
    }

    if (participant.sid !== this._contactPersonParticipant.sid) {
      useMessageStore.getState().add(
        buildTypingMessage({
          role: 'agent',
          agentName: (participant?.attributes as ParticipantAttributes)?.participant_display_name
        })
      )
    }
  }

  private handleOnTypingEnded(participant: TwilioParticipant) {
    if (!this._contactPersonParticipant) {
      return
    }

    if (participant.sid !== this._contactPersonParticipant.sid) {
      useMessageStore.getState().hideTypingMessage()
    }
  }

  private handleOnRemoved(conversation: TwilioConversation) {
    logger.info(`Received conversation removed sid=${conversation.sid}`)

    if (useApp.getState().isNot(Page.End)) {
      useApp.getState().go(Page.End)
    }
  }

  private listenerEvents() {
    if (this._providerConversation && !this._bindEventsHandlers) {
      const boundHandleOnUpdated = this.handleOnUpdated.bind(this)
      const boundHandleOnMessageAdded = this.handleOnMessageAdded.bind(this)
      const boundHandleOnTypingStarted = this.handleOnTypingStarted.bind(this)
      const boundHandleOnTypingEnded = this.handleOnTypingEnded.bind(this)
      const boundHandleOnRemoved = this.handleOnRemoved.bind(this)

      this._bindEventsHandlers = () => {
        this._providerConversation?.off('updated', boundHandleOnUpdated)
        this._providerConversation?.off('messageAdded', boundHandleOnMessageAdded)
        this._providerConversation?.off('typingStarted', boundHandleOnTypingStarted)
        this._providerConversation?.off('typingEnded', boundHandleOnTypingEnded)
        this._providerConversation?.off('removed', boundHandleOnRemoved)
      }

      this._providerConversation.on('updated', boundHandleOnUpdated)
      this._providerConversation.on('messageAdded', boundHandleOnMessageAdded)
      this._providerConversation.on('typingStarted', boundHandleOnTypingStarted)
      this._providerConversation.on('typingEnded', boundHandleOnTypingEnded)
      this._providerConversation.on('removed', boundHandleOnRemoved)
    }
  }

  public async bootstrap(scene: ProviderInitScene, parentSpan: ReturnType<typeof logger.startSpan>) {
    const providerClient = this.parent.client.current
    const roomId = useApp.getState().credentials.roomId
    const triggerFlow = ['pageLoaded', 'sendMessage', 'submitInitialScreen'].indexOf(scene) > -1

    if (!providerClient || !roomId) {
      throw new Error('No found twilio client or conversation sid')
    }

    const span = logger.startChildSpan('Bootstrap', parentSpan)

    try {
      const fetchTwilioConversationAt = Date.now()
      this._providerConversation = await fetchTwilioConversation(providerClient, roomId)
      this.listenerEvents()
      span?.setAttributes({ fetchTwilioConversationDuration: Date.now() - fetchTwilioConversationAt })

      const fetchContactPersonTwilioParticipantAt = Date.now()
      this._contactPersonParticipant = await fetchContactPersonTwilioParticipant(this._providerConversation)
      span?.setAttributes({
        fetchContactPersonTwilioParticipantDuration: Date.now() - fetchContactPersonTwilioParticipantAt
      })

      const { interactionId, feedbackLink, shouldLoadFeedback } = getTwilioConversationAttributes(
        this._providerConversation
      )
      const { selfClosed, bootstrapped } = getContactPersonParticipantAttributes(this._contactPersonParticipant)

      useApp.setState({ interactionId, feedbackLink, shouldLoadFeedback })

      this._isEnd = isEndedTwilioConversation({ instance: this._providerConversation, selfClosed, shouldLoadFeedback })

      if (!this._isEnd && !bootstrapped && triggerFlow) {
        const triggerFlowForTwilioAt = Date.now()
        await triggerFlowForTwilio(this._contactPersonParticipant)
        span?.setAttributes({ updateContactPersonParticipantDuration: Date.now() - triggerFlowForTwilioAt })
        logger.debug('Success trigger flow for update contact person attr')
      }

      const willGoPage = this._isEnd ? Page.End : Page.Conversation

      const fetchTwilioAllMessagesAt = Date.now()
      await fetchTwilioAllMessages(this._providerConversation)
      span?.setAttributes({ fetchTwilioAllMessagesDuration: Date.now() - fetchTwilioAllMessagesAt })

      if (useApp.getState().isNot(willGoPage)) {
        useApp.getState().go(willGoPage)
      }

      span?.setStatus({ code: 1 })
      span?.end()

      return true
    } catch (e) {
      span?.recordException(e as Error)
      span?.setStatus({ code: 2, message: getErrorMessage(e) })
      span?.end()
      throw e
    }
  }

  public typing() {
    if (!this._providerConversation) {
      return Promise.resolve()
    }
    return this._providerConversation.typing()
  }

  public async end() {
    const span = logger.startSpan('End')
    if (!this._providerConversation) {
      const message = 'Failed to end conversation no found conversation'
      span?.recordException(new Error(message))
      span?.setStatus({ code: 2, message })
      span?.end()
      logger.debug(message)
      return false
    }
    try {
      await selfCloseTwilioConversation(this._providerConversation)
      this._isEnd = true
      useBoardCast.getState().emit(BoardCastEvent.conversationEnded)
      useApp.getState().enableLoadFeedback()
      span?.setStatus({ code: 1 })
      span?.end()
      if (useConfig.getState().config?.feedbackFlowId) {
        // Wait for the feedback page to be displayed
        await sleep(1500)
      }
      return true
    } catch (e) {
      const message = `Failed to end conversation sid=${this._providerConversation.sid} error=${getErrorMessage(e)}`
      span?.recordException(e as Error)
      span?.setStatus({ code: 2, message })
      span?.end()
      logger.debug(message)
      return false
    }
  }

  public async send(userInput: UserInput, tid: string) {
    if (!this._providerConversation) {
      return Promise.reject(new Error('No found conversation'))
    }
    const { payload, data } = userInput
    const messageOptions = payload ? { tid, payload, payload_type: 'payload' } : { tid }
    await this._providerConversation.sendMessage(data, messageOptions as TwilioJSONValue)
  }

  public destroy() {
    this._bindEventsHandlers?.()
    this._bindEventsHandlers = null
    this._providerConversation?.removeAllListeners()
    this._providerConversation = null
    this._contactPersonParticipant = null
    this._isEnd = false
  }

  get current() {
    return this._providerConversation
  }

  get isEnd() {
    return this._isEnd
  }
}

export default Conversation
