import { ReactElement, useEffect } from 'react'
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react'

import { Message, Page } from '@td-org/chat-widget-shared'

import { useScrollForChatContent } from '@/hooks/useScroll.ts'
import BottomFixedArea from '@/layout/ChatContent/BottomFixedArea/BottomFixedArea'
import MessageList from '@/layout/ChatContent/MessageList/MessageList.tsx'
import { type PageType } from '@/stores/app/store.ts'
import { useMessagesAction } from '@/stores/message'

import ChatEndedMessage from './ChatEndedMessage'
import Feedback from './Feedback'
import Voiceover from './Voiceover'

import classes from './ChatContent.module.scss'

interface Props {
  page: PageType
  messages: Message[]
  feedbackLink: string
  shouldLoadFeedback: boolean
}

const ChatContent = (props: Props): ReactElement => {
  const { page, messages, feedbackLink = '', shouldLoadFeedback } = props
  const { hideTypingMessage } = useMessagesAction()
  const { osRef, headElRef, footerElRef, onScroll } = useScrollForChatContent()

  const isChatEnded = page === Page.End || page === Page.Error
  const showFeedback = shouldLoadFeedback && feedbackLink

  useEffect(() => {
    if (isChatEnded) {
      hideTypingMessage()
    }
  }, [isChatEnded, hideTypingMessage])

  return (
    <div className={classes.wrapper}>
      <OverlayScrollbarsComponent
        className={classes.chatContent}
        events={{ scroll: onScroll }}
        options={{
          scrollbars: { autoHide: 'scroll' },
          overflow: { x: 'hidden' }
        }}
        ref={osRef}
      >
        <div ref={headElRef} style={{ width: '100%', height: 1 }} />
        <MessageList messages={messages} />
        {isChatEnded && <ChatEndedMessage />}
        {showFeedback && <Feedback feedbackLink={feedbackLink} />}
        <div ref={footerElRef} style={{ width: '100%', height: 1 }} />
      </OverlayScrollbarsComponent>
      <BottomFixedArea page={page} />
      <Voiceover message={messages[messages.length - 1] || undefined} />
    </div>
  )
}

export default ChatContent
