import { type FC, type ReactElement, useMemo } from 'react'
import { useEffect } from 'react'
import { useTranslation } from 'react-i18next'

import { DEFAULT_OPTIONS, Page } from '@td-org/chat-widget-shared'

import Dropdown, { Align } from '@/components/Dropdown'
import Logo from '@/components/Logo/Logo'
import {
  useAppAction,
  useChatContentScroll,
  useEnableRTLLayoutDirection,
  useInitialScreenScroll
} from '@/stores/app/selectors.ts'
import { useMessagesAction, useTypingMessage } from '@/stores/message'
import { useRpc } from '@/stores/rpc'
import clsx from '@/utils/clsx.ts'

import ConfirmEndChat from './components/ConfirmEndChat'
import EndChatButton from './components/EndChatButton'
import MinimizeButton from './components/MinimizeButton'
import MoreVertButton from './components/MoreVertButton'
import RetryEndChat from './components/RetryEndChat'
import useDownloadTranscript from './hooks/useDownloadTranscript'
import useDropdownOptions from './hooks/useDropdownOptions'
import useEndChat from './hooks/useEndChat'

import classes from './ChatHeader.module.scss'

interface Props {
  chatTitle: string
  chatSubtitle: string
  showCloseConversationButton: boolean
  showDownloadTranscriptButton: boolean
}

const ChatHeader: FC<Props> = (props): ReactElement => {
  const { chatSubtitle, chatTitle, showCloseConversationButton, showDownloadTranscriptButton } = props
  const [t] = useTranslation()
  const { end } = useMessagesAction()
  const { go, is, isNot, focusUserInputArea } = useAppAction()
  const enableRTL = useEnableRTLLayoutDirection()
  const { headerVisible } = useInitialScreenScroll()
  const { footerVisible: chatContentFooterVisible } = useChatContentScroll()

  const {
    isEndingChat,
    confirmEndVisible,
    retryEndVisible,
    disableEndChat,
    onEndChat,
    onRetryEndChat,
    onCancelRetryEndChat,
    onCancelConfirmEndChat,
    showConfirmEndChat
  } = useEndChat({
    endChat: end,
    endApp: () => go('end')
  })
  const typeTypingMessage = useTypingMessage()

  const { disableDownloadTranscript, downloadTranscript } = useDownloadTranscript()

  const { options, onOptionItemChange } = useDropdownOptions({
    showEndChatButton: showCloseConversationButton,
    disableDownloadTranscript: disableDownloadTranscript,
    disableEndChat: disableEndChat,
    onDownloadTranscript: downloadTranscript,
    onEndChat: showConfirmEndChat
  })

  const onMinimize = (): void => {
    void useRpc.getState().minimize()
  }

  const formatSubtitle = useMemo(() => {
    if (!chatContentFooterVisible && is(Page.Conversation) && typeTypingMessage) {
      const role = typeTypingMessage?.role
      const name = typeTypingMessage?.participantName
      if (role && role === 'va') {
        return t('isThinking', { name: t(name || '') })
      }

      if (!name) return t('isTypingWithoutName')

      return t('isTyping', { name: t(name || '') })
    }

    return chatSubtitle || t('poweredByTalkdesk')
  }, [chatContentFooterVisible, chatSubtitle, is, t, typeTypingMessage])

  useEffect(() => {
    if (!confirmEndVisible) {
      focusUserInputArea && focusUserInputArea()
    }
  }, [confirmEndVisible, focusUserInputArea])

  return (
    <>
      <div
        className={clsx(classes.header, {
          [classes.withBoxShadow]: isNot(Page.InitialScreen),
          [classes.withSeparator]: is(Page.InitialScreen) && !headerVisible
        })}
      >
        <div className={classes.left}>
          <Logo />
          <div className={classes.desc}>
            <p className={classes.title} title={chatTitle || DEFAULT_OPTIONS.chatTitle}>
              {chatTitle || DEFAULT_OPTIONS.chatTitle}
            </p>
            <p className={classes.subtitle} title={formatSubtitle}>
              {formatSubtitle}
            </p>
          </div>
        </div>
        <div>
          <MinimizeButton onClick={onMinimize} />
          {showDownloadTranscriptButton ? (
            <Dropdown
              align={enableRTL ? Align.LEFT : Align.RIGHT}
              options={options}
              onChange={onOptionItemChange}
              customInput={<MoreVertButton />}
            />
          ) : showCloseConversationButton ? (
            <EndChatButton onClick={showConfirmEndChat} disabled={disableEndChat} />
          ) : null}
        </div>
      </div>

      <ConfirmEndChat
        visible={confirmEndVisible}
        onCancel={onCancelConfirmEndChat}
        onConfirm={onEndChat}
        isLoading={isEndingChat}
      />

      <RetryEndChat
        visible={retryEndVisible}
        onCancel={onCancelRetryEndChat}
        onConfirm={onRetryEndChat}
        isLoading={isEndingChat}
      />
    </>
  )
}

export default ChatHeader
