export function getErrorMessage<T = unknown>(error: T) {
  return typeof error === 'string' ? error : (error as Error)?.message
}

export function toError(target: unknown, unknownErrorMessage = 'unknown error') {
  if (target instanceof Error) {
    return target
  }
  if (typeof target === 'string') {
    return new Error(target)
  }
  return new Error(unknownErrorMessage)
}
