import { Message as TwilioMessage } from '@twilio/conversations'

import { MessageAttributes } from '@td-org/chat-widget-shared'

import { useConfig } from '@/stores/config.ts'

export const hideAssignedMessage = (message: TwilioMessage): boolean => {
  const attributes = message.attributes as MessageAttributes

  if (!attributes?.action?.metadata) return true

  const { cur_identified_id, cur_participant_type, cur_display_name, pre_identified_id, pre_participant_type } =
    attributes.action.metadata
  const { appearanceConfig } = useConfig.getState().config
  const { displayAgentsName, agentsNameDisplayRule } = appearanceConfig.generalSettings

  if (!displayAgentsName || !cur_display_name) return true

  const assignToVa = !pre_participant_type && cur_participant_type === 'VIRTUAL_AGENT'
  const transferBetweenSelfAgent = cur_identified_id === pre_identified_id
  const transferBetweenAgentsWhenSameName =
    agentsNameDisplayRule === 'same name' &&
    pre_participant_type === 'LIVE_AGENT' &&
    cur_participant_type === 'LIVE_AGENT'

  return assignToVa || transferBetweenSelfAgent || transferBetweenAgentsWhenSameName
}
