import { type ComponentType } from 'react'

import { Loader } from '@td-org/chat-widget-shared'

import { getLoader, setLoader } from './LoaderCache'

type LoaderFn<P> = (props: P) => Promise<Partial<P>>

type Identity = { identity: string }

export function Loadable<P extends Identity>(Component: ComponentType<P>, loaderFn: LoaderFn<P>) {
  return (props: P) => {
    const key = props.identity
    if (!getLoader<Partial<P>>(key)) {
      const loader = new Loader(loaderFn(props))
      setLoader(key, loader)
    }

    const newProps = getLoader<Partial<P>>(key)!.load()

    const finalProps = {
      ...props,
      ...newProps
    }

    return <Component {...finalProps} />
  }
}
