import { type ILogObj, IMeta } from 'tslog'

import { type AllConfig, PROXY_URI, safeJsonToString } from '@td-org/chat-widget-shared'

import { useApp } from '@/stores/app/store.ts'
import i18nConfig from '@/utils/i18nConfig.ts'

import { createDynatraceProvider, Provider, ProviderLoggerType } from './dynatrace-provider.ts'

type ReporterPayload = {
  logLevelId: number
  logLevelName: string
  logDate: string
  log: string
}

const shouldReporter = !import.meta.env.DEV

export function Reporter(provider: 'dynatrace') {
  let dynatraceProvider: Provider
  let isInjectorLocalhost = false

  function getRunTimeContext() {
    return {
      documentVisibilityState: document.visibilityState,
      identity: useApp.getState().identity,
      roomId: useApp.getState().credentials.roomId,
      providerRegion: useApp.getState().credentials.region,
      maximize: useApp.getState().isFrameOpened ? 'on' : 'off',
      lang: i18nConfig.current,
      browserLang: window.navigator.language
    }
  }

  function formatTsLog(log: ILogObj): ReporterPayload {
    const { _meta, ...payload } = log
    const meta = _meta as IMeta
    const logName = meta?.name || 'unknown logger name'
    const logDate = meta.date.toISOString()
    const logLevelName = meta.logLevelName
    const logs: string[] = [logName]
    if (payload) {
      try {
        Object.values(payload).forEach((value) => {
          if (value instanceof Function || value instanceof RegExp) return
          if (typeof value === 'object') {
            if ((value as { nativeError?: string })?.nativeError) {
              const error = value as Error
              logs.push(`${error?.name || 'Error'}: ${error?.message || ''}`)
            } else {
              logs.push(safeJsonToString(value))
            }
          } else {
            logs.push(String(value))
          }
        })
      } catch (e: unknown) {
        const error = e as Error
        logs.push(
          'Error formatTsLog payload',
          safeJsonToString(payload),
          `${error?.name || 'Error'}: ${error?.message || ''}`
        )
      }
    }
    return {
      logLevelId: meta.logLevelId,
      logLevelName,
      logDate,
      log: logs.join(' | ')
    }
  }

  function initDynatrace(attributes: AllConfig, proxyUrl: string) {
    const context = {
      talkdeskWidgetHref: window.location.href,
      talkdeskAccountId: attributes?.touchpoint?.account_id,
      talkdeskTouchpointId: attributes?.appProps?.touchpointId,
      talkdeskRegion: attributes?.appProps?.region,
      injectorWindowHref: attributes?.injectorWindowHref,
      environment: import.meta.env.VITE_STAGE,
      userAgent: window.navigator.userAgent
    }

    isInjectorLocalhost = /localhost/gi.test(context.injectorWindowHref || '')
    dynatraceProvider = createDynatraceProvider({ context, proxyUrl })
  }

  function runDynatrace({ log, logLevelName, logDate }: ReporterPayload) {
    if (dynatraceProvider) {
      const loggerType = logLevelName.toLocaleLowerCase() as ProviderLoggerType

      if (shouldReporter && !isInjectorLocalhost) {
        dynatraceProvider[loggerType]?.(log, { logDate, ...getRunTimeContext() })
      }
    }

    return log
  }

  const startSpan = (name: string) => {
    return shouldReporter && !isInjectorLocalhost ? dynatraceProvider.startSpan?.(name, getRunTimeContext()) : null
  }

  return {
    init: (attributes: AllConfig) => {
      const proxyUrl = attributes?.appProps?.proxyHost
        ? `//${attributes?.appProps?.proxyHost}${PROXY_URI.tdReporter}`
        : ''
      switch (provider) {
        case 'dynatrace':
          initDynatrace(attributes, proxyUrl)
          break
        default:
          break
      }
    },
    run: (target: ILogObj, minLevel: number) => {
      const meta = target._meta as IMeta
      if (meta?.logLevelId >= minLevel) {
        const payload = formatTsLog(target)
        switch (provider) {
          case 'dynatrace':
            return runDynatrace(payload)
          default:
            return null
        }
      }
    },
    startSpan,
    startChildSpan: (name: string, parent: ReturnType<typeof startSpan>) => {
      return shouldReporter && !isInjectorLocalhost
        ? dynatraceProvider.startChildSpan?.(name, parent, getRunTimeContext())
        : null
    }
  }
}

const reporter = Reporter('dynatrace')

export default reporter
