import { useTranslation } from 'react-i18next'

import * as icons from '@/components/Icons'

import classes from './Dropdown.module.scss'

export type IconName = 'terminate' | 'download'

export interface Option {
  label: string
  value: unknown
  icon?: IconName
  disabled?: boolean
}

interface DropdownContentProps {
  active?: string | unknown
  options?: Option[]
  onClickOption?: (option: Option) => void
}

const DropdownContent = ({ active, options, onClickOption }: DropdownContentProps) => {
  const [t] = useTranslation()
  const onclick = (option: Option) => {
    onClickOption?.(option)
  }

  return (
    <ul className={classes['dropdown-content']}>
      {options?.map((option, index) => {
        return (
          <li
            key={index}
            className={classes['dropdown-content-item']}
            data-disabled={!!option.disabled}
            data-active={option.value === active}
            title={t(option.label)}
            aria-label={t(option.label)}
          >
            <button disabled={option.disabled} aria-disabled={option.disabled} onClick={() => onclick(option)}>
              {Object.entries(icons).find(([key]) => key.toLowerCase() === option.icon)?.[1]?.({})}
              {t(option.label)}
            </button>
          </li>
        )
      })}
    </ul>
  )
}

export default DropdownContent
