import { type ReactElement, useMemo } from 'react'
import { useTranslation } from 'react-i18next'

import { Dialog } from '@/components/Dialog'
import { Retry } from '@/components/Icons'
import { optionsSelector, useConfig } from '@/stores/config'

interface Props {
  visible: boolean
  isLoading?: boolean
  onConfirm: () => void
  onCancel: () => void
}

const RetryEndChat = (props: Props): ReactElement => {
  const { visible, isLoading = false, onConfirm, onCancel } = props
  const [t] = useTranslation()

  const { chatDialogBoxRetryEndChat, chatConfirmButtonRetryEndChat, chatCancelButtonRetryEndChat } =
    useConfig(optionsSelector)

  const i18n = useMemo(
    () => ({
      chatDialogBoxRetryEndChatTitle: t('chatConfirmButtonRetryEndChat'),
      chatDialogBoxRetryEndChatDescription: chatDialogBoxRetryEndChat || t('chatDialogBoxRetryEndChat'),
      chatConfirmButtonRetryEndChat: chatConfirmButtonRetryEndChat || t('chatConfirmButtonRetryEndChat'),
      chatCancelButtonRetryEndChat: chatCancelButtonRetryEndChat || t('chatCancelButtonRetryEndChat')
    }),
    [t, chatDialogBoxRetryEndChat, chatConfirmButtonRetryEndChat, chatCancelButtonRetryEndChat]
  )

  return (
    <Dialog
      visible={visible}
      title={i18n.chatDialogBoxRetryEndChatTitle}
      description={i18n.chatDialogBoxRetryEndChatDescription}
      isLoading={isLoading}
      onConfirm={onConfirm}
      onCancel={onCancel}
      confirmText={i18n.chatConfirmButtonRetryEndChat}
      cancelText={i18n.chatCancelButtonRetryEndChat}
      icon={<Retry />}
    />
  )
}

export default RetryEndChat
