import { type ErrorInfo } from 'react'

import { MessageAvatar, MessageBaseInfo, MessageButton, MessageItem } from '@td-org/chat-widget-shared'

import ChatBubbleBox from '../ChatBubbleBox/index.ts'

import Avatar from './BubbleAvatar.tsx'
import Name from './BubbleName.tsx'
import Status from './BubbleStatus.tsx'
import Timestamp from './BubbleTImestamp.tsx'
import ChatMessage from './ChatMessage'

export type ChatBubbleProps = {
  enableRTL?: boolean
  hasName?: boolean
  hasAvatar?: boolean
  hasTimestamp?: boolean
  avatarUrl?: string
  avatarType?: MessageAvatar['type']
  avatarVerticalAlign?: string
  participantName?: string
  linkProtocol?: string
  linkTarget?: string
  isInSeries?: boolean
  isLastMessage?: boolean
  onButtonClick: (info: MessageButton) => void
  onResend: () => void
  onMediaSourceError?: (error: Error) => void
  errorMessageText?: string
  onMessageError?: (error: Error, errorInfo: ErrorInfo) => void
  onMediaLoadedData?: () => void
} & MessageBaseInfo &
  MessageItem

const ChatBubble = ({
  sid,
  tid,
  enableRTL = false,
  hasName = false,
  hasAvatar = false,
  hasTimestamp = false,
  status,
  avatarUrl,
  avatarType,
  avatarVerticalAlign,
  formattedTime,
  participantName = '',
  alignment,
  linkProtocol = 'https',
  linkTarget = '_blank',
  isLastMessage = false,
  isInSeries = false,
  onButtonClick,
  onResend,
  onMediaSourceError,
  errorMessageText,
  onMessageError,
  onMediaLoadedData,
  ...messageItemProps
}: ChatBubbleProps) => {
  const hasStatus = status !== 'success' && alignment === 'sent'

  const messageItem: MessageItem = messageItemProps

  return (
    <ChatBubbleBox
      status={hasStatus && <Status status={status} onResend={onResend} />}
      name={hasName && <Name name={participantName} />}
      avatar={hasAvatar && <Avatar align={avatarVerticalAlign} url={avatarUrl} type={avatarType} />}
      timestamp={hasTimestamp && <Timestamp text={formattedTime} />}
      alignment={alignment}
      type={messageItem.type}
      errorMessage={errorMessageText}
      onError={onMessageError}
      isLastMessage={isLastMessage}
      isInSeries={isInSeries}
    >
      <ChatMessage
        sid={sid}
        tid={tid}
        enableRTL={enableRTL}
        messageItem={messageItem}
        alignment={alignment}
        linkProtocol={linkProtocol}
        linkTarget={linkTarget}
        isLastMessage={isLastMessage}
        errorMessageText={errorMessageText}
        onButtonClick={onButtonClick}
        onMediaSourceError={onMediaSourceError}
        onMediaLoadedData={onMediaLoadedData}
      />
    </ChatBubbleBox>
  )
}

ChatBubble.whyDidYouRender = true

export default ChatBubble
