import { Message as TwilioMessage } from '@twilio/conversations'

import { MessageAttributes, Role } from '@td-org/chat-widget-shared'

import locales from '@/locales'
import { useConfig } from '@/stores/config.ts'
import { useMessageStore } from '@/stores/message'

import { getMessageRole } from './getMessageRole.ts'

export const getParticipantName = ({ role, agentName = '' }: { role: Role; agentName?: string }): string => {
  const { appearanceConfig } = useConfig.getState().config
  const { displayAgentsName, displayAutopilotsName, autopilotsName } = appearanceConfig.generalSettings

  switch (role) {
    case 'contact':
      return 'contactName'
    case 'va':
      return displayAutopilotsName ? (autopilotsName ? autopilotsName.trim() : 'autopilotsName') : ''
    case 'agent':
      return displayAgentsName ? agentName : ''
    default:
      return ''
  }
}

export const setMessageParticipantName = (message: TwilioMessage): void => {
  const attributes = message.attributes as MessageAttributes

  if (!attributes?.action?.metadata) return

  const { cur_identified_id, cur_participant_type, cur_display_name } = attributes.action.metadata

  if (cur_participant_type === 'LIVE_AGENT') {
    useMessageStore.getState().setAssigneeName(cur_identified_id, cur_display_name)
  }
}

export const getMessageParticipantName = (message: TwilioMessage): string | keyof typeof locales => {
  const role = getMessageRole(message)
  const agentId = (message.attributes as MessageAttributes)?.agentId || ''
  const agentName = useMessageStore.getState().getAssigneeName(agentId)

  return getParticipantName({ role, agentName })
}
