import { type ButtonsItem, MAX_ALLOWED, TextItem } from '@td-org/chat-widget-shared'

import { convertTextPayload } from '@/utils/receiver/converter/text.ts'
import type { TwilioButtonPayload } from '@/utils/twilio'
import { convertButtonElements, getLastMessageAndPrevious } from '../helpers.ts'

export function convertButtonsPayload(
  responses: string[],
  payload: TwilioButtonPayload
): Array<TextItem | ButtonsItem> {
  const { quick_actions, text_response } = payload

  const textMessages = text_response ? [text_response] : responses

  const { lastMessage, previousMessages } = getLastMessageAndPrevious(textMessages, '')

  return [
    ...convertTextPayload(previousMessages),
    {
      type: 'buttons',
      text: lastMessage,
      buttons: convertButtonElements(quick_actions.elements.slice(0, MAX_ALLOWED.BUTTONS))
    }
  ]
}
