import React from 'react'

import { Avatar } from '@td-org/chat-widget-components'
import { DEFAULT_OPTIONS } from '@td-org/chat-widget-shared'

import TextLink from '@/components/TextLink'
import { optionsSelector, useConfig } from '@/stores/config'
import clsx from '@/utils/clsx.ts'

import styles from './CustomMessage.module.scss'

interface Props {
  message: string
  isBubble: boolean
}

export const CustomMessage = React.forwardRef<HTMLDivElement, Props>((props, ref) => {
  const { message, isBubble } = props

  const { botIcon, botIconVerticalAlign } = useConfig(optionsSelector)

  return (
    <div className={styles.customMessage} ref={ref}>
      {isBubble ? (
        <div
          className={clsx(styles.avatarWrapper, {
            [styles.avatarWrapperAlign]: (botIconVerticalAlign || DEFAULT_OPTIONS.botIconVerticalAlign) === 'top'
          })}
        >
          <Avatar url={botIcon} />
        </div>
      ) : null}

      <div className={styles.wrapper}>
        <div className={clsx(styles.messageContent, { [styles.isBubble]: isBubble })}>
          <TextLink target={message} />
        </div>
      </div>
    </div>
  )
})
