import type { MessageAlignment, MessageButton, MessageItem } from '@td-org/chat-widget-shared'

import VaButtons from '../Buttons/VaButtons.tsx'
import Carousel from '../Carousel/index.ts'
import HtmlMessage from '../HtmlMessage/index.ts'
import MediaMessage from '../MediaMessage/index.ts'
import QuickReplies from '../QuickReply/index.ts'
import SystemMessage from '../SystemMessage'
import TextMessage from '../TextMessage/index.ts'
import TypingIndicator from '../TypingIndicator/index.ts'

type ChatBubbleContainerProps = {
  sid: string
  tid: string
  enableRTL?: boolean
  messageItem: MessageItem
  alignment: MessageAlignment
  linkProtocol: string
  linkTarget: string
  isLastMessage?: boolean
  errorMessageText?: string
  onButtonClick: (info: MessageButton) => void
  onMediaSourceError?: (e: Error) => void
  onMediaLoadedData?: () => void
}

const ChatBubbleContainer = ({
  sid,
  tid,
  enableRTL,
  messageItem,
  onButtonClick,
  onMediaSourceError,
  linkProtocol,
  linkTarget,
  isLastMessage = false,
  errorMessageText,
  onMediaLoadedData
}: ChatBubbleContainerProps) => {
  const type = messageItem.type
  let unSupportedType: string

  const textMessageProps = {
    linkProtocol,
    linkTarget
  }

  switch (type) {
    case 'text':
      return <TextMessage {...messageItem} {...textMessageProps} />
    case 'quick_replies':
      return (
        <QuickReplies
          {...messageItem}
          {...textMessageProps}
          isLastMessage={isLastMessage}
          onButtonClick={onButtonClick}
        />
      )
    case 'carousel':
      return (
        <Carousel
          enableRTL={enableRTL}
          items={messageItem.cards}
          onButtonClick={onButtonClick}
          onError={onMediaSourceError}
        />
      )
    case 'buttons':
      return (
        <VaButtons
          {...textMessageProps}
          text={messageItem.text}
          buttons={messageItem.buttons}
          onButtonClick={onButtonClick}
        />
      )
    case 'rich_text':
      return <HtmlMessage html={messageItem.html} />
    case 'video':
    case 'audio':
    case 'file':
    case 'image':
      return (
        <MediaMessage
          identity={tid || sid}
          onMediaLoadedData={onMediaLoadedData}
          onError={onMediaSourceError}
          type={messageItem.type}
          url={messageItem.url || ''}
          getContentTemporaryUrl={messageItem.getContentTemporaryUrl}
          children={messageItem.children}
        />
      )
    case 'typing':
      return <TypingIndicator />
    case 'system':
      return <SystemMessage {...messageItem} />
    case 'error':
      return <TextMessage text={errorMessageText || messageItem.error_message} {...textMessageProps} />

    default:
      unSupportedType = type
      return <TextMessage text={`Type "${unSupportedType}" is not supported`} {...textMessageProps} />
  }
}

export default ChatBubbleContainer
