import { useEnableDownloadTranscript } from '@/stores/app/selectors.ts'
import { useApp } from '@/stores/app/store.ts'
import { useRpc } from '@/stores/rpc'
import logger from '@/utils/logger'

const useDownloadTranscript = () => {
  const enableDownloadTranscript = useEnableDownloadTranscript()

  const disableDownloadTranscript = !enableDownloadTranscript

  const downloadTranscript = async () => {
    const { interactionId } = useApp.getState()

    const response = await useRpc.getState().downloadTranscript(interactionId)

    if (response?.error) {
      logger.error(`Failed to download transcript for interactionId=${interactionId} error=${response.error}`)
    }
  }

  return {
    disableDownloadTranscript,
    downloadTranscript
  }
}

export default useDownloadTranscript
