import { type ILogObj, type ISettingsParam, Logger } from 'tslog'

import reporter from './reporter.ts'

export const LogLevel = {
  SILLY: 0,
  TRACE: 1,
  DEBUG: 2,
  INFO: 3,
  WARN: 4,
  ERROR: 5,
  FATAL: 6
}
const minLevel = import.meta.env.DEV ? LogLevel.SILLY : LogLevel.INFO
const buildStage = import.meta.env.VITE_STAGE

const getChildSpanName = <T>(name: string, settings?: ISettingsParam<T>) =>
  settings?.name ? `${settings.name}:${name}` : name

class CustomerLogger<T> extends Logger<T> {
  constructor(settings?: ISettingsParam<T>, logObj?: T) {
    super(settings, logObj)
  }

  public startSpan(name: string) {
    return reporter.startSpan(name)
  }

  public startChildSpan(name: string, parent: ReturnType<typeof reporter.startSpan>) {
    return reporter.startChildSpan(name, parent)
  }

  public override getSubLogger(settings?: ISettingsParam<T>, logObj?: T) {
    const logger = super.getSubLogger(settings, logObj) as Logger<T> & {
      startSpan: typeof reporter.startSpan
      startChildSpan: typeof reporter.startChildSpan
    }

    logger.startSpan = (name: string) => reporter.startSpan(getChildSpanName<T>(name, settings))
    logger.startChildSpan = (name: string, parent: ReturnType<typeof reporter.startSpan>) =>
      reporter.startChildSpan(getChildSpanName<T>(name, settings), parent)

    return logger
  }
}

const logger = new CustomerLogger<ILogObj>({
  type: buildStage === 'prd' ? 'hidden' : 'pretty',
  name: 'Widget',
  minLevel,
  prettyLogTemplate: '{{dateIsoStr}} [{{logLevelName}}] {{name}} ',
  attachedTransports: [(log) => reporter.run(log, minLevel)]
})

export default logger
