import IconButton from '@/components/IconButton'
import { Terminate as TerminateIcon } from '@/components/Icons'

import classes from '../ChatHeader.module.scss'

interface EndChatButtonProps {
  disabled: boolean
  onClick: () => void
}

const EndChatButton = ({ disabled, onClick }: EndChatButtonProps) => {
  return (
    <IconButton onClick={onClick} disabled={disabled} aria-label='end chat conversation'>
      <div className={classes.icon}>
        <TerminateIcon />
      </div>
    </IconButton>
  )
}

export default EndChatButton
