import { create } from 'zustand'
import { useShallow } from 'zustand/react/shallow'

import { getDevice,injectCssVariables, WidgetSettings } from '@td-org/chat-widget-shared'

import { Config } from '@/models/Config'
import i18nConfig from '@/utils/i18nConfig.ts'
import { extractCssVars, extractOptions, reassignCssVars } from '@/utils/themeUtil.ts'

import { useRpc } from './rpc'

type State = {
  page: 'Conversation' | 'InitialScreen' | 'MockConversation'
  config: Config
  reset: () => void
  init: () => Promise<void>
  update: () => Promise<void>
  updateContext: () => void
}

export const usePreview = create<State>((set, get) => {
  return {
    page: 'Conversation',
    config: new Config(),
    reset: () => {
      set({ config: new Config() })
    },
    init: async () => {
      useRpc.getState().onPreviewConfigChange((previewConfig) => {
        const { title, description, logoUrl } = previewConfig?.widgetSettings?.appearance || {}

        const options = new Config().options

        set((state) => ({
          page: previewConfig.page,
          config: {
            ...state.config,
            appearanceConfig: {
              ...state.config.appearanceConfig,
              ...previewConfig.widgetSettings
            },
            options: {
              ...new Config().options,
              ...get().config.options,
              chatTitle: title ? title : options?.chatTitle,
              chatSubtitle: description ? description : options.chatSubtitle,
              chatTitleIcon: logoUrl || options.chatTitleIcon
            },
            participantName: previewConfig.userName
          }
        }))

        const cssVars = extractCssVars(reassignCssVars(previewConfig?.widgetSettings?.appearance))

        injectCssVariables(document.documentElement, cssVars)
      })

      const response = await useRpc.getState().getAllConfig()

      if (response?.data) {
        const { isMobile, type: deviceType } = getDevice()
        const { appProps, appConfig, context, touchpoint, participantName } = response.data
        const config = new Config()
        const languageCode = await i18nConfig.changeLanguage(appConfig?.languageCode)

        document.documentElement.className = deviceType

        config.isMobile = isMobile
        config.deviceType = deviceType
        config.appConfig = {
          ...get().config.appConfig,
          ...appConfig,
          languageCode
        } as Config['appConfig']
        config.appProps = appProps
        config.context = context
        config.participantName = participantName

        try {
          if (touchpoint.config) {
            config.appearanceConfig = JSON.parse(touchpoint.config) as WidgetSettings.WidgetSettings
          }
        } catch (e) {
          console.log('Error parsing appearance config, using default appearance config')
        }
        config.options = {
          ...get().config.options,
          ...extractOptions(appConfig?.styles)
        }
        config.styles = appConfig?.styles || {}
        set({ config })
        const cssVars = extractCssVars(appConfig?.styles)
        injectCssVariables(document.documentElement, cssVars)

        await useRpc.getState().previewReady()
      }
    },
    update: () => Promise.resolve(),
    updateContext: () => {}
  }
})

export const usePreviewConfig = () =>
  usePreview(
    useShallow((state) => {
      return {
        page: state.page,
        chatTitle: state.config.options.chatTitle,
        chatSubtitle: state.config.options.chatSubtitle,
        enableEmoji: state.config.appConfig.enableEmoji,
        enableAttachments: state.config.appConfig.enableAttachments,
        enableValidation: state.config.appConfig.enableValidation,
        welcomeMessage: state.config.appearanceConfig.initialScreen.welcomeMessage,
        showInitialScreen: state.config.appearanceConfig.initialScreen.showInitialScreen,
        controls: state.config.appearanceConfig.initialScreen.controls,
        showCloseConversationButton: state.config.appearanceConfig.generalSettings.showCloseConversationButton,
        showDownloadTranscriptButton: state.config.appearanceConfig.generalSettings.showChatTranscriptsDownloadButton,
        showTypingIndicatorButton: state.config.appearanceConfig.generalSettings.showTypingIndicatorButton,
        context: state.config.context,
        isDisabled: state.page === 'InitialScreen' && !state.config.appearanceConfig.initialScreen.showInitialScreen
      }
    })
  )

export const optionsSelector = (state: State): Config['options'] => {
  return state.config.options
}
