import { sleep } from './sleep'

type PromiseOptions = {
  defaultRetry?: number
  retryDelay?: number | ((retry: number) => number)
  onError?: (e: unknown, retry: number) => void
}

function defaultRetryDelay(retry: number) {
  return Math.min(2000 * retry, 30000)
}

export async function promiseRetry<T>(promiseTask: () => Promise<T>, options: PromiseOptions = {}) {
  const { defaultRetry = 5, retryDelay = defaultRetryDelay, onError } = options

  if (defaultRetry <= 0) {
    return await promiseTask()
  }

  let retry = 0

  const run = async (): Promise<T> => {
    try {
      return await promiseTask()
    } catch (e) {
      onError?.(e, retry)

      if (retry === defaultRetry) {
        throw e
      }

      retry += 1

      await sleep(typeof retryDelay === 'function' ? retryDelay(retry) : retryDelay)

      return await run()
    }
  }

  return run()
}
