import { useEffect } from 'react'

type UseClickOutsidePayload = {
  nodes: (HTMLButtonElement | HTMLDivElement | null)[]
  handler?: () => void
  isExecuted: boolean
}

const useClickOutside = ({ handler, isExecuted, nodes }: UseClickOutsidePayload) => {
  useEffect(() => {
    if (!nodes?.[0] || !isExecuted) return

    const containsNode = (target: Node) => nodes.some((node) => node === target || node?.contains(target))

    const handleEvent = ({ target }: MouseEvent | FocusEvent) => {
      if (target instanceof Node && containsNode(target)) {
        return
      }
      handler?.()
    }

    document.addEventListener('click', handleEvent)
    window.addEventListener('blur', handleEvent)

    return () => {
      document.removeEventListener('click', handleEvent)
      window.removeEventListener('blur', handleEvent)
    }
  }, [isExecuted, nodes, handler])
}

export default useClickOutside
