import React from 'react'

import LazyIcon from './LazyIcon'
import { type IconProps } from './types.ts'

const LazyArrow = React.lazy(() => import('./Arrow'))
const LazyAttachment = React.lazy(() => import('./Attachment'))
const LazyClose = React.lazy(() => import('./Close'))
const LazyClear = React.lazy(() => import('./Clear'))
const LazyConfirm = React.lazy(() => import('./Confirm'))
const LazyEmoji = React.lazy(() => import('./Emoji'))
const LazyMinimize = React.lazy(() => import('./Minimize'))
const LazyMoreVert = React.lazy(() => import('./MoreVert'))
const LazyRetry = React.lazy(() => import('./Retry'))
const LazySend = React.lazy(() => import('./Send'))
const LazyWarning = React.lazy(() => import('./Warning'))
const LazyLogo = React.lazy(() => import('./Logo'))
const LazyDownload = React.lazy(() => import('./Download'))
const LazyTerminate = React.lazy(() => import('./Terminate'))
const LazyRefresh = React.lazy(() => import('./Refresh'))

const Arrow = (props: IconProps) => (
  <LazyIcon>
    <LazyArrow {...props} />
  </LazyIcon>
)

const Attachment = () => (
  <LazyIcon>
    <LazyAttachment />
  </LazyIcon>
)

const Close = () => (
  <LazyIcon>
    <LazyClose />
  </LazyIcon>
)

const Clear = () => (
  <LazyIcon>
    <LazyClear />
  </LazyIcon>
)

const Confirm = () => (
  <LazyIcon>
    <LazyConfirm />
  </LazyIcon>
)

const Emoji = () => (
  <LazyIcon>
    <LazyEmoji />
  </LazyIcon>
)

const Minimize = () => (
  <LazyIcon>
    <LazyMinimize />
  </LazyIcon>
)

const MoreVert = () => (
  <LazyIcon>
    <LazyMoreVert />
  </LazyIcon>
)

const Retry = () => (
  <LazyIcon>
    <LazyRetry />
  </LazyIcon>
)

const Send = () => (
  <LazyIcon>
    <LazySend />
  </LazyIcon>
)

const Download = () => (
  <LazyIcon>
    <LazyDownload />
  </LazyIcon>
)

const Terminate = () => (
  <LazyIcon>
    <LazyTerminate />
  </LazyIcon>
)

const Refresh = () => (
  <LazyIcon>
    <LazyRefresh />
  </LazyIcon>
)

const Warning = () => (
  <LazyIcon>
    <LazyWarning />
  </LazyIcon>
)

const Logo = (props: IconProps) => (
  <LazyIcon>
    <LazyLogo {...props} />
  </LazyIcon>
)

export {
  Arrow,
  Attachment,
  Close,
  Clear,
  Confirm,
  Emoji,
  Minimize,
  MoreVert,
  Retry,
  Send,
  Warning,
  Logo,
  Download,
  Terminate,
  Refresh
}
