import { lazy } from 'react'

import { Loadable } from '../Loadable'

import { MediaMessageProps } from './types.ts'

export const preload = () => import('./MediaMessage.tsx')

export default lazy(async () => {
  const Component = (await preload()).default

  const LoadableComponent = Loadable<MediaMessageProps>(Component, async (props) => {
    const { getContentTemporaryUrl } = props
    if (getContentTemporaryUrl) {
      const url = await getContentTemporaryUrl()
      if (url) return { url }
    }
    return {}
  })

  return {
    default: LoadableComponent
  }
})
