import { ValidateResult } from 'react-hook-form'
import i18next from 'i18next'

import { EMAIL_REGEX, PHONE_REGEX } from '@td-org/chat-widget-shared'

export const validateEmail = async (value: string): Promise<ValidateResult> => {
  const isValid = EMAIL_REGEX.test(value)

  if (!isValid && value) {
    return i18next.t('customFieldEmailInvalidFormatMessage') as string
  }

  return true
}

export const validatePhoneNumber = async (value: string): Promise<ValidateResult> => {
  const isValid = PHONE_REGEX.test(value)

  if (!isValid && value) {
    return i18next.t('customFieldPhoneInvalidFormatMessage') as string
  }

  return true
}
