import type { AriaAttributes, MouseEvent, ReactElement, ReactNode, RefObject } from 'react'

import clsx from '@/utils/clsx.ts'

import classes from './Button.module.scss'

type Props = {
  forwardedRef?: RefObject<HTMLButtonElement>
  onClick?: (e: MouseEvent<HTMLButtonElement>) => void
  children: ReactNode
  className?: string
  disabled?: boolean
} & AriaAttributes

const Button = (props: Props): ReactElement => {
  const { forwardedRef, children, onClick, disabled, className, ...ariaAttributes } = props

  const handleClick = (e: MouseEvent<HTMLButtonElement>): void => {
    if (disabled) {
      return
    }

    onClick?.(e)
  }

  return (
    <button
      ref={forwardedRef}
      onClick={handleClick}
      disabled={disabled}
      aria-disabled={disabled}
      className={clsx(classes.button, className)}
      {...ariaAttributes}
    >
      {children}
    </button>
  )
}

export default Button
