import React, { useCallback, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'

import { Arrow } from '@/components/Icons'
import ErrorMessage from '@/layout/InitialScreen/components/ErrorMessage'
import { optionsSelector, useConfig } from '@/stores/config'
import clsx from '@/utils/clsx'
import { toLowerCase } from '../../utils/helper'

import commonStyles from '../../common.module.scss'
import styles from './CustomDropdown.module.scss'

interface Props {
  label: string
  onChange: (value: string) => void
  onBlur: () => void
  options: Option[]
  value: string
  name: string
  isError: boolean
  required: boolean
}

interface Option {
  // label: string
  value: string
  key: string
}

interface SelectProps {
  options: Option[]
  onChange: (value: string) => void
  onBlur: () => void
  value: string
  name: string
  label: string
  isError: boolean
}

export const Select = (props: SelectProps) => {
  const { options, onChange, value, onBlur, isError, label, name } = props
  const dropdownSelectRef = useRef<HTMLDivElement>(null)
  const dropdownMenuRef = useRef<HTMLUListElement>(null)
  const isClicked = useRef(false)
  const [open, setOpen] = useState<boolean>(false)

  const { customDropdownInputPlaceholder } = useConfig(optionsSelector)
  const [t] = useTranslation()

  const placeholder = t('customDropdownPlaceholder', {
    select: customDropdownInputPlaceholder || t('customDropdownInputPlaceholder'),
    label: toLowerCase(label),
    interpolation: { escapeValue: false }
  })

  const handleTrigger = (): void => {
    setOpen(!open)
  }

  const handleSelectOption = (option: Option) => () => {
    onChange(option.value)
    handleTrigger()
  }

  const handleEvent = useCallback(() => {
    if (isClicked.current) {
      isClicked.current = false

      return
    }

    setOpen(false)
  }, [])

  const handleClickCapture = useCallback(() => {
    isClicked.current = true
  }, [])

  useEffect(() => {
    document.addEventListener('click', handleEvent)

    return () => {
      document.removeEventListener('click', handleEvent)
    }
  }, [handleEvent])

  return (
    <div ref={dropdownSelectRef} className={styles.selectContainer} onClickCapture={handleClickCapture}>
      <button
        className={clsx(styles.dropdown, {
          [commonStyles.isError]: isError
        })}
        name={name}
        type='button'
        onClick={handleTrigger}
        onBlur={onBlur}
        aria-expanded={open}
        aria-labelledby={name}
      >
        {value ? (
          <span className={styles.text} title={value}>
            {value}
          </span>
        ) : (
          <span className={styles.placeholder} title={placeholder}>
            {placeholder}
          </span>
        )}
        <Arrow
          className={clsx(styles.arrow, {
            [styles.isActive]: open
          })}
        />
      </button>

      <ul
        className={clsx(styles.menu, { [styles.visible]: open })}
        role='listbox'
        aria-labelledby={name}
        ref={dropdownMenuRef}
      >
        {options?.map((option) => (
          <li
            role='option'
            aria-selected={value === option.value}
            key={option.key}
            title={option.value}
            className={clsx({ [styles.selected]: value === option.value })}
          >
            <button
              className={clsx(styles.option, { [styles.selected]: value === option.value })}
              type='button'
              onClick={handleSelectOption(option)}
            >
              {option.value}
            </button>
          </li>
        ))}
      </ul>
    </div>
  )
}

export const CustomDropdown = React.forwardRef<HTMLDivElement, Props>((props, ref) => {
  const { name, label, onChange, onBlur, options, value, isError, required } = props

  const [t] = useTranslation()

  return (
    <div className={styles.customDropdown} ref={ref}>
      <div className={styles.wrapper}>
        <div className={styles.labelWrapper}>
          <span className={styles.label} id={name}>
            {label}
            {required ? (
              <span aria-label='required' className={commonStyles.isRequired}>
                *
              </span>
            ) : null}
          </span>
        </div>

        <Select
          label={label}
          options={options}
          onChange={onChange}
          onBlur={onBlur}
          value={value}
          isError={isError}
          name={name}
        />

        {isError && <ErrorMessage message={t('customFieldRequiredFormatMessage')} />}
      </div>
    </div>
  )
})
