import { useEffect, useState } from 'react'

import { useAppAction } from '@/stores/app/selectors.ts'

export function useConnectionStatus() {
  const [connected, setConnected] = useState(true)
  const { on, off } = useAppAction()

  useEffect(() => {
    const disconnectHandler = () => {
      setConnected(false)
    }

    const reconnectHandler = () => {
      setConnected(true)
    }

    on('disconnect', disconnectHandler)
    on('reconnect', reconnectHandler)
    on('leave_end', reconnectHandler)
    on('leave_error', reconnectHandler)

    return () => {
      off('disconnect', disconnectHandler)
      off('reconnect', reconnectHandler)
      off('leave_end', reconnectHandler)
      off('leave_error', reconnectHandler)
    }
  }, [on, off])

  return connected
}
