import { lazy, Suspense } from 'react'
import clsx from 'clsx'

import { MessageAvatar } from '@td-org/chat-widget-shared'

import classes from './Avatar.module.scss'

type AvatarProps = {
  url?: string
  className?: string
  type?: MessageAvatar['type']
}

const DefaultAgentAvatarIcon = lazy(() => import('./DefaultAvatarIcon/DefaultAgentAvatarIcon'))
const DefaultAvatarIcon = lazy(() => import('./DefaultAvatarIcon/DefaultAvatarIcon'))

const Avatar = ({ url, className, type = 'system' }: AvatarProps) => {
  return url ? (
    <div
      className={clsx(classes.AvatarBox, className)}
      data-testid={'avatar'}
      style={{ background: `url(${url}) center center / cover no-repeat` }}
    />
  ) : (
    <div className={clsx(classes.AvatarBox, className)} data-testid={'avatar'}>
      {type === 'agent' ? (
        <Suspense>
          <DefaultAgentAvatarIcon />
        </Suspense>
      ) : (
        <Suspense>
          <DefaultAvatarIcon />
        </Suspense>
      )}
    </div>
  )
}

export default Avatar
