import { useCallback, useEffect, useRef } from 'react'
import { OverlayScrollbarsComponentRef } from 'overlayscrollbars-react'

import { AppEvent } from '@td-org/chat-widget-shared'

import { useAppAction, useFrameOpened } from '@/stores/app/selectors.ts'

function createIntersectionOs(
  el: HTMLDivElement,
  options: IntersectionObserverInit,
  onChange?: (isIntersecting: boolean) => void
) {
  const ob = new IntersectionObserver((entries) => {
    const isIntersecting = entries[0].isIntersecting
    onChange?.(isIntersecting)
  }, options)

  ob.observe(el)
  return ob
}

const useIntersection = () => {
  const headElRef = useRef<HTMLDivElement | null>(null)
  const footerElRef = useRef<HTMLDivElement | null>(null)

  const run = useCallback(
    (payload: {
      options: IntersectionObserverInit
      onHeader: (isIntersecting: boolean) => void
      onFooter: (isIntersecting: boolean) => void
    }) => {
      const headerEl = headElRef.current as HTMLDivElement
      const footerEl = footerElRef.current as HTMLDivElement

      let headerOb: IntersectionObserver
      let footerOb: IntersectionObserver

      if (headerEl) {
        headerOb = createIntersectionOs(headerEl, payload.options, (isIntersecting) => {
          payload.onHeader(isIntersecting)
        })
      }

      if (footerEl) {
        footerOb = createIntersectionOs(footerEl, payload.options, (isIntersecting) => {
          payload.onFooter(isIntersecting)
        })
      }

      return () => {
        headerOb?.disconnect()
        footerOb?.disconnect()
      }
    },
    []
  )

  return {
    headElRef,
    footerElRef,
    run
  }
}

export const useScrollForInitialScreen = () => {
  const osRef = useRef<OverlayScrollbarsComponentRef | null>(null)
  const { updateInitialScreenScroll } = useAppAction()
  const { headElRef, footerElRef, run } = useIntersection()

  useEffect(() => {
    const root = osRef.current?.getElement() as HTMLDivElement

    if (!root) {
      return
    }

    return run({
      options: { root },
      onHeader: (isIntersecting: boolean) => {
        updateInitialScreenScroll({ headerVisible: isIntersecting })
      },
      onFooter: (isIntersecting: boolean) => {
        updateInitialScreenScroll({ footerVisible: isIntersecting })
      }
    })
  }, [run, updateInitialScreenScroll])

  return {
    osRef,
    headElRef,
    footerElRef
  }
}

export const useScrollForChatContent = () => {
  const timerRef = useRef<NodeJS.Timeout | null>(null)
  const timerScrollRef = useRef<NodeJS.Timeout | null>(null)
  const osRef = useRef<OverlayScrollbarsComponentRef | null>(null)
  const footerVisibleRef = useRef<boolean>(false)

  const { headElRef, footerElRef, run } = useIntersection()
  const opened = useFrameOpened()
  const { on, off, updateChatContentScroll, updateUnreadMessageCount } = useAppAction()

  const scrollToBottom = useCallback(
    (animate = true) => {
      if (timerRef.current) {
        clearTimeout(timerRef.current)
        timerRef.current = null
      }
      setTimeout(() => {
        updateUnreadMessageCount(0)
        const instance = osRef.current?.osInstance()
        if (instance) {
          const { scrollOffsetElement } = instance.elements()
          const scrollHeight = Math.ceil(scrollOffsetElement.scrollHeight)
          scrollOffsetElement.scrollTo({ behavior: animate ? 'smooth' : 'auto', top: scrollHeight })
        }
      }, 1000 / 60)
    },
    [updateUnreadMessageCount]
  )

  const onScroll = useCallback(() => {
    const target = osRef.current?.osInstance()?.elements()?.scrollOffsetElement as HTMLDivElement
    if (!target) return
    const { scrollTop = 0, scrollHeight = 0, offsetHeight = 0 } = target
    const formatOffsetHeight = Math.floor(offsetHeight)
    const formatScrollHeight = Math.floor(scrollHeight)
    const formatScrollTop = Math.floor(scrollTop)
    const formatScrollOver = formatScrollHeight - formatOffsetHeight
    const payload =
      formatScrollOver <= 0
        ? { scrollTop: formatScrollTop, scrollOver: formatScrollOver, scrollPercent: 0 }
        : {
            scrollTop: formatScrollTop,
            scrollOver: formatScrollOver,
            scrollPercent: Math.ceil((formatScrollTop / (formatScrollHeight - formatOffsetHeight)) * 100)
          }
    if (timerScrollRef.current) {
      clearTimeout(timerScrollRef.current)
      timerScrollRef.current = null
    }
    timerScrollRef.current = setTimeout(() => {
      updateChatContentScroll(payload)
    }, 50)
  }, [updateChatContentScroll])

  useEffect(() => {
    on(AppEvent.need_scroll_to_bottom, scrollToBottom)
    on(AppEvent.send_success, scrollToBottom)
    on(AppEvent.send_failure, scrollToBottom)

    return () => {
      off(AppEvent.need_scroll_to_bottom, scrollToBottom)
      off(AppEvent.send_success, scrollToBottom)
      off(AppEvent.send_failure, scrollToBottom)
    }
  }, [scrollToBottom, on, off])

  useEffect(() => {
    const root = osRef.current?.getElement() as HTMLDivElement

    if (!root) {
      return
    }

    return run({
      options: { root },
      onHeader: (isIntersecting: boolean) => {
        updateChatContentScroll({ headerVisible: isIntersecting })
      },
      onFooter: (isIntersecting: boolean) => {
        updateChatContentScroll({ footerVisible: isIntersecting })
        footerVisibleRef.current = isIntersecting
      }
    })
  }, [run, updateChatContentScroll])

  useEffect(() => {
    const element = osRef.current?.getElement() as HTMLDivElement

    if (!element) return

    const ob = new MutationObserver(() => {
      if (footerVisibleRef.current) {
        scrollToBottom()
      }
    })

    ob.observe(element, {
      childList: true,
      attributes: true,
      subtree: true,
      attributeFilter: []
    })

    return () => {
      ob.disconnect()
    }
  }, [scrollToBottom])

  useEffect(() => {
    if (opened) {
      scrollToBottom()
    }
  }, [opened, scrollToBottom])

  useEffect(() => {
    return () => {
      if (timerRef.current) {
        clearTimeout(timerRef.current)
        timerRef.current = null
      }
      if (timerScrollRef.current) {
        clearTimeout(timerScrollRef.current)
        timerScrollRef.current
      }
    }
  }, [])

  return {
    osRef,
    headElRef,
    footerElRef,
    onScroll
  }
}
