import type { Message as TwilioMessage } from '@twilio/conversations'

import { MediaType, Message, Role } from '@td-org/chat-widget-shared'

import { getMessageRole, getMessageRoleId } from '@/utils/getMessageRole.ts'

import { getMessageAvatar } from './getMessageAvatar.ts'
import { getMessageParticipantName, getParticipantName } from './getMessageParticipantName.ts'
import { toLocaleTimeString } from './time.ts'

type BuildTypingMessagePayload = {
  role: Role
  agentName?: string
}

const TYPING_MESSAGE_ID = 'TYPING_MESSAGE_ID'
const TYPING_MESSAGE = {
  tid: TYPING_MESSAGE_ID,
  sid: 'typing',
  status: 'success',
  alignment: 'received',
  timestamp: 0,
  formattedTime: '',
  items: [
    {
      type: 'typing'
    }
  ]
}

export function isTypingMessage(id: string) {
  return id === TYPING_MESSAGE_ID
}

export function removeTypingMessage(cb?: (id: string) => void) {
  cb?.(TYPING_MESSAGE_ID)
}

export function buildTypingMessage({ role, agentName }: BuildTypingMessagePayload) {
  const avatar = getMessageAvatar(role)
  const participantName = getParticipantName({ role, agentName })

  return {
    ...TYPING_MESSAGE,
    role,
    avatar,
    participantName
  } as Message
}

export function buildMediaMessage(
  tid: string,
  media: FormData,
  mediaType: MediaType,
  props?: Partial<Message>
): Message {
  const timestamp = Date.now()

  const file = media.get('file')

  const fileUrl = file instanceof Blob ? URL.createObjectURL(file) : ''

  return {
    sid: '',
    alignment: 'sent',
    tid: tid,
    status: 'sending',
    items: [
      {
        type: mediaType,
        url: fileUrl,
        smart_text: {
          key: 'download'
        }
      }
    ],
    timestamp,
    formattedTime: toLocaleTimeString(timestamp),
    ...props
  }
}

export function buildTextMessage(tid: string, text: string, props?: Partial<Message>): Message {
  const timestamp = Date.now()

  return {
    sid: '',
    alignment: 'sent',
    tid: tid,
    status: 'sending',
    items: [
      {
        type: 'text',
        text
      }
    ],
    timestamp,
    formattedTime: toLocaleTimeString(timestamp),
    ...props
  }
}

export function buildUpdatedMessage(message: TwilioMessage): Message {
  const { sid, index } = message

  const rid = getMessageRoleId(message)
  const role = getMessageRole(message)
  const participantName = getMessageParticipantName(message)

  return {
    index,
    sid,
    rid,
    role,
    participantName,
    status: 'success'
  } as Message
}
