import clsx from 'clsx'

import classes from './Spinner.module.scss'

type SpinnerProps = { size?: 'tiny' | 'small' | 'medium' } // 16 | 20 | 24

const Spinner = ({ size = 'small' }: SpinnerProps) => {
  return <div className={clsx(classes.spinner, classes[size])} data-testid='sending' />
}

export default Spinner
