import React, { type ReactNode } from 'react'
import { createPortal } from 'react-dom'

import { usePortal } from './usePortal.ts'

type PortalProps = {
  children: ReactNode
}

export function Portal({ children }: PortalProps): React.ReactPortal {
  const { element } = usePortal()

  return createPortal(children, element as Element)
}
