import { type Message as TwilioMessage } from '@twilio/conversations'

import {
  type Message,
  type MessageAlignment,
  type MessageAttributes,
  type MessageItem
} from '@td-org/chat-widget-shared'

import baseLogger from '@/utils/logger.ts'
import { convertCustomText } from '@/utils/receiver/converter/customText.ts'
import { convertHTMLContentPayload } from '@/utils/receiver/converter/htmlContent.ts'
import { validateSchema } from '@/utils/schema/validator.ts'
import {
  ErrorPayload,
  TwilioButtonPayload,
  TwilioCarouselPayload,
  TwilioCustomTextPayload,
  TwilioHTMLContentPayload,
  TwilioMediaPayload,
  TwilioMultiContentPayload,
  TwilioQuickRepliesPayload,
  TwilioSystemMessagePayload
} from '@/utils/twilio'
import { getFileMimes } from '../file.ts'
import { getMessageAvatar } from '../getMessageAvatar.ts'
import { getMessageParticipantName } from '../getMessageParticipantName.ts'
import { getMessageRole, getMessageRoleId } from '../getMessageRole.ts'
import { toLocaleTimeString } from '../time.ts'

import { convertButtonsPayload } from './converter/buttons.ts'
import { convertCarouselPayload } from './converter/carousel.ts'
import { convertMediaPayload } from './converter/media.ts'
import { convertQuickReplyPayload } from './converter/quickReply.ts'
import { convertSystemMessagePayload } from './converter/systemMessage.ts'
import { convertTextPayload } from './converter/text.ts'
import { schemaErrorHandler } from './helpers.ts'

type TwilioPayload =
  | { media_type: 'text' }
  | TwilioCustomTextPayload
  | TwilioCarouselPayload
  | TwilioMediaPayload
  | TwilioQuickRepliesPayload
  | TwilioButtonPayload
  | TwilioMultiContentPayload
  | TwilioHTMLContentPayload
  | TwilioSystemMessagePayload
  | ErrorPayload

const logger = baseLogger.getSubLogger({ name: 'receiver' })

export function receiveTwilioMessage(message: TwilioMessage, identity: string): Message {
  const { author = '', sid, index } = message

  const { responses, payload } = standardize(message)

  const status = 'success'
  const alignment: MessageAlignment = author === identity ? 'sent' : 'received'
  const timestamp = new Date(message.dateCreated || Date.now()).getTime()
  const formattedTime = timestamp ? toLocaleTimeString(timestamp) : ''
  const rid = getMessageRoleId(message)
  const role = getMessageRole(message)
  const avatar = getMessageAvatar(role)
  const participantName = getMessageParticipantName(message)

  const items: MessageItem[] = convert(responses, payload)

  return {
    index,
    sid,
    tid: '',
    rid,
    status,
    alignment,
    timestamp,
    formattedTime,
    items,
    role,
    avatar,
    participantName
  }
}

function convert(responses: string[], payload: TwilioPayload): MessageItem[] {
  let items: MessageItem[] = []

  switch (payload.media_type) {
    case 'text':
      items = convertTextPayload(responses)
      break

    case 'customtext':
      items = convertCustomText(payload)
      break

    case 'htmlcontent':
      items = convertHTMLContentPayload(payload)
      break

    case 'carousel':
      items = convertCarouselPayload(responses, payload)
      break

    case 'media':
      items = convertMediaPayload(responses, payload)
      break

    case 'buttons':
      items = convertButtonsPayload(responses, payload)
      break

    case 'quickreplies':
      items = convertQuickReplyPayload(responses, payload)
      break

    case 'system':
      items = convertSystemMessagePayload(payload)
      break

    case 'multicontent':
      items = payload.content.map((_payload) => convert([], _payload)).flat()
      break

    case 'error':
      items = [{ type: payload.media_type, error_message: payload.error_message }]
      break

    default:
      items = convertTextPayload(responses)
      break
  }

  return items
}

function standardize(message: TwilioMessage): {
  payload: TwilioPayload
  responses: string[]
} {
  const { type } = message
  const body = message.body
  const isMedia = type === 'media'

  let responses = body ? [body] : []
  let payload: TwilioPayload = {
    media_type: 'text'
  }

  const attachedMedia = message.attachedMedia
  if (isMedia && attachedMedia) {
    const mediaItem = attachedMedia[0]
    const contentType = getFileMimes(mediaItem.contentType).prefix
    const getContentTemporaryUrl = () => mediaItem.getContentTemporaryUrl()
    payload = {
      media_type: 'media',
      media: {
        media_type:
          ['video', 'image', 'audio'].indexOf(contentType as 'image' | 'video' | 'audio') > -1
            ? (contentType as 'image' | 'video' | 'audio')
            : 'file',
        ignore_valid: true,
        provider: 'twilio',
        getContentTemporaryUrl: getContentTemporaryUrl
      }
    }
  }

  const attributes = message.attributes as MessageAttributes
  if (attributes?.is_virtual_agent && body) {
    try {
      const parsed = validateSchema(JSON.parse(body))
      responses = parsed.responses
      payload = parsed.payloads[0] || payload
    } catch (error: unknown) {
      logger.warn(
        `Failed to parse virtual agent message=${typeof error === 'string' ? error : (error as Error)?.message}`,
        error
      )
      const errorMessage = schemaErrorHandler(error)
      return {
        responses: [],
        payload: {
          media_type: 'error',
          error_message: errorMessage
        }
      }
    }
  }

  if (attributes?.action?.name === 'digital_interaction_assigned') {
    return {
      responses: [],
      payload: {
        media_type: 'system',
        action_type: 'assign',
        smart_text: {
          key: 'assignMessage',
          options: { name: attributes.action.metadata?.cur_display_name }
        }
      }
    }
  }

  return { responses, payload }
}
