import { ReactNode, RefObject, useMemo } from 'react'

import clsx from '@/utils/clsx'

import { Align, useDropdownContext } from './context'

import classes from './Dropdown.module.scss'

interface DropdownPopupProps {
  forwardedRef?: RefObject<HTMLDivElement>
  visible?: boolean
  children: ReactNode
}

const DropdownPopup = ({ forwardedRef, visible, children }: DropdownPopupProps) => {
  const { align, top } = useDropdownContext()

  const className = useMemo(() => {
    return clsx(classes['dropdown-popup'], { [classes['dropdown-popup-visible']]: !!visible })
  }, [visible])

  const style = useMemo(() => {
    return {
      top: `${top ?? 0}px`,
      left: `${align === Align.LEFT ? 0 : 'auto'}`,
      right: `${align === Align.RIGHT ? 0 : 'auto'}`
    }
  }, [top, align])

  return (
    <div ref={forwardedRef} className={className} style={style}>
      {children}
    </div>
  )
}

export default DropdownPopup
