import { useShallow } from 'zustand/react/shallow'

import { LayoutDirection } from '@td-org/chat-widget-shared'

import { type Action, type Store, useApp } from '@/stores/app/store.ts'
import { useMessageStore } from '@/stores/message'

const actionSelector = (state: Store): Action => ({
  go: state.go,
  on: state.on,
  off: state.off,
  getInitialPage: state.getInitialPage,
  emit: state.emit,
  reset: state.reset,
  is: state.is,
  isNot: state.isNot,
  focusUserInputArea: state.focusUserInputArea,
  setIsFrameOpened: state.setIsFrameOpened,
  setCredentials: state.setCredentials,
  hasProviderCredentials: state.hasProviderCredentials,
  createIdentity: state.createIdentity,
  setFormData: state.setFormData,
  setLayoutDirection: state.setLayoutDirection,
  enableScreenLoading: state.enableScreenLoading,
  hiddenScreenLoading: state.hiddenScreenLoading,
  updateFeedbackStatus: state.updateFeedbackStatus,
  updateChatContentScroll: state.updateChatContentScroll,
  updateInitialScreenScroll: state.updateInitialScreenScroll,
  updateInitialScreenHeight: state.updateInitialScreenHeight,
  enableLoadFeedback: state.enableLoadFeedback,
  updateUnreadMessageCount: state.updateUnreadMessageCount
})

const pageSelector = (state: Store) => state.page

const enableEndChatSelector = (state: Store) => state.hasInitialized && state.page === 'conversation'

const enableDownloadTranscriptSelector = (state: Store) => {
  return ((state.hasInitialized && state.page === 'conversation') || state.page === 'end') && !!state.interactionId
}

const shouldLoadFeedbackSelector = (state: Store) => state.shouldLoadFeedback

const feedbackLinkSelector = (state: Store) => state.feedbackLink

const feedbackStatusSelector = (state: Store) => {
  const status = state.feedbackStatus.status

  return {
    ...state.feedbackStatus,
    active: status === 'SURVEY_UNSUBMITTED',
    hidden: status === 'SURVEY_NOT_AVAILABLE',
    pending: status === 'init',
    showMessage: ['init', 'SURVEY_UNSUBMITTED'].indexOf(status) === -1
  }
}

const frameOpenedSelector = (state: Store) => state.isFrameOpened

const enableRTLLayoutDirectionSelector = (state: Store) => state.layoutDirection === LayoutDirection.rtl

const showScreenLoadingSelector = (state: Store) => state.showScreenLoading

const chatContentScrollSelector = (state: Store) => {
  const { chatContentScroll } = state
  const enableScroll = chatContentScroll.scrollOver !== 0

  return {
    ...chatContentScroll,
    enableScroll,
    showScrollToBottom: enableScroll && chatContentScroll.scrollPercent < 96
  }
}

const initialScreenScrollSelector = (state: Store) => state.initialScreenScroll

const initialScreenHeightSelector = (state: Store) => state.initialScreenHeight

const unreadMessageCountSelector = (state: Store) => state.unreadMessageCount

export const useAppAction = () => useApp(useShallow(actionSelector))
export const usePage = () => useApp(pageSelector)
export const useEnableEndChat = () => useApp(enableEndChatSelector)
export const useEnableDownloadTranscript = () => useApp(enableDownloadTranscriptSelector)
export const useShouldLoadFeedback = () => useApp(shouldLoadFeedbackSelector)
export const useFeedback = () => useApp(feedbackLinkSelector)
export const useFeedbackStatus = () => useApp(feedbackStatusSelector)
export const useFrameOpened = () => useApp(frameOpenedSelector)
export const useEnableRTLLayoutDirection = () => useApp(enableRTLLayoutDirectionSelector)

export const useShowScreenLoading = () => useApp(showScreenLoadingSelector)

export const useChatContentScroll = () => useApp(chatContentScrollSelector)

export const useInitialScreenScroll = () => useApp(initialScreenScrollSelector)

export const useInitialScreenHeight = () => useApp(initialScreenHeightSelector)

export const useUnreadMessageCount = () => useApp(unreadMessageCountSelector)

useApp.subscribe(frameOpenedSelector, (target) => {
  if (target) {
    useMessageStore.getState().updateLastReadIndex()
  }
})

useApp.subscribe(enableRTLLayoutDirectionSelector, (target) => {
  const injector = document.documentElement

  if (target) {
    injector.setAttribute('data-layout-direction', LayoutDirection.rtl)
  } else {
    injector.removeAttribute('data-layout-direction')
  }
})
