import { type Message as TwilioMessage } from '@twilio/conversations'

import { MessageAttributes, Role } from '@td-org/chat-widget-shared'

export const getMessageRole = (message: TwilioMessage): Role => {
  const { attributes, author } = message

  const _attributes = attributes as MessageAttributes

  if (_attributes?.is_virtual_agent) {
    return 'va'
  }

  if (_attributes?.agentId) {
    return 'agent'
  }

  if (author === 'system') {
    return 'system'
  }

  return 'contact'
}

export const getMessageRoleId = (message: TwilioMessage): string => {
  const { attributes } = message

  const _attributes = attributes as MessageAttributes

  if (_attributes?.agentId) {
    return _attributes.agentId
  }

  return ''
}
