import { FC, ReactElement } from 'react'

import { ErrorBoundary } from '@td-org/chat-widget-components'

import { EmojiPickerPopup } from '@/components/EmojiPicker/EmojiPickerPopup'
import { logForErrorBoundary } from '@/utils/logger-helper'

type Props = {
  bottom?: number
  disabled?: boolean
  visible: boolean
  setVisible: () => void
  onSelect: (event: { emoji: string }) => void
  enableRTL?: boolean
}

const EmojiPicker: FC<Props> = (props): ReactElement => {
  const { visible, setVisible, enableRTL = false, bottom, onSelect } = props

  return (
    <ErrorBoundary fallback={<></>} onError={logForErrorBoundary}>
      <EmojiPickerPopup
        visible={visible}
        bottom={bottom}
        onTogglePicker={setVisible}
        onEmojiClick={onSelect}
        enableRTL={enableRTL}
      />
    </ErrorBoundary>
  )
}

export default EmojiPicker
