import React from 'react'
import { UseFormReturn } from 'react-hook-form'
import { useTranslation } from 'react-i18next'

import { Clear as ClearIcon } from '@/components/Icons'
import { optionsSelector, useConfig } from '@/stores/config'
import clsx from '@/utils/clsx.ts'
import { toLowerCase } from '../../utils/helper'
import ErrorMessage from '../ErrorMessage'

import commonStyles from '../../common.module.scss'
import classes from './CustomEmail.module.scss'

interface Props {
  label: string
  resetField: UseFormReturn['resetField']
  onChange: () => void
  onBlur: () => void
  value: string
  defaultValue: string
  name: string
  isError: boolean
  required: boolean
}

export const CustomEmail = React.forwardRef<HTMLInputElement, Props>((props, ref) => {
  const { name, label, resetField, onChange, onBlur, value, defaultValue, isError, required } = props

  const { customFieldInputPlaceholder, customFieldEmailInvalidFormatMessage } = useConfig(optionsSelector)
  const [t] = useTranslation()

  return (
    <div className={classes.customField}>
      <div className={classes.wrapper}>
        <div className={classes.labelWrapper}>
          <p className={classes.label} id={name}>
            {label}
            {required ? (
              <span className={commonStyles.isRequired} aria-label='required'>
                *
              </span>
            ) : null}
          </p>
        </div>

        <div className={classes.inputWrapper}>
          <input
            ref={ref}
            value={value || ''}
            autoComplete='off'
            className={clsx(classes.input, {
              [commonStyles.isError]: isError
            })}
            name={name}
            onChange={onChange}
            onBlur={onBlur}
            placeholder={
              customFieldInputPlaceholder
                ? t('customFieldPlaceholder', {
                    type: customFieldInputPlaceholder,
                    label: toLowerCase(label),
                    interpolation: { escapeValue: false }
                  })
                : ''
            }
            aria-labelledby={name}
          />
          {value && value !== defaultValue && (
            <button
              aria-label={`reset ${label}`}
              className={classes.clear}
              type='button'
              onClick={() => resetField(name, { keepError: true })}
            >
              <ClearIcon />
            </button>
          )}
        </div>
        {isError && (
          <ErrorMessage message={customFieldEmailInvalidFormatMessage || t('customFieldEmailInvalidFormatMessage')} />
        )}
      </div>
    </div>
  )
})
