import { create } from 'zustand'
import { useShallow } from 'zustand/react/shallow'

import {
  type AppConfig,
  getDevice,
  injectCssVariables,
  safeJsonParse,
  safeJsonToString,
  type WidgetSettings} from '@td-org/chat-widget-shared'

import { Config } from '@/models/Config'
import { useApp } from '@/stores/app/store.ts'
import i18nConfig from '@/utils/i18nConfig.ts'
import baseLogger from '@/utils/logger.ts'
import reporter from '@/utils/reporter'
import { extractCssVars, extractOptions, reassignCssVars, reassignOptions } from '@/utils/themeUtil.ts'

import { usePreview } from './preview'
import { useRpc } from './rpc'

type State = {
  config: Config
  reset: () => void
  init: () => Promise<void>
  update: (payload: { appConfig: AppConfig }) => Promise<void>
  updateContext: (payload: Record<string, string>) => void
}

const logger = baseLogger.getSubLogger({ name: 'useConfig' })

export const useOriginalConfig = create<State>((set, get) => {
  let initialized = false

  return {
    config: new Config(),
    reset: () => {
      set({ config: new Config() })
      initialized = false
    },
    init: async () => {
      if (initialized) {
        return
      }

      initialized = true

      const response = await useRpc.getState().getAllConfig()

      if (response?.data) {
        reporter.init(response.data)

        const { isMobile, type: deviceType } = getDevice()
        const { appProps, appConfig, touchpoint, context, injectorWindowHostname } = response.data
        const config = new Config()
        const languageCode = await i18nConfig.changeLanguage(appConfig?.languageCode)

        document.documentElement.className = deviceType

        config.isMobile = isMobile
        config.deviceType = deviceType
        config.injectorWindowHostname = injectorWindowHostname
        config.enableSplitTwilioBootstrap = touchpoint.enable_split_twilio_bootstrap
        config.feedbackFlowId = touchpoint.feedback_flow_id
        config.appConfig = {
          ...get().config.appConfig,
          ...appConfig,
          languageCode
        } as Config['appConfig']
        config.appProps = appProps
        config.context = context

        config.appearanceConfig = safeJsonParse(touchpoint.config, config.appearanceConfig, (error) => {
          logger.error('Error parsing touchpoint appearance config', error)
        }) as WidgetSettings.WidgetSettings

        config.styles = extractCssVars(appConfig.styles)

        config.options = {
          ...get().config.options,
          ...Object.assign(reassignOptions(config?.appearanceConfig?.appearance), extractOptions(appConfig.styles))
        }
        set({ config })

        const cssVars = reassignCssVars(config?.appearanceConfig?.appearance, extractCssVars(appConfig.styles))
        injectCssVariables(document.documentElement, cssVars)

        logger.info(
          `Received configuration: touchpointId=${appProps?.touchpointId} appConfig=${safeJsonToString(
            appConfig
          )} appProps=${safeJsonToString(appProps)} touchpoint=${safeJsonToString(touchpoint)}`
        )

        useApp.setState({ enableInitialForm: config.appearanceConfig.initialScreen.showInitialScreen })
        useApp.getState().setLayoutDirection(languageCode)
      }
    },
    update: async ({ appConfig }: { appConfig: AppConfig }) => {
      if (!initialized) {
        logger.warn('Update app configs failed because the config is not initialized yet.')
        return
      }

      const { config } = get()

      const languageCode = await i18nConfig.changeLanguage(appConfig?.languageCode)

      config.appConfig = {
        ...config.appConfig,
        ...appConfig,
        languageCode
      } as Config['appConfig']
      config.options = {
        ...config.options,
        ...Object.assign(reassignOptions(config?.appearanceConfig?.appearance), extractOptions(appConfig.styles))
      }
      set({ config })

      useApp.getState().setLayoutDirection(languageCode)
    },
    updateContext: (payload: Record<string, string>) => {
      if (!initialized) {
        logger.warn('Update context failed because the config is not initialized yet.')
      }
      const { config } = get()
      config.context = { ...payload }
      set({ config })
    }
  }
})

export const useChatConfig = () =>
  useOriginalConfig(
    useShallow((state) => {
      return {
        chatTitle: state.config.options.chatTitle,
        chatSubtitle: state.config.options.chatSubtitle,
        enableEmoji: state.config.appConfig.enableEmoji,
        enableAttachments: state.config.appConfig.enableAttachments,
        enableValidation: state.config.appConfig.enableValidation,
        enableUserInput: state.config.appConfig.enableUserInput,
        showInitialScreen: state.config.appearanceConfig.initialScreen.showInitialScreen,
        welcomeMessage: state.config.appearanceConfig.initialScreen.welcomeMessage,
        controls: state.config.appearanceConfig.initialScreen.controls,
        showCloseConversationButton: state.config.appearanceConfig.generalSettings.showCloseConversationButton,
        showDownloadTranscriptButton: state.config.appearanceConfig.generalSettings.showChatTranscriptsDownloadButton,
        showTypingIndicatorButton: state.config.appearanceConfig.generalSettings.showTypingIndicatorButton,
        context: state.config.context,
        languageCode: state.config.appConfig.languageCode
      }
    })
  )

export const appPropsSelector = (state: State): Config['appProps'] => state.config.appProps

export const stylesSelector = (state: State): Config['styles'] => state.config.styles

export const optionsSelector = (state: State): Config['options'] => {
  return state.config.options
}

export const initialScreenSelector = (state: State): Config['appearanceConfig']['initialScreen'] => {
  return state.config.appearanceConfig.initialScreen
}

export const chatConfigSelector = (state: State): Config['appearanceConfig']['chat'] => {
  return state.config.appearanceConfig.chat
}

export const generalSettingsSelector = (state: State): Config['appearanceConfig']['generalSettings'] => {
  return state.config.appearanceConfig.generalSettings
}

export const useConfig = /preview.html/gi.test(window.location.href) ? usePreview : useOriginalConfig

export const useInitialScreen = () => useConfig(initialScreenSelector)

export const useAppProps = () => useConfig(appPropsSelector)

export const useStyles = () => useConfig(stylesSelector)
