import { Loader } from '@td-org/chat-widget-shared'

const cache = new Map<string, Loader<unknown>>()

export function getLoader<T = unknown>(key: string): Loader<T> | undefined {
  return cache.get(key) as Loader<T>
}

export function setLoader<T = unknown>(key: string, loader: Loader<T>): void {
  cache.set(key, loader)
}

export function deleteLoader(key: string): void {
  cache.delete(key)
}

export function clearLoader(): void {
  cache.clear()
}
