import { type ReactElement, type ReactNode } from 'react'
import { type ErrorInfo } from 'react'
import clsx from 'clsx'

import { type MessageTypes } from '@td-org/chat-widget-shared'

import { getContainerVariation } from './getVariations.ts'
import Message from './Message.tsx'

import classes from './ChatBubbleBox.module.scss'

export type ChatBubbleBoxProps = {
  name?: ReactNode
  avatar?: ReactNode
  status?: ReactNode
  timestamp?: ReactNode
  children: ReactNode
  errorMessage?: string
  type: MessageTypes
  alignment: 'sent' | 'received'
  onError?: (error: Error, errorInfo: ErrorInfo) => void
  isLastMessage?: boolean
  isInSeries?: boolean
}

function ChatBubbleBox(props: ChatBubbleBoxProps): ReactElement {
  const {
    name,
    avatar,
    status,
    timestamp,
    children,
    alignment,
    type,
    errorMessage,
    onError,
    isLastMessage = false,
    isInSeries = false
  } = props

  return (
    <div
      className={clsx(getContainerVariation(type), {
        [classes.sent]: alignment === 'sent',
        [classes.received]: alignment === 'received',
        [classes.inSeries]: isInSeries,
        [classes.lastMessage]: isLastMessage
      })}
      data-qa={type}
      data-testid={type}
    >
      {avatar && <div className={classes.avatar}>{avatar}</div>}
      <div className={classes.messageContainer}>
        <Message type={type} errorMessage={errorMessage} onError={onError}>
          {children}
        </Message>
        {status && <div className={classes.status}>{status}</div>}
      </div>
      {(name || timestamp) && (
        <div className={classes.nameAndTimestamp}>
          {name && <div className={classes.name}>{name}</div>}
          {name && timestamp && (
            <div className={classes.divide}>
              <span></span>
            </div>
          )}
          {timestamp && <div className={classes.timestamp}>{timestamp}</div>}
        </div>
      )}
    </div>
  )
}

export default ChatBubbleBox
