import { ChangeEvent, FC, ReactElement, useRef } from 'react'

import { MAX_ALLOWED, MediaType } from '@td-org/chat-widget-shared'

import IconButton from '@/components/IconButton'
import { ACCEPT_MIME_TYPES_MAP, acceptFileType, getFileMimes } from '@/utils/file.ts'
import { Attachment as AttachmentIcon } from '../Icons'

import classes from './Attachment.module.scss'

interface Props {
  disabled?: boolean
  onChange: (data: FormData, type: MediaType) => void
}

const Attachment: FC<Props> = (props): ReactElement => {
  const { disabled, onChange } = props

  const handleOnChange = (event: ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files
    if (!files) return

    for (let i = 0; i < files.length; i++) {
      if (ACCEPT_MIME_TYPES_MAP.indexOf(files[i].type) < 0) return

      const formData = new FormData()
      formData.append('file', files[i])

      const fileMime = getFileMimes(files[i].type)

      let fileType = fileMime.prefix

      if (files[i].type === 'application/pdf') {
        fileType = 'file'
      }

      const fileSize = files[i].size
      if (fileSize > MAX_ALLOWED.FILE_SIZE) return
      onChange(formData, fileType as MediaType)
    }
    if (inputRef.current) {
      inputRef.current.value = ''
    }
  }

  const inputRef = useRef<HTMLInputElement>(null)

  const onUploadAttachment = (): void => {
    inputRef.current?.click()
  }

  return (
    <IconButton onClick={onUploadAttachment} disabled={disabled} aria-label='upload attachment'>
      <input
        type='file'
        aria-hidden
        accept={acceptFileType}
        onChange={handleOnChange}
        ref={inputRef}
        tabIndex={-1}
        data-testid='file-input'
        className={classes.hiddenInput}
      />
      <AttachmentIcon />
    </IconButton>
  )
}

export default Attachment
