import { useCallback, useRef } from 'react'
import { useTranslation } from 'react-i18next'

import { ChatBubble } from '@td-org/chat-widget-components'

import { chatConfigSelector, optionsSelector, useConfig } from '@/stores/config.ts'
import { logForErrorBoundary, logForMessageMediaError } from '@/utils/logger-helper.ts'
import { toLocaleTimeString } from '@/utils/time.ts'

import { type WelcomeMessagesProps } from './types.ts'

function WelcomeMessages(props: WelcomeMessagesProps) {
  const { onButtonClick } = props
  const currentTimestamp = useRef<number>(Date.now())

  const { messages } = useConfig(chatConfigSelector)
  const { linkProtocol, linkTarget, botIcon, botIconVerticalAlign } = useConfig(optionsSelector)
  const [t] = useTranslation()

  const chatBubbleProps = {
    avatarVerticalAlign: botIconVerticalAlign,
    linkProtocol,
    linkTarget,
    errorMessageText: t('invalidRichMedia'),
    onButtonClick,
    onMediaSourceError: logForMessageMediaError,
    onMessageError: logForErrorBoundary
  }

  const onResend = useCallback(() => false, [])

  return (
    <>
      {messages.map((message, index) => {
        return (
          <ChatBubble
            {...chatBubbleProps}
            key={message.key}
            sid={message.key}
            tid={message.key}
            avatarUrl={botIcon}
            status={'success'}
            alignment={'received'}
            isInSeries={index > 0}
            hasAvatar={index === 0}
            onResend={onResend}
            timestamp={currentTimestamp.current}
            hasTimestamp={false}
            formattedTime={toLocaleTimeString(currentTimestamp.current)}
            type='text'
            text={message.value}
          />
        )
      })}
    </>
  )
}

export default WelcomeMessages
