import { FormEvent, forwardRef, useCallback, useEffect, useRef } from 'react'
import { Controller, useForm, ValidateResult } from 'react-hook-form'
import debounce from 'lodash.debounce'
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react'

import { type WidgetSettings } from '@td-org/chat-widget-shared'

import Animate from '@/components/Animate'
import { useScrollForInitialScreen } from '@/hooks/useScroll.ts'
import { useAppAction } from '@/stores/app/selectors.ts'

import { pickBy } from './utils/helper'
import { validateEmail, validatePhoneNumber } from './utils/validate'
import {
  CustomButton,
  CustomDropdown,
  CustomEmail,
  CustomField,
  CustomMessage,
  CustomNumber,
  WelcomeMessage
} from './components'

import classes from './initialScreen.module.scss'

const validationMap: Record<string, (value: string) => Promise<ValidateResult>> = {
  field_email: validateEmail,
  field_number: validatePhoneNumber
}

interface Props {
  onSubmitInitialScreen: (customFields: Record<string, string> | null, enableStartConversation: boolean) => void
  defaultValues: Record<string, string>
  enableValidation: boolean
  active: boolean
  welcomeMessage: WidgetSettings.WelcomeMessage
  controls: WidgetSettings.Control[]
  updateChatFrameHeight: () => Promise<number>
}

export const InitialScreen = forwardRef<HTMLDivElement, Props>(function (props, ref) {
  const {
    controls,
    welcomeMessage,
    onSubmitInitialScreen,
    defaultValues,
    enableValidation,
    active,
    updateChatFrameHeight
  } = props
  const enableStartConversationRef = useRef<boolean>(false)

  const { updateInitialScreenHeight } = useAppAction()

  const { osRef, headElRef } = useScrollForInitialScreen()

  const {
    handleSubmit,
    control,
    formState: { errors },
    resetField
  } = useForm({
    mode: 'onChange',
    shouldFocusError: false
  })

  const onSubmit = useCallback(
    (formData: Record<string, unknown> | null) => {
      const collectFields: Record<string, string> | null = pickBy(formData, (_, val) => !!val)

      const completeFields = { ...defaultValues, ...collectFields }

      if (typeof onSubmitInitialScreen === 'function') {
        onSubmitInitialScreen(completeFields, enableStartConversationRef.current)
      }
    },
    [defaultValues, onSubmitInitialScreen]
  )

  const onHandleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    e.stopPropagation()
  }

  useEffect(() => {
    if (!active) return
    const formControls = controls.filter(
      (control) => control.type === 'field' || control.type === 'dropdown'
    ) as WidgetSettings.Control[]

    const needFilledFields = formControls.reduce(
      (acc, control) => {
        acc[control.argument] = defaultValues?.[control.argument] || undefined

        return acc
      },
      {} as Record<PropertyKey, string | undefined>
    )

    const isAllFieldAreFilled =
      formControls.length > 0 && Object.values(needFilledFields).every((value) => value !== undefined)

    // if all fields are filled, then we don't show the initial screen
    if (isAllFieldAreFilled) {
      onSubmit(defaultValues)
    } else {
      formControls.forEach((item) => resetField(item.argument, { defaultValue: defaultValues?.[item.argument] }))
    }
  }, [controls, resetField, defaultValues, active, onSubmit])

  useEffect(() => {
    const element = osRef.current?.getElement()

    if (!element) return

    const observer = new MutationObserver(
      debounce(async () => {
        const height = await updateChatFrameHeight()

        updateInitialScreenHeight(height)
      }, 100)
    )

    observer.observe(element, {
      childList: true,
      subtree: true,
      attributeFilter: ['data-initial-screen-loaded']
    })

    return () => {
      observer.disconnect()
    }
  }, [osRef, updateChatFrameHeight, updateInitialScreenHeight])

  if (!active) return <></>

  return (
    <Animate.page className={classes.initialScreenWrapper}>
      <OverlayScrollbarsComponent
        ref={osRef}
        className={classes.initialScreen}
        options={{ scrollbars: { autoHide: 'scroll' }, overflow: { x: 'hidden' } }}
      >
        <div ref={ref}>
          <div ref={headElRef} style={{ width: '100%', height: 1 }} />
          <WelcomeMessage title={welcomeMessage?.title} subTitle={welcomeMessage?.subtitle} />
          <form onSubmit={onHandleSubmit} data-testid='form'>
            {controls.map((controlItem) => {
              const defaultValue = defaultValues?.[controlItem.argument] || ''

              if (controlItem.type === 'button') {
                return (
                  <div key={controlItem.key}>
                    <CustomButton
                      key={controlItem.key}
                      label={controlItem.label}
                      // trigger={controlItem.trigger}
                      trigger='start_the_chat'
                      submitHandle={() => {
                        enableStartConversationRef.current = true
                        handleSubmit(onSubmit)()
                        // enableStartConversationRef.current = !!controlItem?.enable_start_conversation
                        // handleSubmit(onSubmit)()
                      }}
                    />
                  </div>
                )
              } else if (controlItem.type === 'message') {
                return (
                  <div key={controlItem.key}>
                    <CustomMessage key={controlItem.key} isBubble={controlItem.isBubble} message={controlItem.value} />
                  </div>
                )
              } else if (controlItem.type === 'dropdown') {
                return (
                  <div key={controlItem.key}>
                    <Controller
                      name={controlItem?.argument}
                      control={control}
                      rules={{
                        required: controlItem?.required
                      }}
                      defaultValue={defaultValue}
                      render={({ field }) => (
                        <CustomDropdown
                          {...field}
                          label={controlItem.label}
                          options={controlItem.options || []}
                          required={controlItem.required}
                          isError={errors[controlItem?.argument]?.type === 'required'}
                        />
                      )}
                    />
                  </div>
                )
              } else if (controlItem.type === 'field') {
                if (enableValidation && ['field_email', 'field_number'].includes(controlItem.define)) {
                  const Component = controlItem.define === 'field_email' ? CustomEmail : CustomNumber

                  return (
                    <div key={controlItem.key}>
                      <Controller
                        name={controlItem?.argument}
                        control={control}
                        rules={{
                          required: controlItem?.required,
                          validate: ['field_email', 'field_number'].includes(controlItem?.define)
                            ? validationMap[controlItem?.define]
                            : undefined
                        }}
                        defaultValue={defaultValue}
                        render={({ field }) => (
                          <Component
                            {...field}
                            defaultValue={defaultValue}
                            resetField={resetField}
                            label={controlItem.label}
                            required={controlItem.required}
                            isError={
                              errors[controlItem?.argument]?.type === 'required' ||
                              errors[controlItem?.argument]?.type === 'validate'
                            }
                          />
                        )}
                      />
                    </div>
                  )
                } else {
                  return (
                    <div key={controlItem.key}>
                      <Controller
                        name={controlItem?.argument}
                        control={control}
                        rules={{
                          required: controlItem?.required
                        }}
                        defaultValue={defaultValue}
                        render={({ field }) => (
                          <CustomField
                            {...field}
                            defaultValue={defaultValue}
                            resetField={resetField}
                            label={controlItem.label}
                            required={controlItem.required}
                            isError={errors[controlItem?.argument]?.type === 'required'}
                          />
                        )}
                      />
                    </div>
                  )
                }
              }
            })}
          </form>
        </div>
      </OverlayScrollbarsComponent>
    </Animate.page>
  )
})
