import { ReactElement } from 'react'

import { optionsSelector, useConfig } from '@/stores/config.ts'
import { Logo as LogoIcon } from '../Icons'

import classes from './Logo.module.scss'

const Logo = (): ReactElement => {
  const { chatTitleIcon } = useConfig(optionsSelector)

  return (
    <div className={classes.logo} data-testid={'logo'}>
      {chatTitleIcon ? (
        <div className={classes.customizedLogo} style={{ backgroundImage: `url(${chatTitleIcon})` }} />
      ) : (
        <LogoIcon className={classes.defaultLogo} />
      )}
    </div>
  )
}

export default Logo
