import IconButton from '@/components/IconButton'
import { Emoji as EmojiIcon } from '@/components/Icons'

type Props = {
  disabled?: boolean
  onClick: () => void
}

const EmojiButton = ({ disabled, onClick }: Props) => {
  return (
    <IconButton onClick={onClick} disabled={disabled} aria-label='add emoji'>
      <EmojiIcon />
    </IconButton>
  )
}

export default EmojiButton
